.vc_tta-tabs{
  .vc_tta-tabs-container{
    ul.vc_tta-tabs-list{
      text-align: center;
      margin-bottom: 25px;
      li.vc_tta-tab{
        a{
          border:none;
          background: none;
          position: relative;
          span{
            font-family: 'Raleway',sans-serif;
            font-weight: 700;
            font-size: 17px;
            color:#222222;
            text-transform: uppercase;
            padding:20px 39px;
            &:after{
              content: '\f006';
              font-family: FontAwesome;
              font-size: 14px;
              color:#d9d9d9;
              position: absolute;
              right: -6px;
              top: 15px;
            }
          }
        }
        &:last-child{
          a{
            span{
              &:after{
                content: '';
              }
            }
          }
        }
        &.vc_active{
          a{
            span{
              color: @color-theme;
            }
          }
        }
      }
    }
  }

  .vc_tta-panels-container{
    .vc_tta-panels{
      background: none;
      .vc_tta-panel{
        .vc_tta-panel-body{
          padding:0;
          border:none !important;
          border-radius: 0 !important;
          -webkit-border-radius: 0 !important;
          -moz-border-radius: 0 !important;
          -ms-border-radius: 0 !important;
          -o-border-radius: 0 !important;
        }
      }
    }
  }
  &.vc_tta-color-grey{
    &.vc_tta-style-classic{
      .vc_tta-panels-container{
        .vc_tta-panels{
          background: none;
          border:none;
        }
      }
    }
  }
}