/* 11.12 Quote */
.tzElement_Quote_Container{
  position: relative;
  button{
    background: none;
    border:none;
    position: absolute;
    top:50%;
    &.tzprevslider{
      left: 50%;
      margin-left: -150px;
    }
    &.tznextslider{
      right: 50%;
      margin-right: -150px;
    }
    i{
      color: #686868;
      font-size: 30px;
    }
  }
}
.tzElement_Quote{
  position: relative;
  &.tzQuote_type1{
    .tzQuote_Item{
      padding:0px 170px;
      .tzQuote_Image{
        width: 100px;
        height: 100px;
        padding:5px;
        background:rgba(255,255,255,0.1);
        float: left;
        .border-radius(50%);
        .tzQuote_imageBox{
          width: 100%;
          height: 100%;
          overflow: hidden;
          .border-radius(50%);
          img{
            width: 100%;
            height: auto;
          }
        }
      }

      .tzQuote_Content{
        font-family: 'Droid Serif', serif;
        font-size: 14px;
        color: #FFFFFF;
        margin-left: 130px;
        margin-bottom: 23px;
      }
      .tzQuote_Info{
        margin-left: 130px;
        .tzQuote_Image{
          display: none;
        }
        .tzQuote_Name{
          font-family: 'Montserrat',sans-serif;
          font-weight: 700;
          font-size: 14px;
          color:@color-theme;
        }
        small{
          font-family: 'Ubuntu',sans-serif;
          font-weight: 300;
          font-size: 14px;
          color:#FFFFFF;
        }
        .tzQuote_Employment{
          font-family: 'Ubuntu',sans-serif;
          font-weight: 300;
          font-size: 14px;
          font-style: italic;
          color:#FFFFFF;
        }
      }
    }

    .owl-controls{
      margin:0px;
      padding:0px;
      .owl-pagination{
        position: absolute;
        right: 60px;
        top: 5px;
        .owl-page{
          display: block;
          margin: 0 0 15px;
          span{
            width: 7px;
            height: 7px;
            background: #ffffff;
            opacity: 1;
            position: relative;
            .transition(all 0.3s ease-in-out 0s);
            &:after{
              content: '';
              display: block;
              width: 15px;
              height: 15px;
              border:1px solid @color-theme;
              .border-radius(50%);
              position: absolute;
              top:-5px;
              left: -5px;
              .transform(scale(1.5));
              visibility: hidden;
              opacity: 0;
              .transition(all 0.3s ease-in-out 0s);
            }
          }
          &.active{
            span{
              width: 5px;
              height: 5px;
              background: @color-theme;
              .border-radius(50%);
              position: relative;
              &:after{
                .transform(scale(1));
                visibility: visible;
                opacity: 1;
              }
            }
          }

          &:hover{
            span{
              width: 5px;
              height: 5px;
              background: @color-theme;
              .border-radius(50%);
              position: relative;
              &:after{
                .transform(scale(1));
                visibility: visible;
                opacity: 1;
              }
            }
          }
        }
      }
    }
  }

  &.tzQuote_type2{
    .tzQuote_Item{
      padding:0px 170px;
      text-align: center;
      .tzQuote_Image{
        display: inline-block;
        width: 100px;
        height: 100px;
        padding:5px;
        margin:0 0 80px;
        background:rgba(255,255,255,0.1);
        .border-radius(50%);
        .tzQuote_imageBox{
          width: 100%;
          height: 100%;
          overflow: hidden;
          .border-radius(50%);
          img{
            width: 100%;
            height: auto;
          }
        }
      }

      .tzQuote_Content{
        font-family: 'Droid Serif', serif;
        font-size: 17px;
        color: #ececec;
        margin-bottom: 90px;
        line-height: 30px;
      }
      .tzQuote_Info{
        position: relative;
        display: inline-block;
        padding-left: 50px;
        .tzQuote_Image{
          display: none;
        }
        .tzQuote_Name{
          font-family: 'Montserrat',sans-serif;
          font-weight: 700;
          font-size: 14px;
          color:#FFFFFF;
          display: block;
          text-align: left;
          text-transform: uppercase;
        }
        small{
          display: none;
        }
        .tzQuote_Employment{
          font-family: 'Ubuntu',sans-serif;
          font-weight: 300;
          font-size: 14px;
          font-style: italic;
          color:#FFFFFF;
          display: block;
          text-align: left;
        }

        &:after{
          content: '{';
          display: block;
          width: 40px;
          height: 40px;
          position: absolute;
          top:0;
          left: 0;
          background: @color-theme;
          .border-radius(50%);
          text-align: center;
          font-family: "ElegantIcons";
          font-variant: normal;
          font-weight: normal;
          line-height: 1;
          color: #FFFFFF;
          font-size: 25px;
          text-transform: none;
          padding:7px 0px;
        }
      }
    }

    .owl-controls{
      margin:80px 0 0;
      padding:0px;
      .owl-pagination{
        .owl-page{
          width: 7px;
          height: 7px;
          margin: 0 7px 0;
          span{
            width: 7px;
            height: 7px;
            background: #ffffff;
            opacity: 1;
            position: relative;
            .transition(all 0.3s ease-in-out 0s);
            &:after{
              content: '';
