              width: 15px;
              height: 15px;
              border:1px solid @color-theme;
              .border-radius(50%);
              position: absolute;
              top:-5px;
              left: -5px;
              .transform(scale(1.5));
              visibility: hidden;
              opacity: 0;
              .transition(all 0.3s ease-in-out 0s);
            }
          }
          &.active{
            span{
              width: 5px;
              height: 5px;
              background: @color-theme;
              .border-radius(50%);
              position: relative;
              &:after{
                .transform(scale(1));
                visibility: visible;
                opacity: 1;
              }
            }
          }

          &:hover{
            span{
              width: 5px;
              height: 5px;
              background: @color-theme;
              .border-radius(50%);
              position: relative;
              &:after{
                .transform(scale(1));
                visibility: visible;
                opacity: 1;
              }
            }
          }
        }
      }
    }
  }

  &.tzQuote_type3{
    .tzQuote_Item{
      padding:0px 170px;
      text-align: center;

      .tzQuote_Image{
        display: none;
      }
      .tzQuote_Content{
        font-family: 'Droid Serif', serif;
        font-size: 17px;
        color: #ececec;
        margin-bottom: 65px;
        line-height: 30px;
      }
      .tzQuote_Info{
        position: relative;
        display: inline-block;
        width: 330px;
        padding-bottom: 68px;
        border-bottom: 1px solid #2c2c2c;
        .tzQuote_Image{
          display: inline-block;
          width: 100px;
          height: 100px;
          padding:3px;
          margin:0 0 30px;
          border: 1px solid @color-theme;
          .border-radius(50%);
          .tzQuote_imageBox{
            width: 100%;
            height: 100%;
