              height: auto;
            }
          }
        }
        .tzQuote_Name{
          font-family: 'Raleway',sans-serif;
          font-weight: 800;
          font-size: 14px;
          color:@color-theme;
          display: block;
          text-align: center;
          text-transform: uppercase;
          padding-bottom: 5px;
          letter-spacing: 2px;
        }
        small{
          display: none;
        }
        .tzQuote_Employment{
          font-family: 'Raleway',sans-serif;
          font-weight: 400;
          font-size: 14px;
          color:#FFFFFF;
          display: block;
          text-align: center;
        }
      }
    }

    .owl-controls{
