              border:1px solid @color-theme;
              .border-radius(50%);
              position: absolute;
              top:-5px;
              left: -5px;
              .transform(scale(1.5));
              visibility: hidden;
              opacity: 0;
              .transition(all 0.3s ease-in-out 0s);
            }
          }
          &.active{
            span{
              width: 5px;
              height: 5px;
              background: @color-theme;
              .border-radius(50%);
              position: relative;
              &:after{
                .transform(scale(1));
                visibility: visible;
                opacity: 1;
              }
            }
          }

          &:hover{
            span{
              width: 5px;
              height: 5px;
              background: @color-theme;
              .border-radius(50%);
              position: relative;
              &:after{
                .transform(scale(1));
                visibility: visible;
                opacity: 1;
              }
            }
          }
        }
      }
    }
  }
}