/**
 * 2.0 Typography
 * -----------------------------------------------------------------------------
 */

h1,h2,h3,h4,h5,h6{
  font-family: 'Montserrat',sans-serif;
  font-weight: 400;
  color: #222222;
  margin:0px 0px 20px;
  padding:0px;
}
h1{
  font-size: 35px;
}
h2{
  font-size: 30px;
}
h3{
  font-size: 23px;
}
h4{
  font-size: 18px;
}
h5{
  font-size: 15px;
}
h6{
  font-size: 14px;
}

p{
  font-family: 'Ubuntu',sans-serif;
  font-weight: 300;
  font-size: 14px;
  line-height: 25px;
  color: #666666;
}
.tzFontWeight_Regular{
  p{
    font-weight: 400;
  }
}