/**
 * 5.0 Footer
 * -----------------------------------------------------------------------------
 */

.tzFooter{
  .tzFooterTop{
    background-image: url(images/background_footer.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: top center;
    position: relative;
    padding:70px 0px 50px;
    &:after{
      content: '';
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background-color: rgba(0,0,0,0.5);
      z-index:0;
    }
    .footerattr{
      z-index: 1;
      .widget{
        margin:0 0 22px;
        h3.title-widget{
          margin:0 0 20px;
          padding:0px;
          span{
            font-family: 'Montserrat',sans-serif;
            font-weight: 700;
            font-size: 14px;
            line-height: 27px;
            color: #ffffff;
            text-transform: uppercase;
            a{
              color: #FFFFFF;
            }
          }
        }
        label{
          display: none;
        }
        p{
          font-family: 'Ubuntu',sans-serif;
          font-size: 14px;
          font-weight: 300;
          line-height: 23px;
          color:#868686;
        }
        ul{
          margin:0px;
          padding:0px;
          li{
            list-style: none;
            font-family: 'Ubuntu',sans-serif;
            font-size: 14px;
            font-weight: 300;
            line-height: 23px;
            color:#868686;
            a{
              color:#868686;
              text-decoration: none;
            }
          }
        }
        select{
          width: 260px;
          max-width: 100%;
          font-family: 'Ubuntu',sans-serif;
          font-size: 14px;
          font-weight: 300;
          line-height: 23px;
          color:#868686;
          border:none;
          .border-radius(0);
          padding:12px;
          -moz-appearance:none;
          background: rgba(255, 255, 255, 1) url("images/bg_down.png") no-repeat scroll right 20px top 20px;
          option{
            width: 240px;
          }
        }

        &.widget_contact_info{
          .tzwidget-contact{
            span{
              display: block;
              margin:15px 0px 0px;
              font-family: 'Ubuntu',sans-serif;
              font-size: 14px;
              font-weight: 300;
              line-height: 23px;
              color:#868686;
              i{
                font-size: 16px;
                color: #868686;
                margin-right: 20px;
              }
            }
          }
        }
        &.widget_calendar{
          #calendar_wrap{
            table#wp-calendar{
              caption{
                font-family: 'Ubuntu',sans-serif;
                font-size: 14px;
                font-weight: 400;
                line-height: 23px;
                color:#868686;
                text-transform: uppercase;
              }
              thead{
                  border-width: 1px 1px 0px;
                  border-style: solid;
                  border-color: #868686;
                tr{
                  th{
                    font-family: 'Ubuntu',sans-serif;
                    font-size: 14px;
                    font-weight: 400;
                    line-height: 23px;
                    color:#868686;
                  }
                }
              }
              tbody{
                  border-width: 1px;
                  border-style: solid;
                  border-color: #868686;
                tr{
                  td{
                    font-family: 'Ubuntu',sans-serif;
                    font-size: 14px;
                    font-weight: 300;
                    line-height: 23px;
                    color:#868686;
                    padding: 5px 10px;
                  }
                }
              }
              tfoot{
                a{
                  font-family: 'Ubuntu',sans-serif;
                  font-size: 14px;
                  font-weight: 300;
                  line-height: 23px;
                  color:#868686;
                }
              }
            }
          }
        }
        &.widget_search{
          .searchform{
            position: relative;
            .Tzsearchform{
              font-family: 'Ubuntu',sans-serif;
              font-size: 14px;
              font-weight: 300;
              line-height: 23px;
              color:#868686;
              width: 100%;
              border:none;
              .border-radius(0);
              padding:12px 12px 13px;
            }
            .searchsubmit{
              background: none;
              border: medium none;
              border-radius: 0;
              font-size: 0;
              height: 48px;
              margin: 0;
              padding: 0;
              position: absolute;
              right: 0;
              top: 0;
              width: 48px;
              z-index: 1;
            }
            .icon_search{
              background: #fbc02d none repeat scroll 0 0;
              color: #ffffff;
              font-size: 17px;
              font-weight: bold;
              height: 48px;
              padding: 15px 0;
              position: absolute;
              right: 0;
              text-align: center;
              top: 0;
              width: 48px;
              z-index: 0;
            }
          }
        }
        &.widget_tag_cloud{
          .tagcloud{
            a{
              background: #868686 none repeat scroll 0 0;
              color: #ffffff;
              display: inline-block;
              font-family: "Ubuntu",sans-serif;
              font-size: 14px !important;
              font-weight: 300;
              line-height: 23px;
              margin: 0 1px 5px 0;
              padding: 5px 15px;
              text-decoration: none;
              .transition(all 0.3s ease-in-out 0s);
              &:hover{
                background: @color-theme;
              }
            }
          }
        }
        &.widget_nav_menu{
          .megamenu_container{
            ul.sub-menu{
              padding-left: 20px;
              li{
                a{
                border-bottom: 1px solid #8686868;
                }
              }
            }
          }
        }
        &.dw_twitter{
          .dw-twitter-inner{
            .tweet-item{
              position: relative;
              padding-left: 30px;
              margin-bottom: 35px;
              &:after{
                content: "\f099";
                font-family: FontAwesome;
                font-style: normal;
                font-weight: normal;
                font-size: 16px;
                color: @color-theme;
                position: absolute;
                top:4px;
                left: 0;
              }
              .twitter-user{
                a{
                  font-family: 'Ubuntu',sans-serif;
                  font-size: 14px;
                  font-weight: 300;
                  line-height: 23px;
                  color:#e4e4e4;
                  text-decoration: none;
                }
              }
              .tweet-content{
                font-family: 'Ubuntu',sans-serif;
                font-size: 14px;
                font-weight: 300;
                line-height: 23px;
                color:#868686;
                a{
                  color: #e4e4e4;
                  text-decoration: none;
                }
                span.time{
                  display: block;
                  margin-top: 10px;
                  a{
                    color: #868686;
                  }
                }
              }
              iframe{
                margin-top: 20px;
              }
            }
          }
        }
        &.widget_flickr{
          .tz-flickr{
            a{
              display: block;
              float: left;
              margin-bottom: 12px;
              margin-right: 12px;
              position: relative;
              img{
                margin:0px;
                float:none;
              }
              &:before{
                content: "";
                background: rgba(251,192,45,0.75);
                display: block;
                height: 100%;
                left: 0;
                position: absolute;
                top: 0;
                width: 0;
                .transition(all 0.3s ease-in-out 0s);
              }
              &:after{
                content: "\f00e";
                color: #ffffff;
                font-family: FontAwesome;
                font-size: 14px;
                display: block;
                height: 100%;
                left: 0;
                position: absolute;
                top: 0;
                width: 100%;
                text-align: center;
                padding:29px 0px;
                .transform(scale(0))
              }
              &:hover{
                &:before{
                  width: 100%;
                }
                &:after{
                  .transition(all 0.3s ease-in-out 0.3s);
                  .transform(scale(1))
                }
              }
            }
            &:after{
              content: '';
              display: block;
              clear: both;
            }
          }
        }
        &.widget_newsletterwidget{
          font-family: 'Ubuntu',sans-serif;
          font-size: 14px;
          font-weight: 300;
          line-height: 23px;
          color:#868686;
          .newsletter{
            margin-top: 36px;
            form{
              position: relative;
              .newsletter-email{
                font-family: 'Ubuntu',sans-serif;
                font-size: 14px;
                font-weight: 300;
                line-height: 23px;
                color:#868686;
                width: 100%;
                border:none;
                .border-radius(0);
                padding:12px;
                .box-shadow(none);
              }
              .newsletter-submit{
                background: none;
                border: medium none;
                border-radius: 0;
                font-size: 0;
                height: 48px;
                margin: 0;
                padding: 0;
                position: absolute;
                right: 0;
                top: 0;
                width: 48px;
                z-index: 1;
              }
              &:after{
                content: "\f00c";
                display: block;
                width: 47px;
                height: 47px;
                font-family: FontAwesome;
                font-style: normal;
                font-weight: normal;
                font-size: 16px;
                color: #ffffff;
                background: @color-theme;
                position: absolute;
                top:0px;
                right:0;
                text-align: center;
                padding:10px 0;
              }
            }
          }
        }
      }
    }
  }
  .tzFooterBottom{
    background: #151515;
    .tzCopyright{
      padding:37px 0 33px;
      p{
        font-family: 'Ubuntu',sans-serif;
        font-weight: 300;
        font-size: 14px;
        color: #868686;
        margin:0px;
        padding:0px;
        a{
          color: #868686;
          &:hover{
            color: @color-theme;
          }
        }
      }
    }
    .tzFooterSocial{
      padding:25px 0px;
      ul{
        margin:0px;
        padding:0px;
        &:after{
          content: '';
          display: block;
          clear: both;
        }
        li{
          list-style: none;
          float:left;
          padding:0px 5px;
          a{
            display: inline-block;
            width: 40px;
            height: 40px;
            .border-radius(50%);
            background: #313131;
            text-align: center;
            position: relative;
            i{
              padding:12px 0px;
              color:#868686;
              font-size: 15px;
              position: relative;
              z-index: 99;
              .transition(all 0.3s ease-in-out 0s);
            }
            &:after{
              content: '';
              display: block;
              width: 100%;
              height: 100%;
              .border-radius(50%);
              background: @color-theme;
              position: absolute;
              top:0;
              left: 0;
              .transform(scale(0));
              opacity: 0;
              .transition(all 0.3s ease-in-out 0s);
            }
            &:hover{
              i{
                color:#FFFFFF;
              }
              &:after {
                opacity: 1;
                .transform(scale(1));
              }
            }
          }
        }
      }
    }
  }
}