/* 11.5 Skill */
.tzSkill{
  &.tzskill-item-type1{
    overflow: hidden;
    .tzSkill-title{
      font-family: 'Ubuntu',sans-serif;
      font-weight: 700;
      font-size: 14px;
      color: #222222;
      margin:0px;
      padding: 10px 0 5px;
    }
    .tzskill-item{
      width: 100%;
      height: 3px;
      background: #e5e5e5;
      .tzskill-item-width{
        height: 100%;
        background: @color-theme;
        position: relative;
        span{
          background: #222222 none repeat scroll 0 0;
          border-radius: 2px;
          color: #ffffff;
          display: inline-block;
          font-family: "Ubuntu",sans-serif;
          font-size: 12px;
          font-weight: 300;
          padding: 2px 8px;
          position: absolute;
          right: -19px;
          top: -33px;
          &:after{
            content: '';
            border-left: 5px solid transparent;
            border-right: 5px solid transparent;
            border-top: 5px solid #222222;
            position: absolute;
            top:100%;
            left: 50%;
            margin-left: -5px;
          }
        }
      }
    }
  }

  &.tzskill-item-type2{
    overflow: hidden;
    position: relative;
    padding: 0 0 5px;
    .tzSkill-title{
      font-family: 'Ubuntu',sans-serif;
      font-weight: 700;
      font-size: 14px;
      color: #222222;
      margin:0px;
      padding: 0 0 5px;
    }
    span{
      color: #222222;
      font-family: "Ubuntu",sans-serif;
      font-size: 14px;
      font-weight: 300;
      position: absolute;
      right: 0;
      top: 2px;
    }
    .tzskill-item{
      width: 100%;
      height: 5px;
      background: #e5e5e5;
      .border-radius(3px);
      .tzskill-item-width{
        height: 100%;
        background: @color-theme;
        .border-radius(3px);
        position: relative;
        .tzSkill_Circle{
          width: 15px;
          height: 15px;
          background: #FFFFFF;
          border:4px solid @color-theme;
          .border-radius(50%);
          position: absolute;
          top:-5px;
          right: 0;
        }
      }
    }
  }

  &.tzskill-item-type3{
    overflow: hidden;
    position: relative;
    .tzSkill-title{
      font-family: 'Ubuntu',sans-serif;
      font-weight: 700;
      font-size: 14px;
      color: #222222;
      margin:0px;
      padding: 0 0 5px;
    }
    span{
      color: #222222;
      font-family: "Ubuntu",sans-serif;
      font-size: 14px;
      font-weight: 300;
      position: absolute;
      right: 0;
      top: 2px;
    }
    .tzskill-item{
      width: 100%;
      height: 8px;
      background: #e5e5e5;
      padding:2px;
      .border-radius(3px);
      .tzskill-item-width{
        height: 100%;
        background: @color-theme;
        .border-radius(3px);
        position: relative;
      }
    }
  }

  &.tzskill-item-type4{
    text-align: center;
    .chart {
      position: relative;
      display: inline-block;
      margin:0 0 30px;
      padding:0px;
      canvas {
        position: absolute;
        top: 0;
        left: 0;
      }
      .tzSkill-box{
        width: 100%;
        height: 100%;
        padding: 30px;
        display: table;
        .tzSkill-box-1{
          width: 100%;
          height: 100%;
          background: #f3f3f3;
          padding:18px 0px;
          display: table-cell;
          vertical-align: middle;
          .border-radius(50%);
          .percent {
            display: inline-block;
            font-family: 'Ubuntu',sans-serif;
            font-size: 33px;
            font-weight: 300;
            color: #222222;
            z-index: 2;
            position: relative;
            padding-right: 15px;
            &:after {
              content: '%';
              font-family: 'Ubuntu',sans-serif;
              font-size: 20px;
              color: #222222;
              position: absolute;
              top:5px;
              right: 0;
            }
          }
        }
      }
    }
    .tzSkill-title{
      font-weight: 700;
      margin-bottom: 18px;
      padding:0px 20px;
    }
    .tzSkill-des{
      padding:0px 20px;
    }
  }

