@media (max-width: 992px) {
  .container {
    width: 100%;
  }

  .tz-header{
    .tz-headerTop{
      .tz-headerLeft{
        float: none !important;
        text-align: center;
        .tzheader_support{
          display: inline-block;
        }
      }
      .tz-headerRight{
        float: none !important;
        display: block;
        text-align: center;
        ul.tzheader_social{
          display: inline-block;
          li{
            &:first-child{
              padding-left: 0;
            }
          }
        }
      }
    }

    .tz-headerBottom{
      .navbar-toggle {
        display: block;
        color: #bebebe;
        font-size: 17px;
        font-weight: 300;
        padding: 41px 12px 37px 0;
        margin:0px;
      }
      nav{
        position: absolute;
        top:100%;
        right: 0;
        z-index: 999999;
        width: 100%;
        padding:0 15px;
        .navbar-collapse{
          &.collapse {
            display: none !important;
            height: auto !important;
            padding-bottom: 0;
            overflow: visible !important;
            &.in{
              display: block!important;
            }
          }
        }
        ul.navbar-nav {
          width: 100%;
          background: #ffffff;
          border-top: 3px solid #222222;
          padding:0px;
          .box-shadow(0 0 5px 2px rgba(0, 0, 0, 0.1));
          & > li {
            float: none;
            z-index: 999999;
            &.menu-item-has-children{
             &:after{
               color: #454545;
               content: "\f105";
               font-family: FontAwesome;
               font-size: 14px;
               position: absolute;
               right: 20px;
               top: 12px;
             }
              &:hover{
                &:after{
                  color: #454545;
                  content: "\f107";
                  font-family: FontAwesome;
                  font-size: 14px;
                  position: absolute;
                  right: 20px;
                  top: 12px;
                }
              }
            }
          }
          li{
            a{
              padding:15px;
              border-bottom: 1px solid #ededed;
              background: #ffffff;
              &:hover{
                background-color: #dfdfdf;
              }
            }

            ul.non_mega_menu{
              position: relative;
              top: 0;
              background: none;
              border:none;
              .box-shadow(none);
              visibility: hidden;
              opacity: 1;
              width: 100%;
              height: 0;
              li{
                border: none;
                a{
                  padding:15px;
                  border-bottom: 1px solid #ededed;
                  background: #ffffff;
                  &:hover{
                    background-color: #eee;
                  }
                }
              }

            }
            .themeple_custom_menu_mega_menu{
              position: relative;
              top: 0;
              background: none;
              border:none;
              .box-shadow(none);
              visibility: hidden;
              opacity: 1;
              height: 0;
              &.themeple_mega2{
                width: 100% !important;
              }
              &.themeple_mega3{
                width: 100% !important;
              }
              &.themeple_mega4{
                width: 100% !important;
              }
              &.themeple_mega5{
                width: 100% !important;
              }

              ul.sub-menu{
                padding:0px;
                li{
                  padding:0px;
                  float: none;
                  width: 100% !important;
                  border: none;
                  a{
                    padding:15px;
                    background: #ffffff;
                    &:hover{
                      background-color: #eee;
                    }
                  }
                }
              }
            }
            &:hover{
              > a{
                background-color: #dfdfdf;
              }
              ul.non_mega_menu{
                top: 0;
                visibility: visible;
                opacity: 1;
                height: auto;
                .transition(all 0.3s ease-in-out 0s);
              }
              .themeple_custom_menu_mega_menu{
                top: 0;
                visibility: visible;
                opacity: 1;
                height: auto;
                .transition(all 0.3s ease-in-out 0s);
              }
            }
          }
        }
      }
      &.tz-max-mega-menu{
        .navbar-toggle {
          display: none;
        }
        nav{
          #mega-menu-wrap-primary{
            .mega-menu-toggle{
              position: absolute;
              top:-70px;
              right: 70px;
              &:after{
                content: none;
              }
              &:before{
                content: '\f0c9';
                font-family: FontAwesome;
                font-size: 17px;
                font-weight: 300;
                color:#bebebe;
                .transition(all 0.3s ease-in-out 0s);
              }
              &.mega-menu-open{
                &:before{
                  content: '\f0c9';
                  font-family: FontAwesome;
                  font-size: 17px;
                  font-weight: 300;
                  color:#bebebe;
                  .transition(all 0.3s ease-in-out 0s);
                }
              }

              &:hover{
                &:before{
                  color:@color-theme;
                }
              }
            }

            ul#mega-menu-primary{
              border-top: 3px solid #222222;
              .box-shadow(0 0 5px 2px rgba(0, 0, 0, 0.1));
              li{
                a{
                  line-height: 24px;
                  padding: 8px 15px;
                  border-bottom: 1px solid #ededed
                }
                ul.mega-sub-menu{
                  display: block !important;
                  visibility: hidden;
                  opacity: 1;
                  top: 100%;
                  .box-shadow(none);
                  height: 0;
                  .transition(all 0.3s ease-in-out 0s);
                  li{
                    a{
                      line-height: 24px;
                      padding: 8px 15px;
                      border-bottom: 1px solid #ededed;
                    }
                  }
                }

                &.mega-menu-megamenu{
                  ul.mega-sub-menu{
                    padding:0px;
                    li{
                      width: 100% !important;
                      padding:0px;
                      h4{
                        padding:10px 15px;
                        margin-bottom: 20px;
                      }
                      ul.product_list_widget{
                        li{
                          a{
                            display: block;
                            padding:0 15px;
                            border: none;
                            img{
                              margin:0 20px 20px 0;
                            }
                          }
                        }
                      }
                    }
                  }
                }

                &:hover{
                  ul.mega-sub-menu{
                    height: auto;
                    visibility: visible;
                  }
                }
              }
            }
          }
        }
      }
    }
  }

  .tzElement_Ourteam{
    &.tzOurteam_type1{
      .tzOurteam_imageContainer{
        .tzOurteam_ImageBox{
          .tzOurteam_Image{
            width: 160px;
            height: 160px;
          }
        }
      }
    }
  }

  .tzElement_Quote{
    &.tzQuote_type1{
      .tzQuote_Item{
        padding:0 100px;
      }
    }
  }

  .tzElement_Quote{
    &.tzQuote_type2{
      .tzQuote_Item{
        padding:0 70px;
      }
    }
  }

  .tzshop-wrap {
    .col-padding{
      float: none;
      .product-grid{
        ul.products{
          li.tzShop-item{
            &.tzShop-4column{
              width: 50%;
            }
            &.tzShop-3column{
              width: 50%;
            }
            &.tzShop-2column{
              width: 50%;
            }
          }
        }
      }
    }
  }

  .upsells{
    border-bottom: 1px solid #ededed;
    display: block;
    margin-bottom: 30px;
    padding: 0 0 15px;
    h2{
      font-family: 'Montserrat',sans-serif;
      font-size: 20px;
      font-weight: 700;
      color: #222222;
      line-height: 24px;
      text-transform: uppercase;
      margin-bottom: 15px;
    }
    ul.products{
      li.tzShop-item{
        width: 50%;
      }
    }
  }

  .related{
    ul.products{
      li.tzShop-item{
        width: 50%;
      }
    }
  }

  .tzShopDetail-wrap{
    .tzShopContentDetail{
      .tzShopDetail_Product{
        .tzShopDetail_info{
          h1.product_title{
            margin-top: 20px;
          }
        }
      }
    }
  }

  .woocommerce{
    .cart-collaterals{
      .cross-sells{
        ul.products{
          li.tzShop-item{
            width: 50%;
          }
        }
      }
      .tzCart_totals{
        .tzCollateralsColumn{
          form.woocommerce-shipping-calculator{
            margin-bottom: 100px;
          }
        }
      }
    }
    form{
      table.shop_table{
        tbody{
          tr{
            td.actions{
              .coupon{
                float: left;
              }
              input.button{
                width: auto;
                float: right;
              }
            }
          }
        }
      }
    }
  }

  .woocommerce-account{
    .woocommerce{
      .addresses{
        .address{
          &.col-1{
            margin-bottom: 50px;
          }
        }
      }
    }
  }

  .tzBlogColumn{
    .tzBlogContainer{
      .blogColumn-item{
        .tzBlogInner{
          .tzBlogQuote{
            p{
              padding:0 30px;
            }
          }
        }
      }
    }
  }

  .tz-sidebar{
    &.tz-sidebar-right{
      margin-top: 95px;
    }
    &.tz-sidebar-left{
      margin-bottom: 95px;
    }
    .widget{
      &.widget_search{
        form{
          width: 265px;
        }
      }
      select{
        width: 265px;
      }
      &.widget_calendar{
        #calendar_wrap{
          table{
            width: 265px;
          }
        }
      }
    }
  }

  form#yith-wcwl-form{
    table.wishlist_table{
      tbody{
        tr{
          td.product-add-to-cart{
            a{
              width: 110px;
              padding:10px 5px;
            }
          }
        }
      }
    }
  }

  .tzFooter{
    .tzFooterTop{
      .footerattr{
        .widget{
          &.widget_newsletterwidget{
            .newsletter{
              form{
                width: 265px;
              }
            }
          }
        }
      }
    }
  }
}