/* 11.1 Vc Row */
.wpb_row, .wpb_content_element, ul.wpb_thumbnails-fluid &>li, .last_toggle_el_margin, .wpb_button {
  margin-bottom: 0px!important;
}
.overlay_parallax {
  height: 100%;
  left: 0;
  position: absolute !important;
  top: 0;
  width: 100%;
  z-index: -9999998 !important;
}
.tzPattern{
  height: 100%;
  left: 0;
  position: absolute !important;
  top: 0;
  width: 100%;
  z-index: -9999997 !important;
  background-image: url(images/patternx2.png);
  background-repeat: repeat;
}
.vc_parallax{
  .vc_parallax-inner{
    z-index: -9999999 !important;
  }
}
body#bd {
  overflow-X: hidden;
  position: relative;
}
body {
  > .vc_row {
    margin:0px;
  }

  >.vc_row-fluid {
    //  > .container {
    //    padding:0px;
    //  }
    >.no_container{
      >.vc_col-sm-12{
        padding:0px;
      }
      >.vc_col-sm-6{
        padding:0px;
      }
    }
  }
}
.no_container{
  &:after{
    content: '';
    display: block;
    clear: both;
  }
//  >.vc_col-sm-12{
//    padding:0px;
//  }
}

.wpb_wrapper{
  &:after{
    content: '';
    display: block;
    clear: both;
  }
}
.vc_row{
  .vc_column_container{
    padding-left: 15px;
    padding-right: 15px;
  }
}
