/**
 * 8.0 Sidebar
 * -----------------------------------------------------------------------------
 */
.tz-sidebar{
  .widget{
    margin-bottom: 43px;
    h3.title-widget{
      font-family: 'Montserrat',sans-serif;
      font-size: 14px;
      font-weight: 700;
      line-height: 24px;
      color: #222222;
      text-transform: uppercase;
      margin:0 0 13px;
      padding:0px;
    }
    label{
      font-family: 'Ubuntu',sans-serif;
      font-size: 14px;
      font-weight: 300;
      color: #666666;
      line-height: 24px;
    }
    ul{
      margin:0px;
      padding:0px;
      li{
        list-style: none;
        font-family: 'Ubuntu',sans-serif;
        font-size: 14px;
        font-weight: 300;
        color: #666666;
        line-height: 24px;
        position:relative;
        border-bottom:1px solid #ededed;
        padding:14px;
        &:first-child{
          padding-top: 0;
          &:before{
            top: 2px;
          }
        }
        &:before{
          position: absolute;
          top:15px;
          left: 0px;
          content: '\f0da';
          font-family: FontAwesome;
          color: #666666;
          font-size: 10px;
        }
        a{
          color: #666666;
          text-decoration: none;
        }
      }
    }
    ul.product_list_widget{
      li{
        padding:0 0 20px;
        margin:0 0 20px;
        &:before{
          content: none;
        }
        &:after{
          content: '';
          display: block;
          clear: both;
        }
        padding:0 0 20px;
        margin:0 0 20px;

        a{
          font-family: 'Montserrat',sans-serif;
          font-size: 13px;
          font-weight: 400;
          line-height: 24px;
          color: #222222;
          img{
            width: 70px;
            height: auto;
            margin:0 15px 0 0;
            float: left;
          }
          span{
            font-family: 'Montserrat',sans-serif;
            font-size: 13px;
            font-weight: 400;
            line-height: 24px;
            color: #222222;
            margin:0 0 2px;
            padding:0px;
          }
        }

        .star-rating{
          span{
            &:before{
              color: @color-theme;
            }
          }
        }

        span.reviewer{
          color: @color-theme;
        }

        span.amount{
          font-family: 'Ubuntu',sans-serif;
          font-size: 13px;
          font-weight: 300;
          line-height: 24px;
          color: @color-theme;
        }

        del{
          span.amount{
            font-family: 'Ubuntu',sans-serif;
            font-size: 13px;
            font-weight: 300;
            line-height: 24px;
            color: #666;
          }
        }

        ins{
          text-decoration: none;
          color: @color-theme;
          span.amount{
            font-family: 'Ubuntu',sans-serif;
            font-size: 13px;
            font-weight: 300;
            line-height: 24px;
            color: @color-theme;
          }
        }
      }
    }
    .widget_shopping_cart_content{
      ul.product_list_widget{
        li{
          &:before{
            content: none;
          }
          &:after{
            content: '';
            display: block;
            clear: both;
          }
          padding:0 0 20px;
          margin:0 0 20px;
          .tzMiniCart_image{
            float: left;
            a{
              img{
                float: none;
                width: 70px;
                height: auto;
                margin:0;
              }
            }
          }
          .tzMiniCart_info{
            margin-left: 85px;
            a{
              font-family: 'Montserrat',sans-serif;
              font-size: 13px;
              font-weight: 400;
              line-height: 24px;
              color: #222222;
              margin:0 0 2px;
              padding:0px;
            }
            span.quantity{
              font-family: 'Ubuntu',sans-serif;
              font-size: 13px;
              font-weight: 300;
              line-height: 24px;
              color: @color-theme;
              margin:0px;
              padding:0px;
            }
          }
          a.remove{
            right: 0;
            left: auto;
            color: #222 !important;
            height: 24px;
            width: 24px;
            .transition(all 0.3s ease-in-out 0s);
            &:hover{
              background: @color-theme;
              color: #fff !important;
            }
          }
          &:last-child{
            border:none;
            padding:0;
            margin:0;
          }
        }

        &.cart_list{
          margin-bottom: 20px;
        }
      }
      p.total{
        border-top: 1px solid #ededed;
        padding:20px 0;
        margin:0;
      }
      p.buttons{
        a{
          background-color: #222;
          border-radius: 20px;
          color: #ffffff;
          font-family: "Ubuntu",sans-serif;
          font-size: 14px;
          font-weight: 400;
          line-height: 24px;
          padding: 7px 20px;
          .transition(all 0.3s ease-in-out 0s);
          &:hover{
            background:@color-theme;
          }
        }
      }
    }
    select {
      appearance:none;
      -moz-appearance:none;
      -webkit-appearance:none;
      -ms-appearance:none;
      -o-appearance:none;
//      background: url("images/bg_down.png") no-repeat scroll right 20px top 19px rgba(0, 0, 0, 0);
      border:1px solid #dedede;
      font-family: 'Ubuntu',sans-serif;
      font-weight: 300;
      font-style: italic;
      font-size: 14px;
      line-height: 24px;
      color:#b5b5b5;
      width:100%;
      padding:10px 15px;
    }
    &.widget_archive{
      h3.title-widget{
        margin-bottom: 18px;
      }
      label{
        display: none;
      }
    }
    &.widget_categories{
      h3.title-widget{
        margin-bottom: 18px;
      }
      label{
        display: none;
      }
      ul{
        li{
          &:before{
            content: none;
          }
          padding: 0 0 0 15px;
          border-bottom:1px solid #ededed;
          a{
            display: inline-block;
            padding: 14px 0px;
          }
          ul.children{
            li{
              border-bottom: none;
              border-top:1px solid #ededed;
            }
          }
        }
      }
    }
    &.widget_pages{
      ul{
        li{
          &:before{
            content: none;
          }
          padding: 0 0 0 15px;
          border:none;
          a{
            display: block;
            padding: 14px 0px;
            border-bottom:1px solid #ededed;
          }
        }
      }
    }
    &.widget_search {
      h3.title-widget{
        margin-bottom: 18px;
      }
      form {
        position: relative;
        label {
          display: none;
        }
        input.Tzsearchform {
          width: 100%;
          background: none;
          border:1px solid #dedede;
          font-family: 'Ubuntu',sans-serif;
          font-size: 14px;
          line-height: 24px;
          font-weight: 300;
          font-style: italic;
          padding: 9px 45px 10px 15px;
          color: #111111;
        }
        input.searchsubmit {
          background-color: @color-theme;
          background-image: url(images/search.png)!important;
          background-position: center!important;
          background-repeat: no-repeat!important;
          border: none;
          font-size: 0;
          width:45px;
          height:45px;
          position: absolute;
          top:0;
          right:0;
          &:after{
            content: '';
            display: block;
            width: 20px;
            height: 20px;
            position: absolute;
            top:0px;
            left: 0px;
            background: #ffffff;
          }
        }
        span.icon_search{
          display: none;
        }
      }
    }
    &.widget_tag_cloud{
      h3.title-widget{
        margin-bottom: 18px;
      }
      .tagcloud{
        a{
          display: inline-block;
          float: left;
          margin: 0px 10px 10px 0px;
          padding:4px 11px 6px;
          font-family: 'Ubuntu',sans-serif;
          font-weight: 300;
          font-size: 14px !important;
          text-decoration: none;
          background: #fafafa;
          color: #666666;
          border:1px solid #dedede;
          .border-radius(2px);
          .transition(all 0.3s ease-in-out 0s);
          &:hover{
            border-color: @color-theme;
            background: @color-theme;
            color: #ffffff;
          }
        }
        &:after{
          content: '';
          clear: both;
          display: block;
        }
      }
    }

    &.widget_calendar {
      #calendar_wrap {
        table {
          width: 100%;
          caption {
            color: #222222;
            font-family: "Montserrat",sans-serif;
            font-size: 14px;
            font-weight: 400;
            line-height: 24px;
            padding: 15px 0;
            text-align: center;
          }
          thead {
            border-width: 1px 1px 1px 1px;
            border-style: solid;
            border-color: #dddddd;
            tr {
              th {
                text-align: center;
                color: #222222;
                font-family: "Ubuntu",sans-serif;
                font-size: 14px;
                font-weight: 300;
                padding:15px 9px;
              }
            }
          }
          tbody {
            border-width: 1px 1px 1px 1px;
            border-style: solid;
            border-color: #dddddd;
            tr {
              td {
                text-align: center;
                color: #666666;
                font-family: "Ubuntu",sans-serif;
                font-size: 14px;
                font-weight: 300;
                padding:8px;
              }
              td#today {
                background-color:@color-theme;
                color: #FFFFFF;
              }
            }
          }
          tfoot {
            tr {
              td {
                a {
                  color: #666666;
                  font-family: "Roboto Slab",serif;
                  font-size: 13px;
                  font-weight: 400;
                  padding:6px;
                  display: inline-block;
                }
              }
            }
          }
        }
      }
    }
    &.widget_text{
      .textwidget{
        font-family:'Ubuntu',sans-serif;
        font-size: 14px;
        font-weight: 300;
        color: #666666;
        line-height: 25px;
      }
    }
    &.widget_rss{
      h3.title-widget{
        span{
          a{
            color: #222222;
          }
        }
      }
    }
    &.widget_nav_menu{
      ul{
        li{
          &:before{
            content: none;
          }
          padding: 0 0 0 15px;
          border:none;
          a{
            display: block;
            padding: 14px 0px;
            border-bottom:1px solid #ededed;
          }
        }
      }
    }
    &.widget_price_filter{
      form{
        margin-top: 45px;
        .price_slider_wrapper{
          .ui-widget-content{
            background-color: #ededed;
            .ui-slider-range{
              background: @color-theme;
            }
            .ui-slider-handle{
              border:3px solid @color-theme;
              background: #fff;
            }
          }

          .price_slider_amount{
            margin-top: 30px;
            button.button{
              float: right;
              background-color: #222;
              border-radius: 20px;
              color: #ffffff;
              font-family: "Ubuntu",sans-serif;
              font-size: 14px;
              font-weight: 400;
              line-height: 24px;
              padding: 7px 20px;
              .transition(all 0.3s ease-in-out 0s);
              &:hover{
                background:@color-theme;
              }
            }

            .price_label{
              text-align: left;
              font-family: "Ubuntu",sans-serif;
              font-size: 14px;
              font-weight: 300;
              line-height: 24px;
              color: #666666;
              padding-top: 7px;
            }
          }
        }
      }
    }

    &.widget_product_search{
      form.woocommerce-product-search{
        position: relative;
        label {
          display: none;
        }
        input[type="submit"]{
          background-color: @color-theme;
          background-image: url(images/search.png)!important;
          background-position: center!important;
          background-repeat: no-repeat!important;
          border: none;
          font-size: 0;
          width:45px;
          height:45px;
          position: absolute;
          top:0;
          right:0;
          &:after{
            content: '';
            display: block;
            width: 20px;
            height: 20px;
            position: absolute;
            top:0px;
            left: 0px;
            background: #ffffff;
          }
        }
        input.search-field {
          width: 100%;
          background: none;
          border:1px solid #dedede;
          font-family: 'Ubuntu',sans-serif;
          font-size: 14px;
          line-height: 24px;
          font-weight: 300;
          font-style: italic;
          padding: 9px 45px 10px 15px;
          color: #111111;
        }
        &:after{
          content: '';
          display: block;
          clear: both;
        }
      }
    }

    &.widget_product_tag_cloud{
      .tagcloud{
        a{
          display: inline-block;
          float: left;
          margin: 0px 10px 10px 0px;
          padding:4px 11px 6px;
          font-family: 'Ubuntu',sans-serif;
          font-weight: 300;
          font-size: 14px !important;
          text-decoration: none;
          background: #fafafa;
          color: #666666;
          border:1px solid #dedede;
          .border-radius(2px);
          .transition(all 0.3s ease-in-out 0s);
          &:hover{
            border-color: @color-theme;
            background: @color-theme;
            color: #ffffff;
          }
        }
        &:after{
          content: '';
          clear: both;
          display: block;
        }
      }
    }

    &.tz-recent-w{
      ul{
        li{
          margin: 0 0 20px;
          padding: 0 0 20px;
          &:before{
            content: none;
          }
          &:after{
            content: '';
            display: block;
            clear: both;
          }
          &:last-child{
            border:none;
            margin:0;
            padding:0;
          }
          img{
            float: left;
            width: 77px;
            height: auto;
            margin-right: 15px;
          }
          .tz-recent-content{
            a{
              display: block;
              font-family: 'Montserrat',sans-serif;
              font-size: 13px;
              font-weight: 400;
              color: #222222;
              line-height: 24px;
              text-decoration: none;
              margin-bottom: 4px;
            }
            span{
              color: @color-theme;
            }
          }
        }
      }
      &.tz-recent-grid{
        ul{
          li{
            margin:0 10px 10px 0px;
            padding:0;
            border:none;
            float: left;
            position: relative;
            &:nth-child(3n+1){
              clear: both;
            }
            img{
              margin:0;
            }
            .tz-recent-overlay{
              position: absolute;
              top:0;
              left: 0;
              width: 0%;
              height: 100%;
              background: rgba(251, 192, 45, 0.75);
              .transition(all 0.3s ease-in-out 0s);
            }
            a{
              position: absolute;
              top:30%;
              left: 40%;
              .transform(scale(0));
              .transition(all 0.3s ease-in-out 0.3s);
              i{
                color: #fff;
              }
            }
            &:hover{
              .tz-recent-overlay{
                width: 100%;
              }
              a{
                .transform(scale(1));
              }
            }
          }
          &:after{
            content: '';
            display: block;
            clear: both;
          }
        }
      }
    }

    &.tz-latest-project{
      ul{
        li{
          margin: 0 0 20px;
          padding: 0 0 20px;
          &:before{
            content: none;
          }
          &:after{
            content: '';
            display: block;
            clear: both;
          }
          &:last-child{
            border:none;
            margin:0;
            padding:0;
          }
          img{
            float: left;
            width: 77px;
            height: auto;
            margin-right: 15px;
          }
          .tz-latest-content{
            a{
              display: block;
              font-family: 'Montserrat',sans-serif;
              font-size: 13px;
              font-weight: 400;
              color: #222222;
              line-height: 24px;
              text-decoration: none;
              margin-bottom: 4px;
            }
            span{
              color: @color-theme;
            }
          }
        }
      }
      &.tz-latest-grid{
        ul{
          li{
            margin:0 10px 10px 0px;
            padding:0;
            border:none;
            float: left;
            position: relative;
            max-height: 77px;
            overflow: hidden;
            &:nth-child(3n+1){
              clear: both;
            }
            img{
              margin:0;
            }
            .tz-latest-overlay{
              position: absolute;
              top:0;
              left: 0;
              width: 0%;
              height: 100%;
              background: rgba(251, 192, 45, 0.75);
              .transition(all 0.3s ease-in-out 0s);
            }
            a{
              position: absolute;
              top:30%;
              left: 40%;
              .transform(scale(0));
              .transition(all 0.3s ease-in-out 0.3s);
              i{
                color: #fff;
              }
            }
            &:hover{
              .tz-latest-overlay{
                width: 100%;
              }
              a{
                .transform(scale(1));
              }
            }
          }
          &:after{
            content: '';
            display: block;
            clear: both;
          }
        }
      }
    }
  }
}
