/**
 * 15.0 404 Page
 * -----------------------------------------------------------------------------
 */

.page-404{
  padding:75px 0 120px;
  .error{
    .bug-content{
      h3{
        text-align: center;
        margin-bottom: 10px;
      }
      p{
        font-size: 16px;
        text-align: center;
      }
      span{
        display: block;
        text-align: center;
        font-family: 'Montserrat',sans-serif;
        font-weight: 700;
        font-size: 250px;
        line-height: 250px;
        color: #bebebe;
      }
      a.go-back{
        display: block;
        text-align: center;
        font-family: 'Ubuntu',sans-serif;
        font-size: 16px;
        color: #666;
        &:hover{
          color: @color-theme;
          text-decoration: none;
        }
      }
    }
  }
}