/* 6.2 Blog Default */
.tzBlogDefault{
  background:#fafafa;
  padding: 90px 0px;
  .tzBlogContainer{
    .tzBlogItem{
      background: #fff;
      margin-bottom: 50px;

      .tzBlogQuote{
        position: relative;
        z-index: 1;
        padding:47px 0px 48px;
        .tzBlogQuote_bg{
          position: absolute;
          top:0px;
          left: 0px;
          width: 100%;
          height: 100%;
          overflow: hidden;
          z-index: -1;
          img{
            position: relative;
            max-width: none;
          }
        }
        .tzBlogQuote_overlay{
          position: absolute;
          top:0px;
          left: 0px;
          width: 100%;
          height: 100%;
          background: rgba(1, 7, 20, 0.75);
          z-index: -1;
        }
        .tzBlogQuote_char{
          display: block;
          text-align: center;
          font-size: 90px;
          color: @color-theme;
          line-height: 90px;
          margin-bottom: 13px;
        }
        p{
          font-family: 'Ubuntu',sans-serif;
          font-size: 20px;
          font-weight: 400;
          font-style: italic;
          color: #ffffff;
          line-height: 35px;
          text-align: center;
          padding:0px 52px;
          margin:0px 0px 72px;
        }
        .tzBlogQuote_info{
          display: block;
          margin:0px;
          text-align: center;
          a{
            font-family: 'Ubuntu',sans-serif;
            font-size: 14px;
            font-weight: 300;
            font-style: italic;
            line-height: 22px;
            color: #FFFFFF;
            margin: 0 3px 0 0;
            text-decoration: none;
          }
          small{
            font-family: 'Ubuntu',sans-serif;
            font-size: 14px;
            font-weight: 300;
            font-style: italic;
            line-height: 22px;
            color: #FFFFFF;
            i{
              margin: 0 5px 0 0;
            }
          }
          span{
            font-family: 'Ubuntu',sans-serif;
            font-size: 14px;
            font-weight: 300;
            font-style: italic;
            line-height: 22px;
            color: #b5b5b5;
            i{
              margin: 0 5px 0 0;
            }
          }
        }
      }

      .tzBlog_videoHtml5{
        width: 100%;
        overflow: hidden;
        position: relative;
        .tzblog_autoplay{
          background: @color-theme;
          border: 0 none;
          .border-radius(50%);
          height:50px;
          left: 50%;
          margin-left: -25px;
          margin-top: -25px;
          opacity: 1;
          position: absolute;
          top: 50%;
          width: 50px;
          z-index: 99;
          text-align: center;
          i{
            display: inline-block;
            font-size: 16px;
            color: #FFFFFF;
            padding: 17px 0px;
          }
          &:hover{
            cursor: pointer;
          }
        }
        .tzblog_pauses{
          background: @color-theme;
          border: 0 none;
          .border-radius(50%);
          height:50px;
          left: 50%;
          margin-left: -25px;
          margin-top: -25px;
          opacity: 1;
          position: absolute;
          top: 50%;
          width: 50px;
          z-index: 99;
          text-align: center;
          display: none;
          opacity: 0;
          i{
            display: inline-block;
            font-size: 16px;
            color: #FFFFFF;
            padding: 17px 0px;
          }
          &:hover{
            cursor: pointer;
          }
        }
        &:hover .tzblog_pauses{
          opacity: 1;
        }
        .bg-video{
          height: 100%;
          left: 0;
          position: absolute;
          top: 0;
          width: 100%;
          z-index: 1;
        }
        .videoID{
          width: 100%;
          height: auto;
          overflow: hidden;
          position: absolute;
          top: 0;
          left: 0;
        }

        .tzBlogDate{
          position: absolute;
          top:0px;
          left: 0px;
          z-index: 999;
          .tzDateText{
            width: 60px;
            height: 55px;
            background: @color-theme;
            padding:8px 5px;
            .tzTextDay{
              display: block;
              font-family: 'Montserrat',sans-serif;
              font-weight: 400;
              font-size: 20px;
              line-height: 20px;
              color: #ffffff;
              text-align: center;
              margin-bottom: 4px;
            }
            .tzTextMonth{
              display: block;
              font-family: 'Montserrat',sans-serif;
              font-weight: 700;
              font-size: 11px;
              line-height: 11px;
              color: #ffffff;
              text-align: center;
              text-transform: uppercase;
            }
          }
          .tzBlogIcon{
            width: 60px;
            height: 55px;
            background: #444444;
            text-align: center;
            i{
              font-size: 22px;
              color: #fff;
              padding: 18px;
            }
          }
        }
      }
