      .tzBlogSlider{
        position: relative;
        ul.flex-direction-nav{
          z-index: 9999;
          li{
            a{
              top:50%;
              opacity: 1;
              height: 30px;
              width: 30px;
              margin:-15px 0 0;
              .border-radius(50%);
              border:1px solid #FFFFFF;
              text-align: center;
              background: none;
              &:before{
                font-size: 10px;
                font-weight: lighter;
                color: #ffffff;
                text-shadow: none;
              }
              &.flex-prev{
                left: 30px;
              }
              &.flex-next{
                right: 30px;
              }
              &:hover{
