    .tzserach_notda{
      .page-content{
        form {
          position: relative;
          width: 263px;
          margin-top: 30px;
          label {
            display: none;
          }
          input.Tzsearchform {
            width: 263px;
            background: none;
            border:1px solid #dedede;
            font-family: 'Ubuntu',sans-serif;
            font-size: 14px;
            line-height: 24px;
            font-weight: 300;
            font-style: italic;
            padding: 9px 45px 10px 15px;
            color: #111111;
          }
          input.searchsubmit {
            background-color: @color-theme;
            background-image: url(images/search.png)!important;
            background-position: center!important;
            background-repeat: no-repeat!important;
            border: none;
            font-size: 0;
            width:45px;
            height:45px;
            position: absolute;
            top:0;
            right:0;
            &:after{
              content: '';
              display: block;
              width: 20px;
              height: 20px;
              position: absolute;
              top:0px;
              left: 0px;
              background: #ffffff;
            }
          }
          span.icon_search{
            display: none;
          }
        }
      }
    }
