  .wp-pagenavi{
    span{
      font-family: 'Ubuntu',sans-serif;
      font-size: 14px;
      font-weight: 500;
      line-height: 24px;
      color: #666666;
      padding: 10px 15px;
      background: #dfdfdf;
      .border-radius(2px);
      border:none;
      &.current{
        color: #FFFFFF;
        background: @color-theme;
      }
    }
    a{
      font-family: 'Ubuntu',sans-serif;
      font-size: 14px;
      font-weight: 500;
      line-height: 24px;
      color: #666666;
      padding: 10px 15px;
      background: #dfdfdf;
      .border-radius(2px);
      text-decoration: none;
      border: none;
      .transition(all 0.3s ease-in-out 0s);
      &:hover{
        color: #FFFFFF;
        background: @color-theme;
      }
    }
  }
  ol.flex-control-nav{
    bottom:8px;
    li{
      margin: 0 5px;
      a{
        height: 10px;
        width: 10px;
        background: #ffffff;
        .box-shadow(none);
        .transition(all 0.3s ease-in-out 0s);
        &.flex-active{
          background: @color-theme;
        }
        &:hover{
          background: @color-theme;
        }
      }
    }
  }
