/**
 * 7.0 Single Blog
 * -----------------------------------------------------------------------------
 */
.tzBlogSingle{
  padding:90px 0px 120px;
  background: #fafafa;
  .tzBlogSingleContainer{
    background: #FFFFFF;
    margin-bottom: 30px;
    .tzBlogSingleImage{
      position: relative;
      img{
        max-width: 100%;
        height: auto;
        margin:0 auto;
        display: block;
      }
      .tzBlogDate{
        position: absolute;
        top:0px;
        left: 0px;
        z-index: 999;
        .tzDateText{
          width: 60px;
          height: 55px;
          background: @color-theme;
          padding:8px 5px;
          .tzTextDay{
            display: block;
            font-family: 'Montserrat',sans-serif;
            font-weight: 400;
            font-size: 20px;
            line-height: 20px;
            color: #ffffff;
            text-align: center;
            margin-bottom: 4px;
          }
          .tzTextMonth{
            display: block;
            font-family: 'Montserrat',sans-serif;
            font-weight: 700;
            font-size: 11px;
            line-height: 11px;
            color: #ffffff;
            text-align: center;
            text-transform: uppercase;
          }
        }
        .tzBlogIcon{
          width: 60px;
          height: 55px;
          background: #444444;
          text-align: center;
          i{
            font-size: 22px;
            color: #fff;
            padding: 18px;
          }
        }
      }
    }
    .tzBlogSingleAudio{
      position: relative;
      padding:0px 0px 0px 90px;
      iframe{
        width: 100%;
        border:none;
      }
      .tzBlogDate{
