    .tzBlogSingleSlider{
      position: relative;
      ol.flex-control-nav{
        bottom:8px;
        li{
          margin: 0 5px;
          a{
            height: 10px;
            width: 10px;
            background: #ffffff;
            .box-shadow(none);
            .transition(all 0.3s ease-in-out 0s);
            &.flex-active{
              background: @color-theme;
            }
            &:hover{
              background: @color-theme;
            }
          }
        }
      }
      .tzBlogDate{
        position: absolute;
        top:0px;
        left: 0px;
        z-index: 999;
        .tzDateText{
          width: 60px;
