            padding: 18px;
          }
        }
      }
    }
    .tzBlogSingle_videoHtml5{
      width: 100%;
      overflow: hidden;
      position: relative;
      .tzblog_autoplay{
        background: @color-theme;
        border: 0 none;
        .border-radius(50%);
        height:50px;
        left: 50%;
        margin-left: -25px;
        margin-top: -25px;
        opacity: 1;
        position: absolute;
        top: 50%;
        width: 50px;
        z-index: 99;
        text-align: center;
        i{
          display: inline-block;
          font-size: 16px;
          color: #FFFFFF;
          padding: 17px 0px;
        }
        &:hover{
          cursor: pointer;
        }
      }
      .tzblog_pauses{
        background: @color-theme;
        border: 0 none;
        .border-radius(50%);
        height:50px;
        left: 50%;
        margin-left: -25px;
        margin-top: -25px;
        opacity: 1;
        position: absolute;
        top: 50%;
        width: 50px;
        z-index: 99;
        text-align: center;
        display: none;
        opacity: 0;
        i{
          display: inline-block;
          font-size: 16px;
          color: #FFFFFF;
          padding: 17px 0px;
        }
        &:hover{
          cursor: pointer;
        }
      }
      &:hover .tzblog_pauses{
        opacity: 1;
      }
      .bg-video{
        height: 100%;
        left: 0;
        position: absolute;
        top: 0;
        width: 100%;
        z-index: 1;
      }
      .videoID{
        width: 100%;
        height: auto;
        overflow: hidden;
        position: absolute;
        top: 0;
        left: 0;
      }

      .tzBlogDate{
        position: absolute;
        top:0px;
        left: 0px;
        z-index: 999;
        .tzDateText{
