          }
        }
      }
    }
    .tzBlogSingle_content{
      padding:35px 32px;
      h3.tzBlogSingle_title{
        margin:0px 0px 16px;
        padding:0px;
        font-family: 'Montserrat',sans-serif;
        font-size: 20px;
        font-weight: 400;
        color: #222222;
        line-height: 27px;
      }
      .tzInfomation{
        display: block;
        margin:0px 0px 27px;
        padding:0px 0px 28px;
        border-bottom: 1px solid #ededed;
        a{
          font-family: 'Ubuntu',sans-serif;
          font-size: 14px;
          font-weight: 300;
          font-style: italic;
          line-height: 22px;
          color: #b5b5b5;
          margin: 0 3px 0 0;
          text-decoration: none;
        }
        small{
          font-family: 'Ubuntu',sans-serif;
          font-size: 14px;
          font-weight: 300;
          font-style: italic;
          line-height: 22px;
          color: #b5b5b5;
          i{
            margin: 0 5px 0 0;
          }
        }
        span{
          font-family: 'Ubuntu',sans-serif;
          font-size: 14px;
          font-weight: 300;
          font-style: italic;
          line-height: 22px;
          color: #b5b5b5;
          i{
            margin: 0 5px 0 0;
          }
        }
      }
      .single-content{
        padding:0px 0px 32px;
        margin:0px 0px 30px;
        border-bottom: 1px solid #ededed;
        p{
          font-family: 'Ubuntu',sans-serif;
          font-weight: 300;
          font-size: 14px;
          line-height: 25px;
          color: #666666;
        }
      }
      .tzBlogSingle_bottom{
        .tztag{
          span{
            font-family: 'Montserrat',sans-serif;
            font-weight: 700;
            font-size: 14px;
            line-height: 25px;
            color: #222222;
            margin:0px 8px 0px 0px;
          }
          a{
            font-family: 'Ubuntu',sans-serif;
            font-weight: 300;
            font-size: 14px;
            line-height: 25px;
            color: #666666;
            text-decoration: none;
          }
        }
        .tzshare{
          text-align: right;
            .TzLikeButtonInner{
              display: inline-block;
              span{
                font-family: 'Montserrat',sans-serif;
                font-weight: 700;
                font-size: 14px;
                line-height: 25px;
                color: #222222;
                margin:0px 15px 0px 0px;
                display: block;
                float: left;
              }
              .FacebookButton{
                float: left;
              }
              .TwitterButton{
                float: left;
              }
              .GooglePlusOneButton{
                float: left;
              }
              &:after{
                content: '';
                display: block;
                clear: both;
              }
            }
        }
      }
    }
  }
  .author{
    background: #FFFFFF;
    padding:30px;
    margin:0px 0px 30px;
    .author-avata{
      float: left;
      position: relative;
      .author-img{
        width: 140px;
        height: 140px;
        border-radius: 50%;
        overflow: hidden;
      }
      .author-social{
        position: absolute;
        top:50%;
        right: -33px;
        .TzSocialLink{
          display: block;
          i{
            font-size: 15px;
            margin-bottom: 16px;
          }
          &:last-child i{
            margin:0px;
          }
          &.TzSocialFacebook{
            i{
              color:#4361a7;
            }
          }
          &.TzSocialTwitter{
            i{
              color:#60b5f0;
            }
          }
          &.TzSocialLinkedin{
            i{
              color:#0274b3;
            }
          }
          &.TzSocialDribbble{
            i{
              color:#f26798;
            }
          }
          &.TzSocialGPlus{
            i{
              color: #e35640;
            }
          }
        }
      }
    }
    .author-info{
      margin-left: 210px;
      h3{
        margin:0px;
        padding:0px;
        a{
          font-family: 'Montserrat',sans-serif;
          font-weight: 700;
          font-size: 14px;
          line-height: 22px;
          color: #222222;
          text-decoration: none;
          text-transform: uppercase;
        }
      }
      span{
        font-family: 'Ubuntu',sans-serif;
        font-weight: 300;
        font-size: 14px;
        font-style: italic;
        line-height: 22px;
        color: #b5b5b5;
        display: block;
        margin-bottom: 12px;
      }
      p{
        font-family: 'Ubuntu',sans-serif;
        font-weight: 300;
        font-size: 14px;
        line-height: 22px;
        color: #666666;
      }
    }
    &:after{
      content: '';
      display: block;
      clear: both;
    }
  }
  .tzComments{
    .comments-area{
      .tzCommentContent{
        background: #FFFFFF;
        padding:47px 30px 1px;
        margin-bottom: 30px;
        .comments-title{
          margin:0px 0px 42px;
          padding:0px;
          font-family: 'Montserrat',sans-serif;
          font-weight: 400;
          font-size: 20px;
          color:#222222;
          line-height: 22px;
          text-transform: uppercase;
        }
        ol.comment-list{
          margin:0px;
          padding:0px;
          position: relative;
          &:after{
            content: '';
            display: block;
            width:100%;
            height: 1px;
            background: #FFFFFF;
            position: absolute;
            bottom: 0;
            left: 0;
          }
          li{
            list-style: none;
            article{
              padding:0px 0px 30px;
              border-bottom:1px solid #ededed;
              margin:0px 0px 30px;
              .comment-author{
                display: block;
                float: left;
                .border-radius(50%);
                overflow: hidden;
              }
              .comment-content{
                margin-left: 120px;
                h5{
                  float: left;
                  font-family: 'Montserrat',sans-serif;
                  font-size: 14px;
                  font-weight: 400;
                  line-height: 22px;
                  color: #222222;
                  text-decoration: none;
                  text-transform: uppercase;
                  margin-bottom: 12px;
                  a{
                    color: #222;
                    text-decoration: none;
                    &:hover{
                      color: @color-theme;
                    }
                  }
                }
                .tz-commentInfo{
                  margin-bottom: 13px;
                  .comments-datetime{
                    text-decoration: none;
                    time{
                      font-family: 'Ubuntu',sans-serif;
                      font-weight: 300;
                      font-size: 14px;
                      font-style: italic;
                      line-height: 22px;
                      color: #b5b5b5;
                    }
                  }
                  .comment-edit-link{
                    font-family: 'Ubuntu',sans-serif;
                    font-weight: 300;
                    font-size: 14px;
                    line-height: 22px;
                    color: @color-theme;
                    text-decoration: none;
                    margin-left: 20px;
                    i{
                      font-size: 13px;
                      margin-right: 5px;
                    }
                  }
                  .comment-reply-link{
                    font-family: 'Ubuntu',sans-serif;
                    font-weight: 300;
                    font-size: 14px;
                    line-height: 22px;
                    color: @color-theme;
                    text-decoration: none;
                    margin-left: 20px;
                    i{
                      font-size: 13px;
                      margin-right: 5px;
                    }
                  }
                }
              }
              p{
                font-family: 'Ubuntu',sans-serif;
                font-weight: 300;
                font-size: 14px;
                line-height: 24px;
                color: #666666;
              }
            }
            ol.children{
              padding-left: 120px;
            }
          }
        }
      }
      .tzCommentForm{
        .comment-respond{
          background: #FFFFFF;
          padding:30px;
          h3.comment-reply-title{
            margin:0px;
            padding:0px;
            font-family: 'Montserrat',sans-serif;
            font-weight: 400;
            font-size: 20px;
            color:#222222;
            line-height: 22px;
            text-transform: uppercase;
          }
          form.comment-form{
            p {
              font-family: 'Ubuntu',sans-serif;
              font-weight: 300;
              font-size: 14px;
              line-height: 24px;
              color: #666666;
              margin: 0 0 50px;
              &.comment-form-comment{
                margin: 0 0 43px;
              }
              label {
                display: none;
              }
              a{
                color: @color-theme;
                text-decoration: none;
              }
              textarea{
                width: 100%;
                height: 95px;
                border-width: 0 0 1px;
                border-color: #ededed;
                border-style: solid;
                font-family: 'Ubuntu',sans-serif;
                font-weight: 300;
                line-height: 24px;
                font-size: 14px;
                color:#b5b5b5;
                padding:5px 0px;
              }
              &.form-submit{
                text-align: center;
                input.submit{
                  background: none;
                  .border-radius(0);
                  border-width: 0 0 1px;
                  border-style: solid;
                  border-color: @color-theme;
                  font-family: 'Ubuntu',sans-serif;
                  font-size: 12px;
                  font-weight: 300;
                  color: @color-theme;
                  line-height: 22px;
                  text-transform: uppercase;
                  padding: 5px 15px;

                }
              }
            }
            .tzCommentForm_Top{
              margin-bottom: 37px;
              p{
                margin:0px;
                input{
                  width: 100%;
                  border-width: 0 0 1px;
                  border-color: #ededed;
                  border-style: solid;
                  font-family: 'Ubuntu',sans-serif;
                  font-weight: 300;
                  line-height: 24px;
                  font-size: 14px;
                  color:#b5b5b5;
                  padding:5px 0px;
                }
              }
            }
          }
        }
      }
    }
  }
}