/* 6.1 Blog Column */
.tzBlogColumn{
  padding:90px 0px 100px;
  background: #fafafa;
  .tzBlogContainer{
    margin-right: -30px;
    .blogColumn-item{
      .tzBlogInner{
        margin: 0 30px 30px 0;
        padding:0;
        background: #ffffff;
        .tzBlogQuote{
          position: relative;
          z-index: 1;
          padding:47px 0px 48px;
          .tzBlogQuote_bg{
            position: absolute;
            top:0px;
            left: 0px;
            width: 100%;
            height: 100%;
            overflow: hidden;
            z-index: -1;
            img{
              max-width: none;
              position: relative;
            }
          }
          .tzBlogQuote_overlay{
            position: absolute;
            top:0px;
            left: 0px;
            width: 100%;
            height: 100%;
            background: rgba(1, 7, 20, 0.75);
            z-index: -1;
          }
          .tzBlogQuote_char{
            display: block;
            text-align: center;
            font-size: 90px;
            color: @color-theme;
            line-height: 90px;
            margin-bottom: 13px;
            &:before{
              font-family: "ElegantIcons";
            }

          }
          p{
            font-family: 'Ubuntu',sans-serif;
            font-size: 20px;
            font-weight: 400;
            font-style: italic;
            color: #ffffff;
            line-height: 35px;
            text-align: center;
            padding:0px 52px;
            margin:0px 0px 72px;
          }
          .tzBlogQuote_info{
            display: block;
            margin:0px;
            text-align: center;
            a{
              font-family: 'Ubuntu',sans-serif;
              font-size: 14px;
              font-weight: 300;
              font-style: italic;
              line-height: 22px;
              color: #FFFFFF;
              margin: 0 3px 0 0;
              text-decoration: none;
            }
            small{
              font-family: 'Ubuntu',sans-serif;
              font-size: 14px;
              font-weight: 300;
              font-style: italic;
              line-height: 22px;
              color: #FFFFFF;
              i{
                margin: 0 5px 0 0;
              }
            }
            span{
              font-family: 'Ubuntu',sans-serif;
              font-size: 14px;
              font-weight: 300;
              font-style: italic;
              line-height: 22px;
              color: #b5b5b5;
              i{
                margin: 0 5px 0 0;
              }
            }
          }
        }
        .tzBlog_videoHtml5{
          width: 100%;
          overflow: hidden;
          position: relative;
          .tzblog_autoplay{
            background: @color-theme;
            border: 0 none;
            .border-radius(50%);
            height:50px;
            left: 50%;
            margin-left: -25px;
            margin-top: -25px;
            opacity: 1;
            position: absolute;
            top: 50%;
            width: 50px;
            z-index: 99;
            text-align: center;
            i{
              display: inline-block;
              font-size: 16px;
              color: #FFFFFF;
              padding: 17px 0px;
            }
            &:hover{
              cursor: pointer;
            }
          }
          .tzblog_pauses{
            background: @color-theme;
            border: 0 none;
            .border-radius(50%);
            height:50px;
            left: 50%;
            margin-left: -25px;
            margin-top: -25px;
            opacity: 1;
            position: absolute;
            top: 50%;
            width: 50px;
            z-index: 99;
            text-align: center;
            display: none;
            opacity: 0;
            i{
              display: inline-block;
              font-size: 16px;
              color: #FFFFFF;
              padding: 17px 0px;
            }
            &:hover{
              cursor: pointer;
            }
          }
          &:hover .tzblog_pauses{
            opacity: 1;
          }
          .bg-video{
            height: 100%;
            left: 0;
            position: absolute;
            top: 0;
            width: 100%;
            z-index: 1;
          }
          .videoID{
            width: 100%;
            height: auto;
            overflow: hidden;
            position: absolute;
            top: 0;
            left: 0;
          }

          .tzBlogDate{
            position: absolute;
            top:0px;
            left: 0px;
            z-index: 999;
            .tzDateText{
              width: 60px;
              height: 55px;
              background: @color-theme;
              padding:8px 5px;
              .tzTextDay{
                display: block;
                font-family: 'Montserrat',sans-serif;
                font-weight: 400;
                font-size: 20px;
                line-height: 20px;
                color: #ffffff;
                text-align: center;
                margin-bottom: 4px;
              }
              .tzTextMonth{
                display: block;
                font-family: 'Montserrat',sans-serif;
                font-weight: 700;
                font-size: 11px;
                line-height: 11px;
                color: #ffffff;
                text-align: center;
                text-transform: uppercase;
              }
            }
            .tzBlogIcon{
              width: 60px;
              height: 55px;
              background: #444444;
              text-align: center;
              i{
                font-size: 22px;
                color: #fff;
                padding: 18px;
              }
            }
          }
        }
        .tzBlogAudio{
          position: relative;
          padding:0px 0px 0px 90px;
          iframe{
            width: 100%;
            border:none;
          }
          .tzBlogDate{
            position: absolute;
            top:0px;
            left: 0px;
            z-index: 999;
            .tzDateText{
              width: 60px;
              height: 55px;
              background: @color-theme;
              padding:8px 5px;
              .tzTextDay{
                display: block;
                font-family: 'Montserrat',sans-serif;
                font-weight: 400;
                font-size: 20px;
                line-height: 20px;
                color: #ffffff;
                text-align: center;
                margin-bottom: 4px;
              }
              .tzTextMonth{
                display: block;
                font-family: 'Montserrat',sans-serif;
                font-weight: 700;
                font-size: 11px;
                line-height: 11px;
                color: #ffffff;
                text-align: center;
                text-transform: uppercase;
              }
            }
            .tzBlogIcon{
              width: 60px;
              height: 55px;
              background: #444444;
              text-align: center;
              i{
                font-size: 22px;
                color: #fff;
                padding: 18px;
              }
            }
          }
        }
        .tzBlogVideo{
          position: relative;
          iframe{
            width: 100%;
            height: 100%;
            border:none;
          }
        }
        .tzBlogSlider{
          position: relative;
          .flex-viewport{
            ul.slides{
              li{
                img{
                  width: 100%;
                  height: auto;
                }
              }
            }
          }
          ul.flex-direction-nav{
            z-index: 9999;
            li{
              a{
                top:50%;
                opacity: 1;
                height: 30px;
                width: 30px;
                margin:-15px 0 0;
                .border-radius(50%);
                border:1px solid #FFFFFF;
                text-align: center;
                background: none;
                &:before{
                  font-size: 10px;
                  font-weight: lighter;
                  color: #ffffff;
                  text-shadow: none;
                }
                &.flex-prev{
                  left: 30px;
                }
                &.flex-next{
                  right: 30px;
                }
                &:hover{
                  border:1px solid @color-theme;
                  background: @color-theme;
                }
              }
            }
          }
          ol.flex-control-nav{
            bottom:8px;
            li{
              margin: 0 5px;
              a{
                height: 10px;
                width: 10px;
                background: #ffffff;
                .box-shadow(none);
                .transition(all 0.3s ease-in-out 0s);
                &.flex-active{
                  background: @color-theme;
                }
                &:hover{
                  background: @color-theme;
                }
              }
            }
          }
          .tzBlogDate{
            position: absolute;
            top:0px;
            left: 0px;
            z-index: 999;
            .tzDateText{
              width: 60px;
              height: 55px;
              background: @color-theme;
              padding:8px 5px;
              .tzTextDay{
                display: block;
                font-family: 'Montserrat',sans-serif;
                font-weight: 400;
                font-size: 20px;
                line-height: 20px;
                color: #ffffff;
                text-align: center;
                margin-bottom: 4px;
              }
              .tzTextMonth{
                display: block;
                font-family: 'Montserrat',sans-serif;
                font-weight: 700;
                font-size: 11px;
                line-height: 11px;
                color: #ffffff;
                text-align: center;
                text-transform: uppercase;
              }
            }
            .tzBlogIcon{
              width: 60px;
              height: 55px;
              background: #444444;
              text-align: center;
              i{
                font-size: 22px;
                color: #fff;
                padding: 18px;
              }
            }
          }
        }
        .tzBlogImage{
          position: relative;
          img{
            width: 100%;
            height: auto;
          }
          .tzBlogDate{
            position: absolute;
            top:0px;
            left: 0px;
            z-index: 999;
            .tzDateText{
              width: 60px;
              height: 55px;
              background: @color-theme;
              padding:8px 5px;
              .tzTextDay{
                display: block;
                font-family: 'Montserrat',sans-serif;
                font-weight: 400;
                font-size: 20px;
                line-height: 20px;
                color: #ffffff;
                text-align: center;
                margin-bottom: 4px;
              }
              .tzTextMonth{
                display: block;
                font-family: 'Montserrat',sans-serif;
                font-weight: 700;
                font-size: 11px;
                line-height: 11px;
                color: #ffffff;
                text-align: center;
                text-transform: uppercase;
              }
            }
            .tzBlogIcon{
              width: 60px;
              height: 55px;
              background: #444444;
              text-align: center;
              i{
                font-size: 22px;
                color: #fff;
                padding: 18px;
              }
            }
          }
        }
        .tzBlogContent{
          margin:0px;
          padding:48px 30px 38px;
          h3.title{
            margin:0px 0px 17px;
            padding:0px;
            a{
              font-family: 'Montserrat',sans-serif;
              font-size: 20px;
              font-weight: 400;
              line-height: 27px;
              color: #222222;
              text-decoration: none;
            }
          }
          .tzInfomation{
            display: block;
            margin:0px 0px 22px;
            a{
              font-family: 'Ubuntu',sans-serif;
              font-size: 14px;
              font-weight: 300;
              font-style: italic;
              line-height: 22px;
              color: #b5b5b5;
              margin: 0 3px 0 0;
              text-decoration: none;
            }
            small{
              font-family: 'Ubuntu',sans-serif;
              font-size: 14px;
              font-weight: 300;
              font-style: italic;
              line-height: 22px;
              color: #b5b5b5;
              i{
                margin: 0 5px 0 0;
              }
            }
            span{
              font-family: 'Ubuntu',sans-serif;
              font-size: 14px;
              font-weight: 300;
              font-style: italic;
              line-height: 22px;
              color: #b5b5b5;
              i{
                margin: 0 5px 0 0;
              }
            }
          }
          p{
            font-family: 'Ubuntu',sans-serif;
            font-size: 14px;
            font-weight: 300;
            line-height: 25px;
            color: #666666;
            margin:0px;
            padding:0px;
          }
          a.tzreadmore{
            display: inline-block;
            font-family: 'Ubuntu',sans-serif;
            font-size: 14px;
            color: #FFFFFF;
            font-weight: 300;
            line-height: 25px;
            margin: 31px 0 6px;
            padding:8px 27px;
            background: @color-theme;
            .border-radius(3px);
            text-decoration: none;
          }
        }
      }
    }

    &.no_sidebar{
      &.tzBlogDesktop-4-Column{
        .blogColumn-item{
          .tzBlogInner{
            .tzBlogQuote{
              p{
                padding: 0 30px;
              }
            }
            .tzBlog_videoHtml5{
              height: 147px;
            }
            .tzBlogVideo{
              iframe{
                height: 147px;
              }
            }
          }
        }
      }
      &.tzBlogDesktop-3-Column{
        .blogColumn-item{
          .tzBlogInner{
            .tzBlogQuote{
              p{
                padding: 0 52px;
              }
            }
            .tzBlog_videoHtml5{
              height: 203px;
            }
            .tzBlogVideo{
              iframe{
                height: 203px;
              }
            }
          }
        }
      }
      &.tzBlogDesktop-2-Column{
        .blogColumn-item{
          .tzBlogInner{
            .tzBlogQuote{
              p{
                padding: 0 52px;
              }
            }
            .tzBlog_videoHtml5{
              height: 304px;
            }
            .tzBlogVideo{
              iframe{
                height: 304px;
              }
            }
          }
        }
      }
    }

    &.yes_sidebar{
      &.tzBlogDesktop-4-Column{
        .blogColumn-item{
          .tzBlogInner{
            .tzBlogQuote{
              p{
                padding: 0 10px;
              }
            }
            .tzBlog_videoHtml5{
              height: 106px;
            }
            .tzBlogVideo{
              iframe{
                height: 106px;
              }
            }
          }
        }
      }
      &.tzBlogDesktop-3-Column{
        .blogColumn-item{
          .tzBlogInner{
            .tzBlogQuote{
              p{
                padding: 0 25px;
              }
            }
            .tzBlog_videoHtml5{
              height: 147px;
            }
            .tzBlogVideo{
              iframe{
                height: 147px;
              }
            }
          }
        }
      }
      &.tzBlogDesktop-2-Column{
        .blogColumn-item{
          .tzBlogInner{
            .tzBlogQuote{
              p{
                padding: 0 35px;
              }
            }
            .tzBlog_videoHtml5{
              height: 230px;
            }
            .tzBlogVideo{
              iframe{
                height: 230px;
              }
            }
          }
          .tzBlogContent{
            padding: 22px 30px 30px;
            h3.title{
              margin: 0 0 10px;
            }
            .tzInfomation{
              margin: 0 0 13px;
            }
          }
        }
      }
    }
  }

  .wp-pagenavi{
    span{
      font-family: 'Ubuntu',sans-serif;
      font-size: 14px;
      font-weight: 500;
      line-height: 24px;
      color: #666666;
      padding: 10px 15px;
      background: #dfdfdf;
      .border-radius(2px);
      border:none;
      &.current{
        color: #FFFFFF;
        background: @color-theme;
      }
    }
    a{
      font-family: 'Ubuntu',sans-serif;
      font-size: 14px;
      font-weight: 500;
      line-height: 24px;
      color: #666666;
      padding: 10px 15px;
      background: #dfdfdf;
      .border-radius(2px);
      text-decoration: none;
      border: none;
      .transition(all 0.3s ease-in-out 0s);
      &:hover{
        color: #FFFFFF;
        background: @color-theme;
      }
    }
  }
}