/* 13.9 Checkout */
.woocommerce-checkout{
  .woocommerce{
    .woocommerce-info{
      font-family: 'Ubuntu',sans-serif;
      font-size: 14px;
      font-weight: 300;
      line-height: 24px;
      color: #666666;
      border-color: @color-theme;
      &:before{
        color: @color-theme;
      }
      a{
        color:@color-theme;
      }
    }
    ul.woocommerce-error{
      li{
        font-family: 'Ubuntu',sans-serif;
        font-size: 14px;
        line-height: 24px;
        color: #666666;
        font-weight: 300;
      }
    }
    form.checkout_coupon{
      border: 1px solid #dadada;
      padding: 59px 49px;
      .border-radius(0);
      p{
        input{
          &.input-text{
            border: 1px solid #dadada;
            border-radius: 2px;
            color: #666666;
            font-family: "Ubuntu",sans-serif;
            font-size: 14px;
            font-weight: 300;
            line-height: 24px;
            padding: 10px 15px;
          }
          &.button{
            background: #222222 none repeat scroll 0 0;
            border: medium none;
            border-radius: 25px;
            color: #ffffff;
            font-family: "Ubuntu",sans-serif;
            font-size: 14px;
            font-weight: 300;
            line-height: 24px;
            padding: 10px 30px;
            transition: all 0.3s ease-in-out 0s;
            &:hover{
              background: @color-theme;
            }
          }
        }
      }
    }
    form.checkout{
      margin-top: 75px;
      .col2-set{
        margin-bottom: 45px;
        .col-1{
          .woocommerce-billing-fields{
            h3{
              font-weight: 700;
              text-transform: uppercase;
            }
            p{
              label{
                color: #222222;
                font-family: "Montserrat",sans-serif;
                font-size: 15px;
                font-weight: 400;
                line-height: 24px;
                padding: 13px 0;
                .required{
                  font-weight: 400;
                  color: #222222;
                }
              }

              input{
                border: 1px solid #dadada;
                .border-radius(2px);
                color: #666666;
                font-family: "Ubuntu",sans-serif;
                font-size: 14px;
                font-weight: 300;
                line-height: 24px !important;
                margin-bottom: 20px;
                padding: 10px 15px;
                &.button{
                  background: #222222;
                  color: #FFFFFF;
                  .border-radius(25px);
                  border:none;
                  padding:10px 30px;
                  margin-bottom: 0px;
                  margin-top: 11px;
                  .transition(all 0.3s ease-in-out 0s);
                  &:hover{
                    background: @color-theme;
                  }
                }
              }

              .country_select{
                a{
                  border: 1px solid #dadada;
                  .border-radius(2px);
                  color: #666666;
                  font-family: "Ubuntu",sans-serif;
                  font-size: 14px;
                  font-weight: 300;
                  line-height: 24px !important;
                  margin-bottom: 20px;
                  padding: 10px 15px;
                }
              }

              &#billing_address_1_field{
                margin-bottom: 15px;
              }
            }
          }
        }

        .col-2{
          .woocommerce-shipping-fields{
            h3{
              label{
                font-weight: 700;
                text-transform: uppercase;
                padding:0px 25px 0px 0px;
                margin:0;
                float: left;
              }
            }
            .shipping_address{
              margin-bottom: 10px;
              p{
                label{
                  color: #222222;
                  font-family: "Montserrat",sans-serif;
                  font-size: 15px;
                  font-weight: 400;
                  line-height: 24px;
                  padding: 13px 0;
                  .required{
                    font-weight: 400;
                    color: #222222;
                  }
                }

                input{
                  border: 1px solid #dadada;
                  .border-radius(2px);
                  color: #666666;
                  font-family: "Ubuntu",sans-serif;
                  font-size: 14px;
                  font-weight: 300;
                  line-height: 24px !important;
                  margin-bottom: 20px;
                  padding: 10px 15px;
                  &.button{
                    background: #222222;
                    color: #FFFFFF;
                    .border-radius(25px);
                    border:none;
                    padding:10px 30px;
                    margin-bottom: 0px;
                    margin-top: 11px;
                    .transition(all 0.3s ease-in-out 0s);
                    &:hover{
                      background: @color-theme;
                    }
                  }
                }

                .country_select{
                  a{
                    border: 1px solid #dadada;
                    .border-radius(2px);
                    color: #666666;
                    font-family: "Ubuntu",sans-serif;
                    font-size: 14px;
                    font-weight: 300;
                    line-height: 24px !important;
                    margin-bottom: 20px;
                    padding: 10px 15px;
                  }
                }

                &#billing_address_1_field{
                  margin-bottom: 15px;
                }
              }
            }

            p{
              label{
                color: #222222;
                font-family: "Montserrat",sans-serif;
                font-size: 15px;
                font-weight: 400;
                line-height: 24px;
                padding: 13px 0;
                .required{
                  font-weight: 400;
                  color: #222222;
                }
              }

              textarea{
                border: 1px solid #dadada;
                .border-radius(2px);
                color: #666666;
                font-family: "Ubuntu",sans-serif;
                font-size: 14px;
                font-weight: 300;
                line-height: 24px !important;
                margin-bottom: 20px;
                padding: 10px 15px;
              }
            }
          }
        }
      }

      h3#order_review_heading{
          font-weight: 700;
          text-transform: uppercase;
      }

      #order_review{
        table.woocommerce-checkout-review-order-table{
          margin-bottom: 53px;
          thead{
            tr{
              th{
                padding:14px 0px;
                text-align: left;
              }
            }
          }

          tbody{
            tr{
              td{
                color: #222222;
                font-family: "Montserrat",sans-serif;
                font-size: 15px;
                font-weight: 400;
                line-height: 24px;
                padding:20px 0px;
                text-align: left;
              }
            }
          }

          tfoot{
            tr{
              th{
                color: #222222;
                font-family: "Montserrat",sans-serif;
                font-size: 15px;
                font-weight: 400;
                line-height: 24px;
                padding:20px 0px;
                text-align: left;
                text-transform: uppercase;
              }

              td{
                padding:0px;
                span.amount{
                  color: #222222;
                  font-family: "Montserrat",sans-serif;
                  font-size: 15px;
                  font-weight: 400;
                  line-height: 24px;
                  padding:20px 0px;
                  text-align: left;
                }
              }
            }
          }
        }

        .woocommerce-checkout-payment{
          background: #ededed none repeat scroll 0 0;
          .border-radius(0);
          ul.payment_methods{
            padding:45px 70px 0;
            li{
              label{
                color: #222222;
                font-family: "Montserrat",sans-serif;
                font-size: 15px;
                font-weight: 400;
                line-height: 24px;
                padding: 13px 0;
                a{
                  color: #222;
                  text-decoration: none;
                  margin-left: 10px;
                  &:hover{
                    color: @color-theme;
                  }
                }
              }
              .payment_box{
                margin:0px;
                padding:0px 30px;
                background: none;
                &:after{
                  content: none;
                }

                &.payment_method_paypal{
                  margin-bottom: 24px;
                }
              }
            }
          }

          .place-order{
            padding: 30px 70px 65px;
            input#place_order{
              float: left;
              background: #222222 none repeat scroll 0 0;
              border: medium none;
              .border-radius(25px);
              font-family: 'Ubuntu',sans-serif;
              font-weight: 300;
              font-size: 14;
              line-height: 24px;
              color: #ffffff;
              margin-bottom: 0;
              margin-top: 11px;
              padding: 10px 30px;
              .transition(all 0.3s ease-in-out 0s);
              &:hover{
                background: @color-theme;
              }
            }
          }
        }
      }
    }
  }
}