/* 11.9 Accordion */
.vc_tta-accordion{
  .vc_tta-panels-container{
    .vc_tta-panels{
      .vc_tta-panel{
        .vc_tta-panel-heading{
          .vc_tta-panel-title{
            a{
              span.vc_tta-title-text{
                font-family: 'Montserrat',sans-serif;
                font-size: 15px;
                font-weight: 700;
              }
              .vc_tta-controls-icon{
                height: 8px;
                width: 8px;
              }
            }
          }
        }
      }
    }
  }
}

.vc_tta-style-classic{
  .vc_tta-panels{
    .vc_tta-panel{
      margin-bottom: 10px;
    }
  }
  &.vc_tta-color-white{
    .vc_tta-panel{
      .vc_tta-panel-heading{
        background-color: rgba(0,0,0,0.02) !important;
        border-color: #e1e1e1 !important;
        .vc_tta-panel-title{
          a{
            span.vc_tta-title-text{
              color:#222222;
            }
            .vc_tta-controls-icon:after{
              border-color: #222222;
            }
            .vc_tta-controls-icon:before{
              border-color: #222222;
            }
            .vc_tta-icon{
              color: #222222;
            }
          }
        }
      }
      &.vc_active{
        .vc_tta-panel-heading{
          background-color: #ffffff !important;
        }
      }

      .vc_tta-panel-body{
        border-color: #e1e1e1 !important;
        padding:19px;
      }
    }
  }

  &.vc_tta-shape-rounded{
    .vc_tta-panels{
      .vc_tta-panel{
        .vc_tta-panel-heading{
          border-radius:2px !important;
          -webkit-border-radius:2px !important;
          -ms-border-radius:2px !important;
          -moz-border-radius:2px !important;
          -o-border-radius:2px !important;
        }
        .vc_tta-panel-body{
          border-radius:2px !important;
          -webkit-border-radius:2px !important;
          -ms-border-radius:2px !important;
          -moz-border-radius:2px !important;
          -o-border-radius:2px !important;
        }

        &.vc_active{
          .vc_tta-panel-heading{
            border-bottom-left-radius: 0 !important;
            border-bottom-right-radius: 0 !important;
          }
          .vc_tta-panel-body{
            border-top-left-radius: 0 !important;
            border-top-right-radius: 0 !important;
          }
        }
      }
    }
  }
}

.vc_tta-style-modern{
  .vc_tta-panels-container{
    .vc_tta-panels{
      .vc_tta-panel{
        margin-bottom: 15px;
        .vc_tta-panel-heading{
          border:1px solid #f0f0f0 !important;
          margin-bottom: 0px !important;
          h4.vc_tta-panel-title{
            a{
              padding:12px 68px;
              span.vc_tta-title-text{
                font-weight: 400;
                text-transform: uppercase;
              }
              .vc_tta-controls-icon{
                width: 43px;
                height: 31px;
                left: 6px;
                background: #dfdfdf;
                &:after{
                  width: 8px;
                  height: 8px;
                  border-color: #FFFFFF;
                  left: 24px;
                  top: 11px;
                }
                &:before{
                  width: 8px;
                  height: 8px;
                  border-color: #FFFFFF;
                  left: 17px;
                  top: 18px;
                }
              }
            }
          }
        }
        .vc_tta-panel-body{
          border:none;
          padding:30px 70px 20px;
        }
        &.vc_active{
          .vc_tta-panel-heading{
            h4.vc_tta-panel-title{
              a{
                .vc_tta-controls-icon{
                  background: @color-theme;
                }
              }
            }
          }
        }
      }
    }
  }

  &.vc_tta-color-white{
    .vc_tta-panels-container{
      .vc_tta-panels{
        .vc_tta-panel{
          .vc_tta-panel-heading{
            h4.vc_tta-panel-title{
              a{
                span.vc_tta-title-text{
                  color: #222222 !important;
                }
                .vc_tta-icon{
                  color:#222222;
                }
              }
            }
          }
        }
      }
    }
  }

  &.vc_tta-shape-rounded{
    .vc_tta-panels-container{
      .vc_tta-panels{
        .vc_tta-panel{
          .vc_tta-panel-heading{
            border-radius: 21px !important;
          }
          .vc_tta-controls-icon{
            width: 43px;
            height: 31px;
            left: 6px;
            background: @color-theme;
            border-radius: 21px;
          }
        }
      }
    }
  }
}


