@media (max-width: 1281px) {
  .tz-header{
    .tz-headerBottom{
      nav{
        ul.tz-nav{
          li{
            a{
              padding: 44px 15px 40px;
            }
            ul.non_mega_menu{
              width: 230px;
              li{
                ul.sub-menu{
                  width: 230px;
                }
              }
            }
          }
        }

        #mega-menu-wrap-primary{
          #mega-menu-primary{
            > li.mega-menu-item{
              &.mega-menu-flyout{
                > ul.mega-sub-menu{
                  width: 230px;
                }
              }
              > ul.mega-sub-menu{
                > li.mega-menu-item{
                  .product_list_widget{
                    li{
                      a{
                        img{
                          margin-right: 0px;
                          margin-bottom: 10px;
                        }
                        span.product-title{
                          margin-bottom: 5px;
                        }
                      }
                      .star-rating{
                        margin-bottom: 5px;
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
