@media (max-width: 480px) {
  .vc_col-sm-3{
    width: 100% !important;
  }

  .tz-header{
    .tz-headerTop{
      display: none;
    }
    .tz-headerBottom{
      .tz-header-cart{
        display: none;
      }
      .tz-header-search{
        display: none;
      }
      nav{
        ul.navbar-nav{
          margin:0px;
        }
      }
      &.tz-max-mega-menu{
        nav{
          #mega-menu-wrap-primary{
            .mega-menu-toggle{
              right: 0;
            }
          }
        }
      }
    }
  }

  .tz-Breadcrumb{
    .tzOverlayBreadcrumb{
      padding:50px 0;
      h1{
        font-size: 24px;
      }
    }
  }

  .tzElement-title{
    .tzTitle{
      font-size: 45px;
    }
    .tzSubTitle{
      font-size: 18px;
    }
    .tzDescription{
      padding:0 15px;
    }
  }
  .tzShortcode-title{
    text-align: center;
  }
  .wpb_text_column{
    .wpb_wrapper{
      p{
        text-align: center;
      }
    }
  }
  .tzPortfolio_Grid{
    .tzfilter{
      .tzFillter_box{
        padding:0 15px;
        a{
          padding:8px 15px;
        }
      }
    }

    .tzPortfolioGrid_Content{
      .tzPortfolioGrid-item{
        width: 100% !important;
      }
    }
  }

  .tzElement_Feature{
    &.tzFeature_type1{
      .tzFeature_Icon{
        text-align: center;
        .tzFeature_iconBox{
          display: inline-block;
          float: none;
        }
      }
      h5.tzFeature_title{
        text-align: center;
        margin-left: 0;
      }
      p.tzFeature_subTitle{
        margin-left: 0;
        text-align: center;
      }
      p.tzFeature_description{
        text-align: center;
      }
    }
  }

  .vc_tta-style-modern{
    .vc_tta-panels-container {
      .vc_tta-panels {
        .vc_tta-panel {
          .vc_tta-panel-body{
            padding:30px 0px 20px;
          }
        }
      }
    }
  }

  .tzElement_Video{
    .tzElement_textLeft{
      display: block;
      max-width: 100%;
      padding: 0 15px;
      right: 0;
      text-align: center;
      top: 35%;
      width: 100%;
    }
    .tzElement_textRight{
      display: block;
      max-width: 100%;
      padding: 0 15px;
      left: 0;
      text-align: center;
      top: 65%;
      width: 100%;
    }
  }

  .tzElement_Quote{
    &.tzQuote_type1{
      .tzQuote_Item{
        padding:0;
        text-align: center;
        .tzQuote_Image{
          float: none;
          display: inline-block;
        }
        .tzQuote_Content{
          margin-left:0px;
          margin-top: 23px;
        }
        .tzQuote_Info{
          margin:0 0 20px;
        }
      }
      .owl-controls{
        text-align: center;
        .owl-pagination{
          display: inline-block;
          position: relative;
          right: auto;
          top:auto;
          .owl-page{
            float: left;
          }
          &:after{
            content: '';
            display: block;
            clear: both;
          }
        }
      }
    }
    &.tzQuote_type2{
      .tzQuote_Item{
        padding:0;
      }
    }
  }

  .tzElement_Feature{
    &.tzFeature_type2{
      &.tzFeature_text-icon{
        .tzFeature_Icon{
          float: none;
          text-align: center;
          margin-bottom: 20px;
          .tzFeature_iconBox{
            display: inline-block;
          }
        }

        h5.tzFeature_title{
          margin-right: 0;
          text-align: center;
        }
        p.tzFeature_description{
          margin-right: 0;
          text-align: center;
        }
      }

      &.tzFeature_icon-text{
        .tzFeature_Icon{
          float: none;
          text-align: center;
          margin-bottom: 20px;
          .tzFeature_iconBox{
            display: inline-block;
          }
        }

        h5.tzFeature_title{
          margin-left: 0;
          text-align: center;
        }
        p.tzFeature_description{
          margin-left: 0;
          text-align: center;
        }
      }
    }
  }

  .tzElement_viewPost{
    .tzViewPost_Grid{
      .tzPost_Item{
        .tzPost_Box{
          .tzPost_Content{
            .tzPostImage{
              float: none;
              width: 100%;
            }
            .tzPost_info{
              margin-left: 0;
              .tzPost_infomation{
                text-align: center;
              }
              .tzPost_title{
                text-align: center;
                &:after{
                  left: 50%;
                  margin-left: -15px;
                }
              }
              p{
                text-align: center;
              }
            }
          }
        }
      }
    }
  }

  .tzBlogDefault{
    .tzBlogContainer{
      .tzBlogItem{
        .tzBlogQuote{
          p{
            padding:0 15px;
          }
        }
        .tzBlogContent{
          padding:15px 15px 25px;
        }
      }
    }
  }
  .tzBlogColumn{
    .tzBlogContainer{
      .blogColumn-item{
        &.tz_feature_item{
          width: 100% !important;
        }
      }
    }
  }

  .tzBlogSingle{
    .author{
      .author-avata{
        float: none;
        text-align: center;
        .author-img{
          display: inline-block;
        }
        .author-social{
          right: 10px;
        }
      }
      .author-info{
        margin-left: 0;
        h3{
          text-align: center;
        }
        span{
          text-align: center;
        }
        p{
          text-align: center;
        }
      }
    }

    .tzComments{
      .comments-area{
        .tzCommentContent{
          padding:47px 15px 1px;
          ol.comment-list{
            li{
              article{
                text-align: center;
                .comment-author{
                  float: none;
                  display: inline-block;
                }
                .comment-content{
                  text-align: left;
                  margin-left: 0;
                }
              }
              ol.children{
                padding-left: 0;
              }
            }
          }
        }
      }
    }
  }

  .tzshop-wrap {
    .col-padding{
      float: none;
      .grid_pagination_block{
        text-align: center;
        form.woocommerce-ordering{
          float: none;
          text-align: center;
          margin-bottom: 20px;
        }
        p.woocommerce-result-count{
          float: none;
          margin-left: 0;
        }
        .tzview-style{
          float: none;
          display: inline-block;
          &:after{
            content: '';
            display: block;
            clear: both;
          }
        }
      }
      .product-grid{
        ul.products{
          li.tzShop-item{
            &.tzShop-4column{
              width: 100%;
            }
            &.tzShop-3column{
              width: 100%;
            }
            &.tzShop-2column{
              width: 100%;
            }
          }
        }
      }

      .product-list{
        ul.products{
          li.tzShop-item{
            .tzShop-item_inner{
              .tzShop-item_image{
                width: 100%;
                float: none;
              }
              .tzShop-item_info{
                margin-left: 0;
                h3.tzShop-item_title{
                  margin-top: 20px;
                }
                .tzShop-item_button_list{
                  .yith-wcwl-add-to-wishlist{
                    .yith-wcwl-add-button{
                      a{
                        padding:8px 12px;
                        margin-bottom: 20px;
                      }
                    }
                    .yith-wcwl-wishlistexistsbrowse{
                      a{
                        padding:8px 12px;
                        margin-bottom: 20px;
                      }
                    }
                  }

                  .tzShop-item_button{
                    a{
                      padding:8px 12px;
                      margin-bottom: 20px;
                    }
                  }
                }
              }
            }
          }
        }
      }

      .woocommerce-pagination{
        text-align: center !important;
      }
    }
  }
  .upsells{
    ul.products{
      li.tzShop-item{
        width: 100%;
      }
    }
  }

  .related{
    ul.products{
      li.tzShop-item{
        width: 100%;
      }
    }
  }
  .woocommerce{
    .cart-collaterals{
      .cross-sells{
        ul.products{
          li.tzShop-item{
            width: 100%;
          }
        }
      }

      .tzCart_totals{
        .tzCollateralsColumn{
          form.woocommerce-shipping-calculator{
            .shipping-calculator-form{
              padding: 29px 19px;
            }
          }
          .cart_totals{
            table{
              padding: 15px 20px 0;
              tbody{
                tr{
                  th{
                    padding-right: 15px;
                  }
                }
              }
            }
            .wc-proceed-to-checkout{
              padding:20px;
            }
          }
        }
      }
    }

    form{
      table{
        &.shop_table{
          margin-right: 0;
          thead{
            tr{
              th{
                font-size: 13px;
                padding:14px 0px;
                &.product-price{
                  display: none;
                }
              }
            }
          }
          tbody{
            tr{
              &.cart_item{
                td{
                  padding:20px 0px;
                  &.product-price{
                    display: none;
                  }
                  &.product-quantity{
                    .quantity{
                      input{
                        padding:9px;
                        width: 60px;
                      }
                    }
                  }
                }
              }
              td{
                &.actions{
                  .coupon{
                    input{
                      width: 130px;
                    }
                  }
                }
                &.product-price{
                  display: none;
                }
              }
            }
          }
        }
      }
    }
  }
  .tzShopDetail-wrap{
    .tzShopContentDetail{
      .tzShopDetail_Product{
        .tzShopDetail_images{
          #tzShopDetail_slide{
            width: 100% !important;
          }
          #tzShopDetailSlide-carousel{
            display: none;
          }
        }
      }
    }
  }
  .woocommerce-checkout{
    .woocommerce{
      form.checkout{
        .col2-set{
          .col-1{
            .woocommerce-billing-fields{
              p{
                width: 100%;
              }
            }
          }

          .col-2{
            .woocommerce-shipping-fields{
              .shipping_address{
                p{
                  width: 100%;
                }
              }
            }
          }
        }

        #order_review{
          .woocommerce-checkout-payment{
            ul.payment_methods{
              padding:15px 15px 0;
              li{
                label{
                  a{
                    line-height: 24px;
                  }
                }
              }
            }
          }
        }
      }
    }
    #payment{
      ul.payment_methods{
        li{
          img{
            display: none;
          }
        }
      }
    }
  }
  .woocommerce-account{
    .woocommerce{
      .addresses{
        .address{
          padding:20px 15px 0;
          header.title{
            margin-bottom: 25px;
            h3{
              float: none;
            }
            a{
              float: none;
              display: inline-block;
            }
          }
        }
      }

      form{
        p{
          width: 100%;
        }
      }
    }
  }
  form#yith-wcwl-form table{
    &.wishlist_table{
      tbody{
        tr{
          td{
            padding:20px 0;
            &.product-stock-status{
              span{
                display: inline-block;
                font-size: 0;
                height: 20px;
                width: 20px;
                position: relative;
                &.wishlist-in-stock{
                  &:after{
                    font-family: FontAwesome;
                    content: "\f058";
                    display: inline-block;
                    font-size: 20px;
                    content: #222;
                    position: absolute;
                    top:0;
                    left: 0;
                  }
                }
                &.wishlist-out-of-stock{
                  &:after{
                    font-family: FontAwesome;
                    content: "\f057";
                    display: inline-block;
                    font-size: 20px;
                    content: #222;
                    position: absolute;
                    top:0;
                    left: 0;
                  }
                }
              }
            }

            &.product-add-to-cart{
              a{
                background: none;
                border:none;
                .border-radius(0);
                display: inline-block;
                font-size: 0;
                height: 20px;
                width: 20px;
                position: relative;
                margin: 4px 0 0;
                padding:0;
                &:after{
                  font-family: FontAwesome;
                  content: "\f217";
                  display: inline-block;
                  font-size: 20px;
                  position: absolute;
                  top:0;
                  left: 0;
                  color:#222;
                }
                &:hover{
                  background: none;
                  &:after{
                    color: @color-theme;
                  }
                }
              }
            }
          }
        }
      }
    }
  }

  .tzFooter{
    .tzFooterBottom{
      .tzCopyright{
        float: none !important;
        text-align: center;
      }
      .tzFooterSocial{
        padding:0;
        float: none !important;
        ul{
          text-align: center;
          li{
            display: inline-block;
            float: none;
            margin-bottom: 25px;
          }
        }
      }
    }
  }
}