/* 13.8 My Account */
.woocommerce-account{
  .woocommerce{
    p.myaccount_user{
      margin-bottom: 66px;
      strong{
        color: @color-theme;
      }
      a{
        color: #666666;
        &:hover{
          color: @color-theme;
          text-decoration: none;
        }
      }
    }
    h2{
      font-weight: 700;
      text-transform: uppercase;
    }
    .addresses{
      margin-top: 55px;
      .address{
        border:1px solid #dadada;
        padding:55px 49px 32px;
        header.title{
          h3{
            font-weight: 700;
            text-transform: uppercase;
          }
          a{
            .border-radius(25px);
            background: #222222;
            color: #ffffff;
            font-size: 14px;
            font-weight: 300;
            font-family: 'Ubuntu',sans-serif;
            line-height: 24px;
            border:none;
            padding: 10px 30px;
            width: auto;
            .transition(all 0.3s ease-in-out 0s);
            &:hover{
              background: @color-theme;
              text-decoration: none;
            }
          }
        }
      }
    }

    form{
      h3{
        font-weight: 700;
        text-transform: uppercase;
      }
      p{
        label{
          color: #222222;
          font-family: "Montserrat",sans-serif;
          font-size: 15px;
          font-weight: 400;
          line-height: 24px;
          padding: 13px 0;
          .required{
            font-weight: 400;
            color: #222222;
          }
        }

        input{
          border: 1px solid #dadada;
          .border-radius(2px);
          color: #666666;
          font-family: "Ubuntu",sans-serif;
          font-size: 14px;
          font-weight: 300;
          line-height: 24px !important;
          margin-bottom: 20px;
          padding: 10px 15px;
          &.button{
            background: #222222;
            color: #FFFFFF;
            .border-radius(25px);
            border:none;
            padding:10px 30px;
            margin-bottom: 0px;
            margin-top: 11px;
            .transition(all 0.3s ease-in-out 0s);
            &:hover{
              background: @color-theme;
            }
          }
        }

        .country_select{
          a{
            border: 1px solid #dadada;
            .border-radius(2px);
            color: #666666;
            font-family: "Ubuntu",sans-serif;
            font-size: 14px;
            font-weight: 300;
            line-height: 24px !important;
            margin-bottom: 20px;
            padding: 10px 15px;
          }
        }

        &#billing_address_1_field{
          margin-bottom: 15px;
        }
      }
    }
  }
}