/* 13.7 Wishlist */
form#yith-wcwl-form{
  .wishlist-title{
    h2{
      font-family: 'Montserrat',sans-serif;
      font-size: 20px;
      font-weight: 700;
      color: #222222;
      line-height: 24px;
      text-transform: uppercase;
      margin-bottom: 15px;
    }
  }
  table.wishlist_table{
    margin-bottom: 0px;
    thead{
      tr{
        th{
          border:none;
        }
      }
    }
    tbody{
      tr{
        td {
          text-align: center;
          padding: 20px;
          a {
            font-size: 15px;
            color: #222222 !important;
            font-weight: 400;
            font-family: 'Montserrat', sans-serif;
            line-height: 24px;
            text-decoration: none;
            &.remove {
              display: inline-block;
            }
            img {
              width: 80px;
              height: auto;
            }
          }
          span.amount {
            font-size: 15px;
            color: #222222 !important;
            font-weight: 400;
            font-family: 'Montserrat', sans-serif;
            line-height: 24px;
          }
          &.product-remove {
            padding: 20px 0px;
            a{
              width: 22px;
              height: 22px;
              &:hover{
                background: @color-theme;
                color: #fff !important;
              }
            }
          }
          &.product-thumbnail {
            width: 80px;
            padding: 20px 0px;
          }
          &.product-name {
            text-align: left;
          }
          &.product-price{
            ins{
              text-decoration: none;
            }
          }
          &.product-stock-status{
            span{
              font-size: 15px;
              font-weight: 400;
              font-family: 'Montserrat', sans-serif;
              line-height: 24px;
            }
          }
          &.product-add-to-cart{
            a{
              background: #222222;
              font-size: 14px;
              color: #FFFFFF !important;
              font-weight: 300;
              font-family: 'Ubuntu',sans-serif;
              line-height: 24px;
              padding:10px 30px;
              border:none;
              .border-radius(25px);
              width: 100%;
              .transition(all 0.3s ease-in-out 0s);
              &:hover{
                background: @color-theme;
              }
            }
          }
        }
      }
    }

    tfoot{
      tr{
        td{
          padding:0px;
          .yith-wcwl-share{
            margin:20px 0 0;
            h4.yith-wcwl-share-title{
              display: inline-block;
              float: left;
              margin:0 10px 0 0;
            }
            ul{
              li{
                margin:0px 5px;
              }
            }
          }
        }
      }
    }
  }
}