.cross-sells{
  margin-bottom: 78px;
  padding: 0px;
  float: none;
  width: 100%;
  h2{
    font-family: 'Montserrat',sans-serif;
    font-size: 20px;
    font-weight: 700;
    color: #222222;
    line-height: 24px;
    text-transform: uppercase;
    margin-bottom: 15px;
  }
  ul.products{
    margin:0px -15px;
    li.tzShop-item{
      margin:0px;
      padding:15px;
      width: 25%;
      .tzShop-item_inner{
        .tzShop-item_image{
          position: relative;
          overflow: hidden;
          img{
            position: relative;
            max-width: none;
          }
          .tzShop-item_overlay{
            width: 100%;
            height: 100%;
            background: rgba(0,0,0,0);
            position: absolute;
            top:0;
            left: 0;
            .transition(all 0.3s ease-in-out 0s);
          }
          .yith-wcwl-add-to-wishlist{
            position: absolute;
            top:50%;
            margin-top: -20px;
            left: 50%;
            margin-left: -45px;
            width: 40px;
            height: 40px;
            padding:0px;
            background: @color-theme;
            float: left;
            .transform(scale(1.5));
            opacity: 0;
            visibility: hidden;
            .transition(all 0.3s ease-in-out 0.3s);
            .border-radius(50%);
            .yith-wcwl-add-button{
              width: 100%;
              height: 100%;
              a{
                display: inline-block;
                width: 100%;
                height: 100%;
                text-align: center;
                padding:9px 0px;
                i{
                  font-size: 14px;
                  color: #000;
                  margin:0px;
                  padding:0px;
                }
                span{
                  position: absolute;
                  top:-60px;
                  left: 50%;
                  margin-left: -53px;
                  width: 106px;
                  padding:3px 0;
                  background: #fff;
                  font-family: 'Ubuntu',sans-serif;
                  font-weight: 400;
                  font-size: 12px;
                  color:#666666;
                  .border-radius(2px);
                  .transition(all 0.3s ease-in-out 0s);
                  opacity: 0;
                  visibility: hidden;
                  &:after{
                    content: '';
                    display: block;
                    border-left: 4px solid rgba(0, 0, 0, 0);
                    border-right: 4px solid rgba(0, 0, 0, 0);
                    border-top: 4px solid #fff;
                    left: 50%;
                    margin-left: -4px;
                    position: absolute;
                    top: 100%;
                    z-index: 9999;
                  }
                }
                &:hover{
                  span{
                    top:-44px;
                    opacity: 1;
                    visibility: visible;
                  }
                }
              }
            }

            .yith-wcwl-wishlistexistsbrowse{
              width: 100%;
              height: 100%;
              a{
                display: inline-block;
                width: 100%;
                height: 100%;
                text-align: center;
                padding:9px 0px;
                i{
                  font-size: 14px;
                  color: #000;
                  margin:0px;
                  padding:0px;
                }
                span{
                  position: absolute;
                  top:-60px;
                  left: 50%;
                  margin-left: -53px;
                  width: 106px;
                  padding:3px 0;
                  background: #fff;
                  font-family: 'Ubuntu',sans-serif;
                  font-weight: 400;
                  font-size: 12px;
                  color:#666666;
                  .border-radius(2px);
                  .transition(all 0.3s ease-in-out 0s);
                  opacity: 0;
                  visibility: hidden;
                  &:after{
                    content: '';
                    display: block;
                    border-left: 4px solid rgba(0, 0, 0, 0);
                    border-right: 4px solid rgba(0, 0, 0, 0);
                    border-top: 4px solid #fff;
                    left: 50%;
                    margin-left: -4px;
                    position: absolute;
                    top: 100%;
                    z-index: 9999;
                  }
                }
                &:hover{
                  span{
                    top:-44px;
                    opacity: 1;
                    visibility: visible;
                  }
                }
              }
            }
          }

          .tzShop-item_button{
            position: absolute;
            top:50%;
            margin-top: -20px;
            right: 50%;
            margin-right: -45px;
            width: 40px;
            height: 40px;
            padding:0px;
            background: @color-theme;
            .border-radius(50%);
            float: left;
            .transform(scale(1.5));
            opacity: 0;
            visibility: hidden;
            .transition(all 0.3s ease-in-out 0.3s);
            a{
              display: inline-block;
              width: 100%;
              height: 100%;
              text-align: center;
              padding:13px 0px;
              margin:0px;
              background: none;
              i{
                font-size: 14px;
                color: #000;
                margin:0px;
                padding:0px;
              }
              span{
                position: absolute;
                top:-60px;
                left: 50%;
                margin-left: -53px;
                width: 106px;
                padding:9px 0;
                background: #fff;
                font-family: 'Ubuntu',sans-serif;
                font-weight: 400;
                font-size: 12px;
                color:#666666;
                .border-radius(2px);
                .transition(all 0.3s ease-in-out 0s);
                opacity: 0;
                visibility: hidden;
                &:after{
                  content: '';
                  display: block;
                  border-left: 4px solid rgba(0, 0, 0, 0);
                  border-right: 4px solid rgba(0, 0, 0, 0);
                  border-top: 4px solid #fff;
                  left: 50%;
                  margin-left: -4px;
                  position: absolute;
                  top: 100%;
                  z-index: 9999;
                }
              }
              &:hover{
                span{
                  top:-44px;
                  opacity: 1;
                  visibility: visible;
                }
              }
            }
          }
        }
        &:hover{
          .tzShop-item_overlay{
            background: rgba(0,0,0,0.5);
          }
          .yith-wcwl-add-to-wishlist{
            .transform(scale(1));
            opacity: 1;
            visibility: visible;
          }
          .tzShop-item_button{
            .transform(scale(1));
            opacity: 1;
            visibility: visible;
          }
        }

        .tzShop-item_info{
          padding:18px 0 13px;
          h3.tzShop-item_title{
            margin:0px;
            padding:0px;
            a{
              font-family: 'Montserrat',sans-serif;
              font-size: 15px;
              font-weight: 700;
              color: #222222;
              line-height: 24px;
            }
          }

          .star-rating{
            margin:12px 0 0;
            &:before{
              color: #b5b5b5;
            }
            span{
              &:before{
                color: @color-theme;
              }
            }
          }

          .price{
            font-family: 'Montserrat',sans-serif;
            font-size: 15px;
            font-weight: 700;
            color: @color-theme;
            margin:10px 0 0;
            del{
              display: inline-block;
              color: #b5b5b5;
              span.amount{
                font-family: 'Montserrat',sans-serif;
                font-size: 15px;
                font-weight: 400;
                color: #b5b5b5;
              }
            }

            ins{
              display: inline-block;
              text-decoration: none;
              margin-left: 8px;
              span.amount{
                font-family: 'Montserrat',sans-serif;
                font-size: 15px;
                font-weight: 700;
                color: @color-theme;
              }
            }
          }

          .tzShop-item_des{
            display: none;
          }
          .tzShop-item_button_list{
            display: none;
          }
        }
      }
    }
  }
}