/**
 * 12.0 Page
 * -----------------------------------------------------------------------------
 */

.TzPage_Default{
  padding:125px 0;
  .tzComments{
    .comments-area{
      .tzCommentContent{
        margin: 65px 0px 0px;
        h2.comments-title{
          font-family: 'Montserrat',sans-serif;
          font-size: 20px;
          font-weight: 400;
          line-height: 24px;
          color: #222222;
          text-transform: uppercase;
          margin:0 0 25px;
          padding:0px;
        }
        ol.comment-list{
          margin:0px 0px 55px;
          padding:0px;
          position: relative;
          li{
            list-style: none;
            article{
              padding:0px 0px 30px;
              border-bottom:1px solid #ededed;
              margin:0px 0px 30px;
              .comment-author{
                display: block;
                float: left;
                .border-radius(50%);
                overflow: hidden;
              }
              .comment-content{
                margin-left: 120px;
                cite{
                  float: left;
                  font-family: 'Montserrat',sans-serif;
                  font-size: 14px;
                  font-weight: 400;
                  line-height: 22px;
                  color: #222222;
                  text-decoration: none;
                  text-transform: uppercase;
                  margin-bottom: 13px;
                }
                .tz-commentInfo{
                  margin-bottom: 13px;
                  .comments-datetime{
                    text-decoration: none;
                    time{
                      font-family: 'Ubuntu',sans-serif;
                      font-weight: 300;
                      font-size: 14px;
                      font-style: italic;
                      line-height: 22px;
                      color: #b5b5b5;
                    }
                  }
                  .comment-edit-link{
                    font-family: 'Ubuntu',sans-serif;
                    font-weight: 300;
                    font-size: 14px;
                    line-height: 22px;
                    color: @color-theme;
                    text-decoration: none;
                    margin-left: 20px;
                    i{
                      font-size: 13px;
                      margin-right: 5px;
                    }
                  }
                  .comment-reply-link{
                    font-family: 'Ubuntu',sans-serif;
                    font-weight: 300;
                    font-size: 14px;
                    line-height: 22px;
                    color: @color-theme;
                    text-decoration: none;
                    margin-left: 20px;
                    i{
                      font-size: 13px;
                      margin-right: 5px;
                    }
                  }
                }
              }
              p{
                font-family: 'Ubuntu',sans-serif;
                font-weight: 300;
                font-size: 14px;
                line-height: 24px;
                color: #666666;
              }
            }
            ol.children{
              padding-left: 120px;
            }
          }
        }
      }
      .tzCommentForm{
        .comment-respond{
          margin: 65px 0px 0px;
          h3.comment-reply-title{
            font-family: 'Montserrat',sans-serif;
            font-size: 20px;
            font-weight: 400;
            line-height: 24px;
            color: #222222;
            text-transform: uppercase;
            margin:0px;
            padding:0px;
          }
          form.comment-form{
            p{
              font-family: 'Ubuntu',sans-serif;
              font-weight: 300;
              font-size: 14px;
              line-height: 24px;
              color: #666666;
              margin: 0 0 23px;
              &.comment-form-comment{
                margin:0px;
              }
              label {
                display: none;
              }
              a{
                color: @color-theme;
                text-decoration: none;
              }
              textarea{
                width: 100%;
                height: 95px;
                border-width: 1px;
                border-color: #ededed;
                border-style: solid;
                .border-radius(2px);
                font-family: 'Ubuntu',sans-serif;
                font-weight: 300;
                line-height: 24px;
                font-size: 14px;
                color:#b5b5b5;
                padding:10px 20px;
              }
              &.form-submit{
                margin:17px 0px 0px;
                input.submit{
                  background: @color-theme;
                  border:none;
                  .border-radius(2px);
                  font-family: 'Montserrat',sans-serif;
                  font-size: 14px;
                  font-weight: 700;
                  color: #FFFFFF;
                  line-height: 22px;
                  text-transform: uppercase;
                  padding: 11px 25px;

                }
              }
            }
            .tzCommentForm_Top{
              margin-bottom: 20px;
              p{
                margin:0px;
                input{
                  width: 100%;
                  border-width: 1px;
                  border-color: #ededed;
                  border-style: solid;
                  .border-radius(2px);
                  font-family: 'Ubuntu',sans-serif;
                  font-weight: 300;
                  line-height: 24px;
                  font-size: 14px;
                  color:#b5b5b5;
                  padding:10px 20px;
                  margin:0px;
                }
              }
            }
          }
        }
      }
    }
  }
}