/* 13.3 Shop Detail */

.tzShopDetail-wrap {
  padding:61px 0px;
  .tzShopContentDetail{
    .tzShopDetail_Product{
      .tzShopDetail_images{
        &:after{
          content: '';
          display: block;
          clear: left;
        }
        #tzShopDetail_slide {
          float:right;
          margin-right:0px;
          position: relative;
          height: 603px;
          overflow: hidden;
          ul {
            margin:0;
            padding:0;
            width:100%;
            height: 100%;
            li {
              width:100%;
              height: 603px;
              display:none;
              margin:0px;
              padding:0px;
              position:relative;
              overflow: hidden;
              .border-radius(0);
              img{
                position: relative;
                max-width: none;
              }
              a {
                position: absolute;
                top:10px;
                left:15px;
                font-family: 'Roboto',sans-serif;
                font-weight: 400;
                font-size: 10px;
                line-height: 18px;
                color: #656565;
                i {
                  padding-right:5px;
                }
              }
              a:hover {
                text-decoration: none;
              }
              img {
                width:100%;
                height: auto;
              }
            }
          }
          li.active {
            display:block !important;
          }
          li {
            span.opacity {
              position:absolute;
              bottom:0;
              left:0;
              display:block;
              width:100%;
              height:60px;
              background:#000;
              filter:alpha(opacity=50);
              -moz-opacity:0.5;
              -khtml-opacity: 0.5;
              opacity: 0.5;
              z-index:500;
            }
            span.content {
              position:absolute;
              bottom:0;
              left:0;
              display:block;
              width:100%;
              height:60px;
              z-index:1000;
              h1 {
                font-size:14px;
                margin:5px 0;
                padding:0 10px;
                color:#42e2e8;
              }
              p {
                font-size:11px;
                margin:5px 0;
                padding:0 10px;
                color:#42e2e8;
              }
            }
          }
        }

        #tzShopDetailSlide-carousel {
          float:left;
          width:95px;
          margin-right:14px;
          position:relative;
          .jcarousel-skin-tango {
            .jcarousel-container-vertical{
              width: 100%!important;
              height: 605px!important;
              .jcarousel-next{
                display: none!important;
              }
              .jcarousel-prev {
                display: none!important;
              }
              .jcarousel-clip-vertical {
                width: 100%!important;
                height: 605px!important;
              }
            }
          }
          ul {
            margin:0;
            padding:0;
            list-style:none;
            li {
              background:#fff;
              height:115px;
              width: 95px;
              margin-bottom:7px;
              position:relative;
              border:5px solid rgba(0, 0, 0, 0);
              float: none;
              &:after {
                content: '';
                width: 100%;
                height: 100%;
                position: absolute;
                top:0px;
                left: 0px;
                border:1px solid rgba(0,0,0,0);
                transform: scaleX(1.11) scaleY(1.09);
                -webkit-transform: scaleX(1.11) scaleY(1.09);
                -moz-transform: scaleX(1.11) scaleY(1.09);
                -ms-transform: scaleX(1.11) scaleY(1.09);
                -o-transform: scaleX(1.11) scaleY(1.09);
              }
              a {
                display: inline-block;
                height:105px;
                width: 85px;
                position:relative;
                overflow: hidden;
                z-index: 1;
                img{
                  position: relative;
                  max-width: none;
                }
              }
              .arrow {
                left:3px;
                top:28px;
                position:absolute;
                width:20px;
                height:40px;
                background:url(images/arrow_white.png) no-repeat 0 0;
                display:block;
              }
              &.bd_active:after {
                content: '';
                width: 100%;
                height: 100%;
                position: absolute;
                top:0px;
                left: 0px;
                border:1px solid @color-theme;
                transform: scaleX(1.11) scaleY(1.09);
                -webkit-transform: scaleX(1.11) scaleY(1.09);
                -moz-transform: scaleX(1.11) scaleY(1.09);
                -ms-transform: scaleX(1.11) scaleY(1.09);
                -o-transform: scaleX(1.11) scaleY(1.09);
              }
              &:hover{
                :after{
                  content: '';
                  width: 100%;
                  height: 100%;
                  position: absolute;
                  top:0px;
                  left: 0px;
                  border:1px solid @color-theme;
                  transform: scaleX(1.11) scaleY(1.09);
                  -webkit-transform: scaleX(1.11) scaleY(1.09);
                  -moz-transform: scaleX(1.11) scaleY(1.09);
                  -ms-transform: scaleX(1.11) scaleY(1.09);
                  -o-transform: scaleX(1.11) scaleY(1.09);
                }
              }
            }
            .active {
              filter:alpha(opacity=100);
              -moz-opacity:1.0;
              -khtml-opacity: 1.0;
              opacity: 1.0;
            }
            .faded {
              filter:alpha(opacity=50);
              -moz-opacity:0.5;
              -khtml-opacity: 0.5;
              opacity: 0.5;
            }
          }
        }
      }
      .tzShopDetail_info{
        width: 100%;
        h1.product_title{
          font-family: 'Montserrat',sans-serif;
          font-size: 20px;
          font-weight: 400;
          text-decoration: none;
          text-transform: uppercase;
          color: #222222;
          margin-bottom: 8px;
        }

        .woocommerce-product-rating{
          margin-bottom: 14px;
          .star-rating{
            span{
              &:before{
                color: @color-theme;
              }
            }
          }

          a.woocommerce-review-link{
            font-family: 'Ubuntu',sans-serif;
            font-size: 14px;
            font-weight: 300;
            color: #b5b5b5;
            line-height: 24px;
          }
        }

        p.price{
          font-family: 'Montserrat',sans-serif;
          font-weight: 700;
          font-size: 25px;
          color: @color-theme;
          line-height: 25px;
          margin-bottom: 27px;
          span.amount{
            font-family: 'Montserrat',sans-serif;
            font-weight: 700;
            font-size: 25px;
            color: @color-theme;
            line-height: 25px;

          }
          del{
            text-decoration: none;
            opacity: 1;
            margin-right: 13px;
            span.amount{
              font-weight: 400;
              color: #b5b5b5;
              text-decoration: line-through;
            }
          }

          ins{
            text-decoration: none;
          }
        }
        div[itemprop="description"] {
          border-bottom:1px solid #ededed;
          padding-bottom: 39px;
          margin-bottom: 30px;
        }
        form.cart{
          display: inline-block;
          float: left;
          .quantity{
            margin-right: 10px;
            input{
              font-family: 'Ubuntu',sans-serif;
              font-size: 14px;
              font-weight: 300;
              color: #666666;
              line-height: 24px;
              border:1px solid #ededed;
              .border-radius(2px);
              padding:7px 10px;
              width: 70px;
            }
          }

          button{
            font-family: 'Ubuntu',sans-serif;
            font-size: 14px;
            font-weight: 400;
            color: #FFFFFF;
            line-height: 24px;
            background: #c7c7c7;
            .border-radius(20px);
            padding:7px 20px;
            .transition(all 0.3s ease-in-out 0s);
            &:hover{
              background: @color-theme;
            }
          }

          table.group_table{
            tbody{
              tr{
                td{
                  label{
                    a{
                      font-family: 'Montserrat',sans-serif;
                      font-size: 14px;
                      font-weight: 400;
                      text-decoration: none;
                      color: #222222;
                      display: inline-block;
                      padding: 22px 0px;
                    }
                  }
                  &.price{
                    span.amount{
                      font-family: 'Montserrat',sans-serif;
                      font-size: 14px;
                      font-weight: 400;
                      text-decoration: none;
                      color: @color-theme;
                      padding: 8px 0px;
                      display: inline-block;
                    }
                    del{
                      span.amount{
                        text-decoration: line-through;
                        color: #b5b5b5;
                      }
                    }
                  }
                }
              }
            }
          }
        }

        p.cart{
          display: inline-block;
          float: left;
          a{
            font-family: 'Ubuntu',sans-serif;
            font-size: 14px;
            font-weight: 400;
            color: #FFFFFF;
            line-height: 24px;
            background: #c7c7c7;
            .border-radius(20px);
            padding:7px 20px;
            .transition(all 0.3s ease-in-out 0s);
            &:hover{
              background: @color-theme;
            }
          }
        }

        form.variations_form{
          table.variations{
            margin-bottom: 0px;
            tbody{
              tr{
                td{
                  label{
                    font-family: 'Ubuntu',sans-serif;
                    font-size: 16px;
                    font-weight: 400;
                    color:#666666;
                    padding:20px 0px;
                    text-transform: capitalize;
                  }
                  &.value{
                    select{
                      font-family: 'Ubuntu',sans-serif;
                      font-size: 14px;
                      font-weight: 300;
                      color: #666666;
                      line-height: 24px;
                      border:1px solid #ededed;
                      .border-radius(2px);
                      padding:7px 10px;
                    }

                    a.reset_variations{
                      font-family: 'Ubuntu',sans-serif;
                      font-size: 14px;
                      font-weight: 300;
                      color: #666666;
                      line-height: 24px;
                      text-decoration: none;
                      display: inline-block !important;
                      margin-top: 5px;
                      &:hover{
                        color: @color-theme;
                      }
                    }
                  }
                }
              }
            }
          }

          .single_variation_wrap{
            .single_variation{
              margin-left: 40px;
              margin-bottom: 17px;
              span.price{
                font-family: 'Montserrat',sans-serif;
                font-weight: 700;
                font-size: 20px;
                color: @color-theme;
                line-height: 25px;
                span.amount{
                  font-family: 'Montserrat',sans-serif;
                  font-weight: 700;
                  font-size: 20px;
                  color: @color-theme;
                  line-height: 25px;

                }
                del{
                  text-decoration: none;
                  opacity: 1;
                  margin-right: 13px;
                  span.amount{
                    font-weight: 400;
                    color: #b5b5b5;
                    text-decoration: line-through;
                  }
                }
                ins{
                  text-decoration: none;
                }
              }
            }
            .variations_button{
              &:after{
                content: '';
                display: block;
                clear: both;
              }
            }
          }
        }

        p.out-of-stock{
          font-family: 'Ubuntu',sans-serif;
          font-size: 24px;
          font-weight: 400;
          margin-bottom: 20px;
        }

        .yith-wcwl-add-to-wishlist{
          display: inline-block;
          margin:0 0 30px;
          float: left;
          .yith-wcwl-add-button{
            margin-left: 10px;
            a{
              display: inline-block;
              font-family: 'Ubuntu',sans-serif;
              font-size: 14px;
              font-weight: 400;
              color: #FFFFFF;
              line-height: 24px;
              background: #c7c7c7;
              .border-radius(20px);
              padding:7px 20px;
              text-decoration: none;
              .transition(all 0.3s ease-in-out 0s);
              &:hover{
                background: @color-theme;
              }
            }
            img{
              display: none !important;
            }
          }

          .yith-wcwl-wishlistaddedbrowse{
            margin-left: 10px;
            span.feedback{
              display: none;
            }
            a{
              display: inline-block;
              font-family: 'Ubuntu',sans-serif;
              font-size: 14px;
              font-weight: 400;
              color: #FFFFFF;
              line-height: 24px;
              background: #c7c7c7;
              .border-radius(20px);
              padding:7px 20px;
              text-decoration: none;
              .transition(all 0.3s ease-in-out 0s);
              &:hover{
                background: @color-theme;
              }
            }
          }

          .yith-wcwl-wishlistexistsbrowse{
            margin-left: 10px;
            span.feedback{
              display: none;
            }
            a{
              display: inline-block;
              font-family: 'Ubuntu',sans-serif;
              font-size: 14px;
              font-weight: 400;
              color: #FFFFFF;
              line-height: 24px;
              background: #c7c7c7;
              .border-radius(20px);
              padding:7px 20px;
              text-decoration: none;
              .transition(all 0.3s ease-in-out 0s);
              &:hover{
                background: @color-theme;
              }
            }
          }
        }

        .product_meta{
          border-top: 1px solid #ededed;
          margin-top: 2px;
          padding-top: 44px;
          span{
            font-family: 'Ubuntu',sans-serif;
            font-size: 14px;
            font-weight: 400;
            color: #666666;
            margin-right: 15px;
            span{
              font-weight: 300;
              margin-right: 0;
            }
            a{
              font-weight: 300;
              color: #666666;
              text-decoration: none;
              &:hover{
                color: @color-theme;
              }
            }
          }
        }

        .product_share{
          margin-top: 25px;
          span{
            font-family: 'Ubuntu',sans-serif;
            font-size: 14px;
            font-weight: 400;
            color: #666666;
            margin-right: 15px;
            float: left;
            line-height: 40px;
          }
          .product_share_social{
            .tz_social{
              background-color: #c7c7c7;
              .border-radius(0);
              cursor: pointer;
              display: inline-block;
              font-size: 15px;
              height: 40px;
              line-height: 25px;
              margin: 0 5px;
              position: relative;
              text-align: center;
              -webkit-user-select: none;
              -moz-user-select: none;
              -ms-user-select: none;
              user-select: none;
              width: 40px;
              .transition(all 0.3s ease-in-out 0s);
              span{
                background: @color-theme;
                border-radius: 0;
                display: block;
                height: 0;
                left: 50%;
                margin: 0;
                position: absolute;
                top: 50%;
                .transition(all 0.3s ease-in-out 0s);
                width: 0;
              }
              &:hover{
                .border-radius(50%);
                span{
                  width: 40px;
                  height: 40px;
                  .border-radius(50%);
                  top:0;
                  left: 0;
                }
              }
              i{
                background: none;
                color: #ffffff;
                height: 40px;
                left: 0;
                line-height: 40px;
                position: absolute;
                top: 0;
                .transition(all 0.3s ease-in-out 0s);
                width: 40px;
                z-index: 10;
              }
            }
          }
        }
      }

      &.product-type-grouped{
        .tzShopDetail_info{
          form.cart{
            display: block;
            float: none;
          }

          .yith-wcwl-add-to-wishlist{
            .yith-wcwl-add-button{
              margin-left: 0px;
            }

            .yith-wcwl-wishlistaddedbrowse{
              margin-left: 10px;
              span.feedback{
                display: none;
              }
              a{
                display: inline-block;
                font-family: 'Ubuntu',sans-serif;
                font-size: 14px;
                font-weight: 400;
                color: #FFFFFF;
                line-height: 24px;
                background: #c7c7c7;
                .border-radius(20px);
                padding:7px 20px;
                text-decoration: none;
                .transition(all 0.3s ease-in-out 0s);
                &:hover{
                  background: @color-theme;
                }
              }
            }

            .yith-wcwl-wishlistexistsbrowse{
              margin-left: 0px;
            }
          }
        }
      }

      &.product-type-variable{
        .tzShopDetail_info{
          form.cart{
            display: block;
            float: none;
          }

          .yith-wcwl-add-to-wishlist{
            .yith-wcwl-add-button{
              margin-left: 0px;
            }

            .yith-wcwl-wishlistaddedbrowse{
              margin-left: 10px;
              span.feedback{
                display: none;
              }
              a{
                display: inline-block;
                font-family: 'Ubuntu',sans-serif;
                font-size: 14px;
                font-weight: 400;
                color: #FFFFFF;
                line-height: 24px;
                background: #c7c7c7;
                .border-radius(20px);
                padding:7px 20px;
                text-decoration: none;
                .transition(all 0.3s ease-in-out 0s);
                &:hover{
                  background: @color-theme;
                }
              }
            }

            .yith-wcwl-wishlistexistsbrowse{
              margin-left: 0px;
            }
          }
        }
      }

      &.outofstock{
        .yith-wcwl-add-to-wishlist{
          .yith-wcwl-add-button{
            margin-left: 0px;
          }

          .yith-wcwl-wishlistaddedbrowse{
            margin-left: 10px;
            span.feedback{
              display: none;
            }
            a{
              display: inline-block;
              font-family: 'Ubuntu',sans-serif;
              font-size: 14px;
              font-weight: 400;
              color: #FFFFFF;
              line-height: 24px;
              background: #c7c7c7;
              .border-radius(20px);
              padding:7px 20px;
              text-decoration: none;
              .transition(all 0.3s ease-in-out 0s);
              &:hover{
                background: @color-theme;
              }
            }
          }

          .yith-wcwl-wishlistexistsbrowse{
            margin-left: 0px;
          }
        }
      }

      .woocommerce-tabs{
        border-bottom: 1px solid #ededed;
        display: block;
        padding: 0 0 76px;
        margin-bottom: 45px;
        margin-top: 73px;

        ul.tabs{
          margin:0px;
          padding:0px;
          &:before{
            border: none;
          }
          li{
            margin:0 52px 0 0;
            padding:0px;
            border:none;
            background: none;
            &:before{
              content: none;
            }
            &:after{
              content: none;
            }
            a{
              margin:0px;
              padding:0px;
              font-family: 'Montserrat',sans-serif;
              font-size: 20px;
              font-weight: 400;
              color: #222222;
              line-height: 24px;
              text-transform: uppercase;
              &:hover{
                color: @color-theme;
              }
            }
            &.active{
              a{
                color: @color-theme;
              }
            }
          }
        }

        .panel{
          margin:0px;
          padding:31px 0 0;
          border:none;
          box-shadow: none;
          background: none;
          h2{
            font-family: 'Ubuntu',sans-serif;
            font-weight: 400;
            font-size: 20px;
            color:#222222;
            margin-bottom: 10px;
          }
          table.shop_attributes{
            margin: 20px 0 0;
          }
          #reviews{
            #comments{
              margin-bottom: 20px;
              ol.commentlist{
                margin:20px 0 0;
                padding:0px;
                li{
                  .comment_container{
                    &:after{
                      content: '';
                      display: block;
                      clear: both;
                    }
                    img{
                      width: 90px;
                      height: 90px;
                      background: none;
                      border:none;
                      padding:0px;
                      .border-radius(50%);
                    }
                    .comment-text{
                      margin-left: 120px;
                      .border-radius(0);
                      padding:10px 15px;
                      border: none;
                      p.meta{
                        margin-bottom: 2px;
                        strong[itemprop="author"]{
                          font-family: 'Montserrat',sans-serif;
                          font-weight: 400;
                          font-size: 14px;
                          color:#222222;
                          text-transform: uppercase;
                        }
                        time{
                          font-style: italic;
                          font-size: 14px;
                          color:#b5b5b5;
                        }
                      }
                    }
                  }
                }
              }
            }
            #review_form_wrapper{
              #review_form{
                .comment-respond{
                  h3.comment-reply-title{
                    font-size: 20px;
                    font-family: 'Ubuntu',sans-serif;
                    font-weight: 400;
                    color: #222222;
                    margin-bottom: 10px;
                  }

                  form#commentform{
                    p{
                      label{
                        font-family: 'Ubuntu',sans-serif;
                        font-weight: 400;
                        font-size: 14px;
                        color:#222222;
                        margin-bottom: 10px;
                      }
                      p.stars{
                        span{
                          a{
                            &:after{
                              color: #b5b5b5;
                            }
                            &.active:after{
                              color: @color-theme;
                            }
                            &:hover:after{
                              color: @color-theme;
                            }
                          }
                        }
                      }

                      textarea{
                        border:1px solid #ededed;
                        .border-radius(0);

                        font-family: 'Ubuntu',sans-serif;
                        font-weight: 300;
                        font-size: 14px;
                        color:#666666;
                        padding:10px 15px;
                      }
                      &.form-submit{
                        input{
                          display: inline-block;
                          font-family: 'Ubuntu',sans-serif;
                          font-size: 14px;
                          font-weight: 400;
                          color: #FFFFFF;
                          line-height: 24px;
                          background: #c7c7c7;
                          .border-radius(20px);
                          padding:7px 20px;
                          text-decoration: none;
                          .transition(all 0.3s ease-in-out 0s);
                          &:hover{
                            background: @color-theme;
                          }
                        }
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }

      &.tzShopDetail_navigationSlide_right{
        .tzShopDetail_images{
          #tzShopDetail_slide {
            float:left;
          }

          #tzShopDetailSlide-carousel {
            margin-left:15px;
            margin-right: 0px;
          }
        }
      }
    }
  }
}
