/* 13.6 Cart */
.woocommerce{
  .woocommerce-message{
    font-family: 'Ubuntu',sans-serif;
    font-size: 14px;
    font-weight: 300;
    line-height: 24px;
    color: #666666;
  }
  ul.woocommerce-error{
    li{
      font-family: 'Ubuntu',sans-serif;
      font-size: 14px;
      font-weight: 300;
      line-height: 24px;
      color: #666666;
    }
  }
  p{
    &.return-to-shop{
      a.button{
        background: #222222 none repeat scroll 0 0;
        border: medium none;
        border-radius: 25px;
        color: #ffffff !important;
        font-family: "Ubuntu",sans-serif;
        font-size: 14px;
        font-weight: 300;
        line-height: 24px;
        padding: 10px 30px;
        transition: all 0.3s ease-in-out 0s;
        &:hover{
          background: @color-theme;
        }
      }
    }
  }
  form{
    table.shop_table{
      border:none;
      margin-bottom: 78px;
      thead{
        tr{
          th{
            font-family: 'Montserrat',sans-serif;
            font-size: 16px;
            font-weight: 400;
            color: #212121;
            line-height: 24px;
            text-transform: uppercase;
            text-align: center;
            padding:14px 20px;
            &.product-name{
              text-align: left;
            }
          }
        }
      }

      tbody{
        tr{
          &.cart_item {
            td {
              text-align: center;
              padding: 20px;
              a {
                font-size: 15px;
                color: #222222 !important;
                font-weight: 400;
                font-family: 'Montserrat', sans-serif;
                line-height: 24px;
                text-decoration: none;
                &.remove {
                  display: inline-block;
                }
                img {
                  width: 80px;
                  height: auto;
                }
              }
              span.amount {
                font-size: 15px;
                color: #222222 !important;
                font-weight: 400;
                font-family: 'Montserrat', sans-serif;
                line-height: 24px;
              }
              &.product-remove {
                padding: 20px 0px;
                a{
                  width: 22px;
                  height: 22px;
                  &:hover{
                    background: @color-theme;
                    color: #fff !important;
                  }
                }
              }
              &.product-thumbnail {
                width: 80px;
                padding: 20px 0px;
              }
              &.product-name {
                text-align: left;
              }
              &.product-subtotal {
                span.amount {
                  color: @color-theme !important;
                }
              }
              &.product-quantity {
                .quantity {
                  input {
                    font-size: 14px;
                    color: #666666 !important;
                    font-weight: 300;
                    font-family: 'Cinzel', serif;
                    line-height: 24px;
                    padding: 9px 15px;
                    border: 1px solid #e0e0e0;
                    .border-radius(2px);
                    width: 90px;
                  }
                }
              }
            }
          }
          td{
            &.actions{
              padding: 30px 0;
              input{
                background: #222222;
                font-size: 14px;
                color: #FFFFFF;
                font-weight: 300;
                font-family: 'Ubuntu',sans-serif;
                line-height: 24px;
                padding:10px 30px;
                border:none;
                .border-radius(25px);
                width: auto;
                .transition(all 0.3s ease-in-out 0s);
                &:hover{
                  background: @color-theme;
                }
              }
              .coupon{
                input{
                  font-size: 14px;
                  color: #666666 !important;
                  font-weight: 300;
                  font-family: 'Ubuntu',sans-serif;
                  line-height: 24px !important;
                  padding:10px 15px !important;
                  border:1px solid #e0e0e0 !important;
                  .border-radius(2px);
                  width: 270px;
                  background: none;
                  &.input-text{
                    margin: 0 10px 0 0;
                  }
                  &.button{
                    .border-radius(25px);
                    background: #222222;
                    color: #ffffff !important;
                    border:none !important;
                    padding: 10px 30px !important;
                    width: auto;
                    .transition(all 0.3s ease-in-out 0s);
                    &:hover{
                      background: @color-theme;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
  .cart-collaterals{
    @import "cross-sells";
    .tzCart_totals{
      .tzCollateralsColumn{
        form.woocommerce-shipping-calculator{
          p{
            margin-bottom: 30px;
            a{
              font-family: 'Montserrat',sans-serif;
              font-size: 20px;
              font-weight: 700;
              color: #222222;
              line-height: 24px;
              text-transform: uppercase;
              margin-bottom: 15px;
              text-decoration: none;
            }
          }
          .shipping-calculator-form{
            display: block !important;
            border:1px solid #dadada;
            padding:59px 49px;
            p{
              margin:0px;
              padding:0px;
              select{
                font-family: 'Ubuntu',sans-serif;
                font-size: 14px;
                font-weight: 300;
                color: #666666;
                line-height: 24px;
                padding:12px 12px;
                margin-bottom: 20px;
                border:1px solid #dadada;
                .border-radius(2px);
              }
              input{
                font-family: 'Ubuntu',sans-serif;
                font-size: 14px;
                font-weight: 300;
                color: #666666;
                line-height: 24px;
                padding:10px 15px;
                margin-bottom: 20px;
                border:1px solid #dadada;
                .border-radius(2px);
              }
              button.button{
                background: #222222;
                font-size: 14px;
                color: #FFFFFF;
                font-weight: 300;
                font-family: 'Ubuntu',sans-serif;
                line-height: 24px;
                padding:10px 30px;
                border:none;
                .border-radius(25px);
                width: 100%;
                .transition(all 0.3s ease-in-out 0s);
                &:hover{
                  background: @color-theme;
                }
              }
            }
          }
        }

        .cart_totals{
          width: 100%;
          h2{
            font-family: 'Montserrat',sans-serif;
            font-size: 20px;
            font-weight: 700;
            color: #222222;
            line-height: 24px;
            text-transform: uppercase;
            margin-bottom: 31px;
          }
          table{
            padding:45px 70px 0px;
            background: #ededed;
            margin:0px;
            width: 100%;
            tbody{
              tr{
                th{
                  font-family: 'Montserrat',sans-serif;
                  font-size: 15px;
                  font-weight: 400;
                  color: #222222;
                  line-height: 24px;
                  border:none;
                  padding: 13px 0px;
                }
                td{
                  border:none;
                  line-height: 24px;
                  padding: 13px 0px;
                  span{
                    font-family: 'Montserrat',sans-serif;
                    font-size: 15px;
                    font-weight: 400;
                    color: #222222;
                    line-height: 24px;
                  }
                }
                &.order-total{
                  td{
                    span{
                      color: @color-theme;
                    }
                  }
                }
              }
            }
          }
          .wc-proceed-to-checkout{
            background: #ededed;
            margin:0px;
            padding:19px 70px 50px;
            a{
              background: #222222;
              font-size: 14px;
              color: #FFFFFF;
              font-weight: 300;
              font-family: 'Ubuntu',sans-serif;
              line-height: 24px;
              padding:10px 30px;
              border:none;
              .border-radius(25px);
              width: 100%;
              .transition(all 0.3s ease-in-out 0s);
              &:hover{
                background: @color-theme;
              }
            }
          }
        }
      }
    }
  }
}