/* 11.14 Portfolio Grid */
.tzPortfolio_Grid{
  .tzfilter{
    text-align: center;
    margin-bottom: 60px;
    .tzFillter_box{
      display: inline-block;
      padding:0 25px;
      a{
        display: inline-block;
        font-family: 'Ubuntu',sans-serif;
        font-size: 14px;
        font-weight: 300;
        color: #666666;
        padding:13px 25px;
        background: #fdfdfd;
        border:1px solid #f0f0f0;
        float: left;
        text-decoration: none;
        margin-left: -1px;
        margin-bottom: 10px;
        z-index: 0;
        .transition(all 0.3s ease-in-out 0s);
        &.selected{
          z-index: 1;
          position: relative;
          border: 1px solid @color-theme;
          background: @color-theme;
          color: #ffffff;
        }
        &:hover{
          z-index: 1;
          position: relative;
          border: 1px solid @color-theme;
          background: @color-theme;
          color: #ffffff;
        }
      }

    }
  }

  .tzPortfolioGrid_Content{
    .tzPortfolioGrid-item{
      .tz-inner{
        position: relative;
        overflow: hidden;
        .item-img{
          position: relative;
          overflow: hidden;
          img{
            position: relative;
            max-width: none;
//            .transform(scale(1) rotate(0));
            //              .transition(all 2s ease-in-out 0s);
          }
        }
        .tzPortfolioGrid_hover1{
          position: absolute;
          top:0px;
          left: 0px;
          width: 100%;
          height: 100%;
          padding:20px;
          .transform(scale(1.05));
          .transition(all 0.3s ease-in-out 0s);
          visibility: hidden;
          opacity: 0;
          .tzPortfolioGrid_table{
            display: table;
            width: 100%;
            height: 100%;
            background: rgba(255,255,255,0.95);
            .tzPortfolioGrid_table_cell{
              display: table-cell;
              vertical-align:middle;
              h3{
                margin:0 0 16px;
                padding:0 30px 19px;
                text-align: center;
                position: relative;
                a{
                  font-family: 'Montserrat',sans-serif;
                  font-size: 14px;
                  font-weight: 700;
                  color: #222222;
                  line-height: 24px;
                  text-decoration: none;
                  text-transform: uppercase;
                }
                &:after{
                  content: '';
                  display: block;
                  width: 50px;
                  height: 1px;
                  background: #222222;
                  position: absolute;
                  bottom:0px;
                  left: 50%;
                  margin-left: -25px;
                }
              }
              span.tzcat{
                margin:0px;
                padding:0 30px;
                display: block;
                text-align: center;
                a{
                  font-family: 'Ubuntu',sans-serif;
                  font-size: 14px;
                  font-weight: 300;
                  font-style: italic;
                  color: #666666;
                  line-height: 24px;
                  text-decoration: none;
                  display: inline-block;
                }
              }
            }
          }
        }
        .tzPortfolioGrid_hover2{
          position: absolute;
          top:0px;
          left: 0px;
          width: 100%;
          height: 100%;
          .tzOverlayImage{
            //              position: absolute;
            //              top:0px;
            //              left: 0px;
            width: 100%;
            height: 100%;
            background: rgba(0,0,0,0.2);
            .transform(scale(1.1));
            visibility: hidden;
            opacity: 0;
            .transition(all 0.3s ease-in-out 0s);
          }
          a{
            display: block;
            width: 40px;
            height: 40px;
            position: absolute;
            top:50%;
            margin-top: -20px;
            background: #FFFFFF;
            text-align: center;
            .transition(all 0.3s ease-in-out 0s);
            .border-radius(50%);
            visibility: hidden;
            opacity: 0;
            &.tz-ViewImage{
              right: 50%;
              margin-right: 45px;
              .transition(all 0.3s ease-in-out 0s);
            }
            &.tz-ViewDetail{
              left: 50%;
              margin-left: 45px;
              .transition(all 0.3s ease-in-out 0s);
            }
            i{
              color: #222222;
              font-size: 14px;
              padding: 13px 0px;
              .transition(all 0.3s ease-in-out 0s);
            }
            &:hover{
              background: @color-theme;
              i{
                color: #FFFFFF;
              }
            }
          }
        }
        .tzPortfolioGrid_hover3{
          position: absolute;
          bottom:-100%;
          left: 0;
          background: rgba(251,192,45,0.9);
          width: 100%;
          padding:16px 30px 17px;
          .transition(all 0.3s ease-in-out 0s);
          h3{
            margin:0 0 3px;
            padding:0px;
            a{
              font-family: 'Montserrat',sans-serif;
              font-size: 14px;
              font-weight: 700;
              line-height: 24px;
              color: #ffffff;
              text-transform: uppercase;
              text-decoration: none;
            }
          }
          span.tzcat{
            font-family: 'Ubuntu',sans-serif;
            font-weight: 300;
            font-size: 14px;
            font-style: italic;
            line-height: 24px;
            color: #FFFFFF;
            a{
              color: #FFFFFF;
              text-decoration: none;
            }
          }
        }
      }

      &:hover .tz-inner{
          .item-img{
            img{
              .transition(all 2s ease-in-out 0s);
              .transform(scale(1.2) rotate(4deg));
            }
          }
          .tzPortfolioGrid_hover1{
            .transform(scale(1));
            visibility: visible;
            opacity: 1;
          }
          .tzPortfolioGrid_hover2{
            .tzOverlayImage{
              background: rgba(0,0,0,0.5);
              .transform(scale(1));
              visibility: visible;
              opacity: 1;
            }
            a{
              visibility: visible;
              opacity: 1;
              &.tz-ViewImage{
                margin-right: 5px;
                .transform(rotate(360deg));
              }
              &.tz-ViewDetail{
                margin-left: 5px;
                .transform(rotate(-360deg));
              }
            }
          }
          .tzPortfolioGrid_hover3{
            position: absolute;
            bottom:0;
          }
      }
    }
  }

  #tz_append {
    margin-top: 80px;
    text-align: center;
    a{
      border: 3px solid #d0cfcf;
      border-radius: 50%;
      display: inline-block;
      height: 65px;
      width: 65px;
      i{
        color: #d0cfcf;
        font-size: 45px;
        padding: 9px 0;
      }
    }
  }
}