/**
 * 10.0 Single Portfolio
 * -----------------------------------------------------------------------------
 */

.tzPortfolio_Single{
  padding:90px 0px 100px;
  .tzPortfolio_Single_Container{
    .tzPortfolio_Single_video{
      iframe{
        width: 100%;
        height: 100%;
        border:none;
      }
    }
    .tzPortfolio_SingleImage{
      img{
        display: block;
        margin:auto;
        max-width: 100%;
        height: auto;
      }
    }
    .tzPortfolio_Single_Slider{
      position: relative;
      ol.flex-control-nav{
        bottom:8px;
        li{
          margin: 0 5px;
          a{
            height: 10px;
            width: 10px;
            background: #ffffff;
            .box-shadow(none);
            .transition(all 0.3s ease-in-out 0s);
            &.flex-active{
              background: @color-theme;
            }
            &:hover{
              background: @color-theme;
            }
          }
        }
      }
      ul.flex-direction-nav{
        z-index: 9999;
        li{
          a{
            top:50%;
            opacity: 1;
            height: 40px;
            width: 40px;
            margin:-20px 0 0;
            .border-radius(2px);
            font-size: 0;
            text-align: center;
            background: @color-theme;
            &:before{
              font-size: 14px;
              font-weight: lighter;
              color: #ffffff;
              text-shadow: none;
              padding:10px 0px;
            }
            &.flex-prev{
              left: 30px;
            }
            &.flex-next{
              right: 30px;
            }
            &:hover{
              background: @color-theme;
            }
          }
        }
      }
      ul{
        li{
          img{
            display: block;
            max-width: 100%;
            margin:auto;
            height: auto;
          }
        }
      }
    }
    .tzPortfolio_Single_videoHtml5{
      width: 100%;
      height: 100%;
      overflow: hidden;
      position: relative;
      .tzPortfolio_autoplay{
        background: @color-theme;
        border: 0 none;
        .border-radius(50%);
        height:50px;
        left: 50%;
        margin-left: -25px;
        margin-top: -25px;
        opacity: 1;
        position: absolute;
        top: 50%;
        width: 50px;
        z-index: 99;
        text-align: center;
        i{
          display: inline-block;
          font-size: 16px;
          color: #FFFFFF;
          padding: 17px 0px;
        }
        &:hover{
          cursor: pointer;
        }
      }
      .tzPortfolio_pauses{
        background: @color-theme;
        border: 0 none;
        .border-radius(50%);
        height:50px;
        left: 50%;
        margin-left: -25px;
        margin-top: -25px;
        opacity: 1;
        position: absolute;
        top: 50%;
        width: 50px;
        z-index: 99;
        text-align: center;
        display: none;
        opacity: 0;
        i{
          display: inline-block;
          font-size: 16px;
          color: #FFFFFF;
          padding: 17px 0px;
        }
        &:hover{
          cursor: pointer;
        }
      }
      &:hover .tzPortfolio_pauses{
        opacity: 1;
      }
      .bg-video{
        height: 100%;
        left: 0;
        position: absolute;
        top: 0;
        width: 100%;
        z-index: 1;
      }
      .videoID{
        width: 100%;
        height: auto;
        overflow: hidden;
        position: absolute;
        top: 0;
        left: 0;
      }
    }
    .tzPortfolio_Single_content{
      padding:54px 0 0;
      h3{
        margin:0px 0px 22px;
        padding:0px;
        font-family: 'Montserrat',sans-serif;
        font-size: 20px;
        font-weight: 400;
        color: #222222;
        line-height: 27px;
      }
      .single-content{
        p{
          font-family: 'Ubuntu',sans-serif;
          font-size: 14px;
          font-weight: 300;
          line-height: 25px;
          color: #666666;
        }
      }
      table{
        width: 100%;
        tbody{
          tr{
            border-bottom: 1px solid #ececec;
            td{
              padding:13px 0px;
              margin:0px;
              font-family: 'Ubuntu',sans-serif;
              font-weight: 300;
              font-size: 14px;
              line-height: 24px;
              color:#666666;
              width: 55px;
              a{
                color: #666666;
                text-decoration: none;
                &:hover{
                  color: @color-theme;
                }
              }
            }
          }
        }
      }
    }
  }
  .tzPortfolio_related{
    margin: 60px 0 0;
    h5.related-title{
      font-family: 'Montserrat',sans-serif;
      font-size: 20px;
      font-weight: 400;
      line-height: 24px;
      color: #222222;
      text-transform: uppercase;
      margin:0 0 25px;
      padding:0px;
    }
    .tzPortfolio_relateBox{
      margin:0px -15px;
      .tzPortfolio_relateSlider{
        .related-item{
          position: relative;
          overflow: hidden;
          margin:0px 15px;
          .tz-image-item{
            height: 270px;
            position: relative;
            overflow: hidden;
            img{
              position: relative;
              max-width: none;
              .transform(scale(1) rotate(0));
            }
          }
          &:hover .tz-image-item{
            img{
              .transition(all 2s ease-in-out 0s);
              .transform(scale(1.2) rotate(-4deg));
            }
          }
          .tzRelated_hover1{
            position: absolute;
            top:0px;
            left: 0px;
            width: 100%;
            height: 100%;
            padding:20px;
            .transform(scale(1.05));
            .transition(all 0.3s ease-in-out 0s);
            visibility: hidden;
            opacity: 0;
            .tzRelated_table{
              display: table;
              width: 100%;
              height: 100%;
              background: rgba(255,255,255,0.95);
              .tzRelated_table_cell{
                display: table-cell;
                vertical-align:middle;
                h3{
                  margin:0 0 16px;
                  padding:0 50px 19px;
                  text-align: center;
                  position: relative;
                  a{
                    font-family: 'Montserrat',sans-serif;
                    font-size: 14px;
                    font-weight: 700;
                    color: #222222;
                    line-height: 24px;
                    text-decoration: none;
                    text-transform: uppercase;
                  }
                  &:after{
                    content: '';
                    display: block;
                    width: 50px;
                    height: 1px;
                    background: #222222;
                    position: absolute;
                    bottom:0px;
                    left: 50%;
                    margin-left: -25px;
                  }
                }
                span.tzcat{
                  margin:0px;
                  padding:0 50px;
                  display: block;
                  text-align: center;
                  a{
                    font-family: 'Ubuntu',sans-serif;
                    font-size: 14px;
                    font-weight: 300;
                    font-style: italic;
                    color: #666666;
                    line-height: 24px;
                    text-decoration: none;
                    display: inline-block;
                  }
                }
              }
            }
          }

          &:hover .tzRelated_hover1{
            .transform(scale(1));
            visibility: visible;
            opacity: 1;
          }
          .tzRelated_hover2{
            position: absolute;
            top:0px;
            left: 0px;
            width: 100%;
            height: 100%;
            .tzOverlayImage{
              //              position: absolute;
              //              top:0px;
              //              left: 0px;
              width: 100%;
              height: 100%;
              background: rgba(0,0,0,0.2);
              .transform(scale(1.1));
              visibility: hidden;
              opacity: 0;
              .transition(all 0.3s ease-in-out 0s);
            }
            a{
              display: block;
              width: 40px;
              height: 40px;
              position: absolute;
              top:50%;
              margin-top: -20px;
              background: #FFFFFF;
              text-align: center;
              .transition(all 0.3s ease-in-out 0s);
              .border-radius(50%);
              visibility: hidden;
              opacity: 0;
              &.tz-ViewImage{
                right: 50%;
                margin-right: 45px;
                .transition(all 0.3s ease-in-out 0s);
              }
              &.tz-ViewDetail{
                left: 50%;
                margin-left: 45px;
                .transition(all 0.3s ease-in-out 0s);
              }
              i{
                color: #222222;
                font-size: 14px;
                padding: 13px 0px;
                .transition(all 0.3s ease-in-out 0s);
              }
              &:hover{
                background: @color-theme;
                i{
                  color: #FFFFFF;
                }
              }
            }
          }
          &:hover .tzRelated_hover2{
            .tzOverlayImage{
              background: rgba(0,0,0,0.5);
              .transform(scale(1));
              visibility: visible;
              opacity: 1;
            }
            a{
              visibility: visible;
              opacity: 1;
              &.tz-ViewImage{
                margin-right: 5px;
                .transform(rotate(360deg));
              }
              &.tz-ViewDetail{
                margin-left: 5px;
                .transform(rotate(-360deg));
              }
            }
          }
          .tzRelated_hover3{
            position: absolute;
            bottom:-100%;
            left: 0;
            background: rgba(251,192,45,0.9);
            width: 100%;
            padding:16px 30px 17px;
            .transition(all 0.3s ease-in-out 0s);
            h3{
              margin:0 0 3px;
              padding:0px;
              a{
                font-family: 'Montserrat',sans-serif;
                font-size: 14px;
                font-weight: 700;
                line-height: 24px;
                color: #ffffff;
                text-transform: uppercase;
                text-decoration: none;
              }
            }
            span.tzcat{
              a{
                font-family: 'Ubuntu',sans-serif;
                font-weight: 300;
                font-size: 14px;
                font-style: italic;
                line-height: 24px;
                color: #FFFFFF;
                text-decoration: none;
              }
            }
          }
          &:hover .tzRelated_hover3{
            bottom:0;
          }
        }
      }
    }
  }
  .tzComments{
    margin: 65px 0px 0px;
    .comments-area{
      .tzCommentContent{
        h2.comments-title{
          font-family: 'Montserrat',sans-serif;
          font-size: 20px;
          font-weight: 400;
          line-height: 24px;
          color: #222222;
          text-transform: uppercase;
          margin:0 0 25px;
          padding:0px;
        }
        ol.comment-list{
          margin:0px 0px 55px;
          padding:0px;
          position: relative;
          li{
            list-style: none;
            article{
              padding:0px 0px 30px;
              border-bottom:1px solid #ededed;
              margin:0px 0px 30px;
              .comment-author{
                display: block;
                float: left;
                .border-radius(50%);
                overflow: hidden;
              }
              .comment-content{
                margin-left: 120px;
                cite{
                  float: left;
                  font-family: 'Montserrat',sans-serif;
                  font-size: 14px;
                  font-weight: 400;
                  line-height: 22px;
                  color: #222222;
                  text-decoration: none;
                  text-transform: uppercase;
                  margin-bottom: 13px;
                }
                .tz-commentInfo{
                  margin-bottom: 13px;
                  .comments-datetime{
                    text-decoration: none;
                    time{
                      font-family: 'Ubuntu',sans-serif;
                      font-weight: 300;
                      font-size: 14px;
                      font-style: italic;
                      line-height: 22px;
                      color: #b5b5b5;
                    }
                  }
                  .comment-edit-link{
                    font-family: 'Ubuntu',sans-serif;
                    font-weight: 300;
                    font-size: 14px;
                    line-height: 22px;
                    color: @color-theme;
                    text-decoration: none;
                    margin-left: 20px;
                    i{
                      font-size: 13px;
                      margin-right: 5px;
                    }
                  }
                  .comment-reply-link{
                    font-family: 'Ubuntu',sans-serif;
                    font-weight: 300;
                    font-size: 14px;
                    line-height: 22px;
                    color: @color-theme;
                    text-decoration: none;
                    margin-left: 20px;
                    i{
                      font-size: 13px;
                      margin-right: 5px;
                    }
                  }
                }
              }
              p{
                font-family: 'Ubuntu',sans-serif;
                font-weight: 300;
                font-size: 14px;
                line-height: 24px;
                color: #666666;
              }
            }
            ol.children{
              padding-left: 120px;
            }
          }
        }
      }
      .tzCommentForm{
        .comment-respond{
          h3.comment-reply-title{
            font-family: 'Montserrat',sans-serif;
            font-size: 20px;
            font-weight: 400;
            line-height: 24px;
            color: #222222;
            text-transform: uppercase;
            margin:0px;
            padding:0px;
          }
          form.comment-form{
            p{
              font-family: 'Ubuntu',sans-serif;
              font-weight: 300;
              font-size: 14px;
              line-height: 24px;
              color: #666666;
              margin: 0 0 23px;
              &.comment-form-comment{
                margin:0px;
              }
              label {
                display: none;
              }
              a{
                color: @color-theme;
                text-decoration: none;
              }
              textarea{
                width: 100%;
                height: 95px;
                border-width: 1px;
                border-color: #ededed;
                border-style: solid;
                .border-radius(2px);
                font-family: 'Ubuntu',sans-serif;
                font-weight: 300;
                line-height: 24px;
                font-size: 14px;
                color:#b5b5b5;
                padding:10px 20px;
              }
              &.form-submit{
                margin:17px 0px 0px;
                input.submit{
                  background: @color-theme;
                  border:none;
                  .border-radius(2px);
                  font-family: 'Montserrat',sans-serif;
                  font-size: 14px;
                  font-weight: 700;
                  color: #FFFFFF;
                  line-height: 22px;
                  text-transform: uppercase;
                  padding: 11px 25px;

                }
              }
            }
            .tzCommentForm_Top{
              margin-bottom: 20px;
              p{
                margin:0px;
                input{
                  width: 100%;
                  border-width: 1px;
                  border-color: #ededed;
                  border-style: solid;
                  .border-radius(2px);
                  font-family: 'Ubuntu',sans-serif;
                  font-weight: 300;
                  line-height: 24px;
                  font-size: 14px;
                  color:#b5b5b5;
                  padding:10px 20px;
                  margin:0px;
                }
              }
            }
          }
        }
      }
    }
  }
}