/* 11.3 List */

.tz-plazart-list{
  margin:0px;
  padding:0px;
  li{
    list-style: none;
    border-bottom:1px dashed #dbd9d9;
    font-family:'Ubuntu',sans-serif;
    font-weight:300;
    font-size:14px;
    color:#444444;
    padding:15px 0px;
    i{
      margin-right: 12px;
      color:@color-theme;
    }
  }
  &.tzList-no_underlined{
    li{
      border:none;
      padding:8px 0px;
    }
  }
}

