/* 11.6 Title */
.tzElement-title{
  text-align: center;
  .tzTitle{
    position: relative;
    font-family: 'Montserrat',sans-serif;
    font-weight: 400;
    font-size: 65px;
    color: rgba(34,34,34,0.08);
    text-transform: uppercase;
    display: block;
    line-height: 70px;
  }
  &.tz-title-type-2{
    .tzTitle{
      font-family: 'Raleway', sans-serif;
      font-size: 35px;
      color: #222222;
      font-weight: 800;
      line-height: 45px;
      margin-bottom: 31px;
      &:after{
        content: '';
        display: block;
        width: 70px;
        height: 4px;
        position: absolute;
        bottom:-15px;
        left: 50%;
        margin-left: -35px;
        background: @color-theme;
      }
    }
  }
  .tzSubTitle{
    font-family: 'Montserrat',sans-serif;
    font-weight: 700;
    font-size: 21px;
    color: #222222;
    text-transform: uppercase;
    display: block;
    line-height: 22px;
    margin: 0 0 17px;
    padding: 0;
  }
  .tzDescription{
    padding:0 27%;
  }
  &.tz-title-type-2{
    .tzDescription{
      color: #666666;
      font-family: "Droid Serif",serif;
      font-size: 14px;
      font-style: italic;
      line-height: 24px;
    }
  }
}
.tzShortcode-title{
  font-family: 'Ubuntu',sans-serif;
  font-size: 20px;
  font-weight: 500;
  color: #222222;
}