/* 11.11 Our Team */
@keyframes tz_scale_rotate{
  from {
    .transform(scale(2) rotateY(0));
  }
  to {
    .transform(scale(2) rotateY(360deg));
  }
}
.tzElement_Ourteam{
  &.tzOurteam_type1{
    text-align: center;
    .tzOurteam_imageContainer{
      position: relative;
      padding:0px 20px;
      .tzOurteam_ImageBox{
        position: relative;
        border-width:0px 0px 1px;
        border-style: solid;
        border-color: #ececec;
        .tzOurteam_shapeLeft{
          position: absolute;
          bottom: -2px;
          height: 3px;
          left: -6px;
          width: 3px;
          background: @color-theme;
          .border-radius(50%);
          &:after{
            position: absolute;
            top:-3px;
            left: -3px;
            content: '';
            display: block;
            width: 9px;
            height: 9px;
            border: 1px solid @color-theme;
            .transform(scale(1.1));
            .border-radius(50%);
          }
        }

        .tzOurteam_shapeRight{
          position: absolute;
          bottom: -2px;
          height: 3px;
          right: -6px;
          width: 3px;
          background: @color-theme;
          .border-radius(50%);
          &:after{
            position: absolute;
            top:-3px;
            left: -3px;
            content: '';
            display: block;
            width: 9px;
            height: 9px;
            border: 1px solid @color-theme;
            .transform(scale(1.1));
            .border-radius(50%);
          }
        }

        .tzOurteam_Image{
          width:230px;
          height: 230px;
          position: relative;
          overflow: hidden;
          border-width: 1px 1px 0px;
          border-style: solid;
          border-color: #ececec;
          margin:auto;
          padding:0px;
          img{
            position: relative;
            width: 100%;
            height: auto;
          }
        }
      }
    }
    .tzOurteam_Info{
      padding:28px 25px 17px;
      .tzOurteam_Name{
        font-family: 'Montserrat',sans-serif;
        font-size: 14px;
        font-weight: 700;
        color: #222222;
        text-transform: uppercase;
        margin:0 0 12px;
        padding:0px;
      }
      .tzOurteam_Job{
        font-family: 'Ubuntu',sans-serif;
        font-weight: 300;
        font-style: italic;
        font-size:14px;
        color: #666666;
        margin:0 0 19px;
        padding:0;
        display: block;
      }
    }
    .tzOurteam_Social{
      a.tzSocial_Item{
        display: inline-block;
        width: 40px;
        height: 40px;
        border:1px solid #dadada;
        text-align: center;
        padding:9px 0px;
        margin:0px 5px;
        .border-radius(50%);
        .transition(all 0.3s ease-in-out 0s);
        i{
          font-size: 15px;
          color: #9e9e9e;
          .transition(all 0.3s ease-in-out 0s);
        }
        &:hover{
          border:1px solid @color-theme;
          background: @color-theme;
          i{
            color: #ffffff;
          }
        }
      }
    }
  }

  &.tzOurteam_type2{
    .tzOurteam_Image{
      position: relative;
      overflow: hidden;
      img{
        width: 100%;
        height: auto;
      }
      .tzOurteam_Overlay{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(251,192,45,0);
        visibility: hidden;
        opacity: 0;
        .transition(all 0.3s ease-in-out 0s);
      }

      .tzOurteam_Social{
        position: absolute;
        top:0px;
        left: 0px;
        width: 100%;
        height: 100%;
        .transition(all 0.3s ease-in-out 0.1s);
        .transform(scale(2));
        visibility: hidden;
        opacity: 0;

        .tzOurteam_Social_table{
          display: table;
          width: 100%;
          height: 100%;
          .tzOurteam_Social_table_cell{
            display: table-cell;
            vertical-align: middle;
            text-align: center;
            a.tzSocial_Item{
              display: inline-block;
              margin: 0 12px;
              i{
                font-size: 18px;
                color: #FFFFFF;
              }
              &:hover{
                i{
                  animation-name: tz_scale_rotate;
                  animation-duration: 1.5s;
                  animation-delay: 0s;
                  animation-timing-function: linear;
                  animation-iteration-count: infinite;
                }
              }
            }
          }
        }
      }

      &:hover{
        .tzOurteam_Overlay{
          background: rgba(251,192,45,0.5);
          visibility: visible;
          opacity: 1;
        }
        .tzOurteam_Social{
          .transform(scale(1));
          visibility: visible;
          opacity: 1;
        }
      }
    }
    .tzOurteam_Info{
      background: #FFFFFF;
      border-width: 0px 1px 1px;
      border-style: solid;
      border-color: #f3f3f3;
      text-align: center;
      padding:21px;
      .tzOurteam_Name{
        font-family: 'Ubuntu',sans-serif;
        font-size: 15px;
        font-weight: 700;
        color: #222222;
        margin:0px 0px 10px;
        padding:0px;
        text-transform: uppercase;
      }
      .tzOurteam_Job{
        font-family: 'Ubuntu',sans-serif;
        font-size: 14px;
        font-weight: 300;
        font-style: italic;
        color: #666666;
      }
    }
  }
}