/**
 * 3.0 Header
 * -----------------------------------------------------------------------------
 */

.tz-header{
  z-index: 99999;
  /* Header Type 1 */
  .tz-headerTop{
    background: @color-theme;
    &:after{
      content: '';
      clear: both;
      display: block;
    }
    .tz-headerLeft{
      .tzheader_support{
        &:after{
          content: '';
          clear: both;
          display: block;
        }
        .tzheader_wpml{
          float: left;
          #lang_sel{
            ul{
              margin:0px;
              padding:0px;
              li{
                list-style: none;
                position: relative;
                a{
                  display:block;
                  font-family: 'Cinzel', serif;
                  font-weight: 300;
                  font-size: 13px;
                  line-height: 13px;
                  color: #ffffff;
                  text-decoration: none;
                  padding:16px 20px 16px 0px;
                  position: relative;
                  i{
                    font-size: 14px;
                    padding-right: 10px;
                  }
                  &:after{
                    content: '\f107';
                    font-family: FontAwesome;
                    color: #ffffff;
                    position: absolute;
                    top:18px;
                    right: 0;
                  }
                }
                ul{
                  position: absolute;
                  top:120%;
                  left: 0;
                  width: 250px;
                  border-top: 3px solid #222222;
                  background: #fff;
                  visibility: hidden;
                  opacity: 0;
                  .box-shadow(0 1px 6px -4px #000000);
                  .transition(all 0.3s ease-in-out 0s);
                  z-index: 99;
                  li{
                    a{
                      border-bottom: 1px solid #dcdcdc;
                      padding:15px 30px;
                      color:#666666;
                      &:after{
                        content: none;
                      }
                      img{
                        margin-right: 15px;
                      }
                      &:hover{
                        background: #ededed;
                        color: #222222;
                      }
                    }
                  }
                }
                &:hover{
                  ul{
                    top:100%;
                    visibility: visible;
                    opacity: 1;
                  }
                }
              }
            }
          }
        }
        .tzheader_line{
          float: left;
          font-size: 14px;
          color: rgba(255,255,255,0.3);
          padding:11px 15px;
        }
        .tzheader_site{
          float: left;
          font-family: 'Cinzel', serif;
          font-weight: 300;
          font-size: 13px;
          line-height: 13px;
          color: #ffffff;
          text-decoration: none;
          padding:16px 0px;
          i{
            font-size: 14px;
            padding-right: 10px;
          }
        }
      }
    }

    .tz-headerRight{
      ul.tzheader_social{
        margin:0px;
        padding:0px;
        &:after{
          content: '';
          clear: both;
          display: block;
        }
        li{
          list-style: none;
          float: left;
          padding:12px 0px 10px 26px;
          &:last-child{
            border: none;
          }
          a{
            i{
              font-size: 15px;
              color: #FFFFFF;
              .transition(all 0.3s ease-in-out 0s);
            }
            &:hover i{
              color: #222222;
            }
          }
        }
      }
    }
  }
  .tz-headerBottom{
    position: relative;
    .tz_icon_menu{
      display: none;
    }
    a.tz_logo{
      font-family: 'Montserrat', sans-serif;
      font-size: 40px;
      font-weight: 700;
      color: #222222;
      text-decoration: none;
      padding:23px 0px 21px;
    }
    nav{
      ul.tz-nav{
        li{
          position: relative;
          a{
            font-family: 'Montserrat', sans-serif;
            font-size: 13px;
            font-weight: 400;
            color: #222222;
            text-decoration: none;
            text-transform: uppercase;
            padding:44px 27px 40px;
            margin:0px;
          }
          &.current-menu-item a{
              color: @color-theme;
          }
          ul.non_mega_menu{
            position: absolute;
            top: 120%;
            left: 0px;
            margin:0px;
            padding:0px;
            background: #FFFFFF;
            border-top: 3px solid #222222;
            width: 270px;
            visibility: hidden;
            opacity: 0;
            z-index: 9999;
            .box-shadow(0 1px 6px -4px #000000);
            .transition(all 0.3s ease-in-out 0s);
            li{
              list-style: none;
              position: relative;
              border-bottom: 1px solid #ededed;
              .transition(all 0.3s ease-in-out 0s);
              &:last-child{
                border: none;
              }
              &.menu-item-has-children:after{
                content: "\f0da";
                font-family: FontAwesome;
                font-size: 15px;
                content: #222222;
                position: absolute;
                top:14px;
                right: 20px;
              }
              &:hover{
                background: #ededed;
                a{
                  color: #222222;
                }
              }
              &.current-menu-item{
                background: #ededed;
                a{
                  color: #222222;
                }
              }
              a{
                display: block;
                margin:0px;
                padding: 14px 29px 16px;
                font-family: 'Cinzel', serif;
                font-weight: 300;
                font-size: 14px;
                line-height: 24px;
                text-decoration: none;
                text-transform: none;
                color: #666666;
                .transition(all 0.3s ease-in-out 0s);
              }
              ul.sub-menu{
                position: absolute;
                top:0px;
                left: 120%;
                margin:0px;
                padding:0px;
                background: #FFFFFF;
                border-top: 3px solid #222222;
                width: 270px;
                opacity: 0;
                visibility: hidden;
                .box-shadow(0 1px 6px -4px #000000);
                .transition(all 0.3s ease-in-out 0s);
              }
              &:hover ul.sub-menu{
                left: 100%;
                opacity: 1;
                visibility: visible;
              }
            }
          }
          &:hover ul.non_mega_menu{
            top:100%;
            opacity: 1;
            visibility: visible;
          }
          .themeple_custom_menu_mega_menu{
            &.themeple_mega1{
              width: 220px!important;
            }
            &.themeple_mega2{
              width: 440px!important;
              >ul{
                >li{
                  width: 205px!important;
                }
              }
            }
            &.themeple_mega3{
              width: 600px!important;
              >ul{
                >li{
                  width: 190px!important;
                }
              }
            }
            &.themeple_mega4{
              width: 800px!important;
              >ul{
                >li{
                  width: 192px!important;
                }
              }
            }
            &.themeple_mega5{
              width: 930px!important;
              >ul{
                >li{
                  width: 180px!important;
                }
              }
            }
            position: absolute;
            top:120%;
            left: 0px;
            margin:0px;
            padding:0px;
            background: #fff;
            border-top: 3px solid #222222;
            visibility: hidden;
            opacity: 0;
            z-index: 9999;
            .box-shadow(0 1px 6px -4px #000000);
            .transition(all 0.3s ease-in-out 0s);
            ul.sub-menu{
              margin:0px;
              padding:0px;
              &:after{
                content: '';
                clear: both;
                display: block;
              }
              >li{
                list-style: none;
                float: left;
                width: 100%;
                >a{
                  display: block;
                  margin:0px;
                  padding: 14px 0px 16px;
                  font-family: 'Cinzel', serif;
                  font-weight: 300;
                  font-size: 14px;
                  line-height: 24px;
                  text-decoration: none;
                  text-transform: none;
                  color: #666666;
                  border-bottom: 1px solid #ededed;
                  .transition(all 0.3s ease-in-out 0s);
                  &:hover{
                    color: @color-theme;
                  }
                }
                &.current-menu-item{
                  >a{
                    color: @color-theme;
                  }
                }
              }
            }
            >ul.sub-menu{
              padding:0px 15px;
              >li{
                padding:0px 15px;
              }
            }
          }
          &:hover .themeple_custom_menu_mega_menu{
            top:100%;
            opacity: 1;
            visibility: visible;
          }
        }
      }

      /* Max Mega Menu */
      #mega-menu-wrap-primary{
        #mega-menu-primary{
          > li.mega-menu-item{
            > a{
              font-family: "Montserrat",sans-serif;
            }
            > ul.mega-sub-menu{
              top:120%;
              visibility: hidden;
              opacity: 0;
              display: block!important;
              .transition(all 0.3s ease-in-out 0s);
              > li.mega-menu-item{
                &.mega-current-menu-item{
                  > a{
                    color: @color-theme;
                  }
                }
                &.mega-menu-clear{
                  clear: none;
                }
                h4{
                  font-family: "Montserrat",sans-serif;
                  font-size: 14px;
                  font-weight: 400;
                  color:#222222;
                  line-height: 24px;
                  padding:36px 0px 26px;
                  border-bottom: 1px solid #ededed;
                }
                ul{
                  li{
                    a{
                      display: block;
                      margin:0px;
                      font-family: 'Cinzel', serif;
                      font-weight: 300;
                      font-size: 14px;
                      text-decoration: none;
                      text-transform: none;
                      color: #666666;
                      border-bottom: 1px solid #ededed;
                      .transition(all 0.3s ease-in-out 0s);
                      &:hover{
                        color: @color-theme;
                      }
                    }
                  }
                }
                &.woocommerce{
                  ul{
                    li{
                      a{
                        line-height: 24px;
                      }
                    }
                  }
                }
                .product-categories{
                  li{
                    a{
                      padding-top: 14px;
                      padding-bottom: 16px;
                    }
                  }
                }
                .product_list_widget{
                  li{
                    margin:0px 0px 21px;
                    &:after{
                      content: '';
                      display: block;
                      clear: both;
                    }
                    a{
                      img{
                        float: left;
                        width: 80px;
                        height: auto;
                        margin-right: 20px;
                        margin-bottom: 20px;
                      }
                      span.product-title{
                        font-family: 'Ubuntu',sans-serif;
                        font-weight: 400;
                        font-size: 14px;
                        color: #222222;
                        display: block;
                        margin-bottom: 10px;
                      }
                    }
                    .star-rating{
                      font-family: star !important;
                      font-size: 1em !important;
                      height: 1em !important;
                      line-height: 1 !important;
                      overflow: hidden !important;
                      position: relative !important;
                      width: 5.4em !important;
                      margin-bottom: 15px;
                      &:before{
                        display: block !important;
                      }
                      span{
                        &:before{
                          color: @color-theme;
                        }
                      }
                    }
                    span.amount{
                      font-family: "Montserrat",sans-serif;
                      font-size: 13px;
                      font-weight: 400;
                      color:#222222;
                      line-height: 24px;
                    }
                    ins{
                      text-decoration: none;
                    }
                  }
                }
                li.mega-menu-item{
                  > a{
                    font-family: "Ubuntu",sans-serif;
                    border-color: #ededed;
                    border-style: solid;
                    border-width: 0 0 1px;
                  }
                  &.mega-current-menu-item{
                    > a{
                      color: @color-theme;
                    }
                  }
                }
                &.widget_top_rated_products{
                  h4{
                    border:none;
                  }
                  ul.product_list_widget{
                    li{
                      a{
                        padding:0;
                        border:none;
                      }
                    }
                  }
                }
                &.widget_products{
                  h4{
                    border:none;
                  }
                  ul.product_list_widget{
                    li{
                      a{
                        padding:0;
                        border:none;
                      }
                    }
                  }
                }
              }
            }
            &.mega-menu-megamenu{
              > ul.mega-sub-menu{
                > li.mega-menu-item{
                  > a{
                    font-family: "Montserrat",sans-serif;
                  }
                }
              }
            }
            &:hover{
              > ul.mega-sub-menu{
                top:100%;
                visibility: visible;
                opacity: 1;
              }
            }

            &.mega-current-menu-item{
              > a{
                color:@color-theme;
                background: none;
              }
            }
            &.mega-current-menu-ancestor{
              > a{
                color:@color-theme;
                background: none;
              }
            }
          }

          > li.mega-menu-flyout{
            ul.mega-sub-menu{
              li.mega-menu-item{
                a{
                  border-color: #ededed;
                  border-style: solid;
                  border-width: 0 0 1px;
                }
                ul.mega-sub-menu{
                  left: 120%;
                  visibility: hidden;
                  opacity: 0;
                  .transition(all 0.3s ease-in-out 0s);
                }
                &:hover{
                  ul.mega-sub-menu{
                    left: 100%;
                    visibility: visible;
                    opacity: 1;
                  }
                }
              }
            }
          }
        }
      }
      .mega-menu-wrap{
        ul.mega-menu{
          li{
            a{
              font-family: "Montserrat",sans-serif;
            }
          }
        }
      }
    }
    .tz-header-cart{
      position: relative;
      span{
        font-size: 17px;
        color: #bebebe;
        display: inline-block;
        padding:46px 0 41px 22px;
        .transition(all 0.3s ease-in-out 0s);
        &:hover{
          color: @color-theme;
        }
      }
      .widget_shopping_cart{
        position: absolute;
        top:120%;
        right: 0px;
        width: 430px;
        padding:30px;
        border-top: 3px solid #222222;
        visibility: hidden;
        opacity: 0;
        z-index:9999;
        background: #ffffff;
        .box-shadow(0 0 5px 2px rgba(0, 0, 0, 0.1));
        .transition(all 0.3s ease-in-out 0s);
        .widget_shopping_cart_content{
          ul.cart_list{
            padding: 0;
            margin:0;
            li{
              list-style: none;
              position: relative;
              padding:0px 0px 20px;
              margin:0px 0px 20px;
              border-bottom: 1px solid #ededed;
              .tzMiniCart_image{
                float: left;
                a{
                  display: inline-block;
                  img{
                    float: none;
                    width: 80px;
                    height: auto;
                    margin:0px;
                  }
                }
              }

              .tzMiniCart_info{
                margin-left: 100px;
                padding-right: 50px;
                a{
                  font-family: 'Cinzel', serif;
                  font-weight: 400;
                  font-size: 14px;
                  color: #222222;
                  line-height: 24px;
                  text-decoration: none;
                  display: block;
                }
                span.quantity{
                  padding:0px;
                  margin:0px;
                  font-family: 'Montserrat', sans-serif;
                  font-size: 14px;
                  font-weight: 400;
                  color: #222222;
                  span.amount{
                    padding:0px;
                    margin:0px;
                    font-family: 'Montserrat', sans-serif;
                    font-size: 14px;
                    font-weight: 400;
                    color: @color-theme;
                  }
                }
              }

              a.remove{
                position: absolute;
                top:10px;
                right: 0;
                left: auto;
                font-size: 20px;
                color: #222222 !important;
                &:hover{
                  background: @color-theme;
                  color: #ffffff !important;
                }
              }

              &.empty{
                font-family: 'Cinzel', serif;
                font-weight: 400;
                font-size: 14px;
                color: #222222;
                line-height: 24px;
                margin:0px;
                padding:0px;
                border:none;
              }
            }
          }

          p.total{
            float: right;
            margin:0px;
            padding:3px 0px 0px;
            border:none;
            strong{
              font-family: 'Cinzel', serif;
              font-weight: 300;
              font-size: 14px;
              color: #151515;
            }
            span.amount{
              padding:0;
              font-family: 'Cinzel', serif;
              font-weight: 300;
              font-size: 14px;
              color: @color-theme;
            }
          }
          p.buttons{
            float: left;
            margin:0px;
            a{
              font-family: 'Cinzel', serif;
              font-weight: 300;
              font-size: 14px;
              color: #ffffff;
              padding: 8px 20px;
              margin-right: 7px;
              .border-radius(2px);
              background: #222222;
              .transition(all 0.3s ease-in-out 0s);
              text-align: center;
              &:hover{
                background: @color-theme;
              }
            }
          }
        }
      }
      &:hover{
        cursor: pointer;
        .widget_shopping_cart{
          top:100%;
          visibility: visible;
          opacity: 1;
        }
      }
    }
    .tz-header-search{
      position: relative;
      span{
        font-size: 17px;
        color: #bebebe;
        display: inline-block;
        padding:46px 0 41px 8px;
        &.icon_close{
          display: none;
        }
        &:hover{
          color: @color-theme;
          cursor: pointer;
        }
      }
      .tz-header-search-form{
        position: absolute;
        top:120%;
        right: 0px;
        width: 270px;
        border-top: 3px solid #222222;
        visibility: hidden;
        opacity: 0;
        z-index:9999;
        .transition(all 0.3s ease-in-out 0s);
        .searchform{
          label{
            display: none;
          }
          input{
            width: 100%;
            font-family: 'Cinzel', serif;
            font-size: 14px;
            font-weight: 300;
            font-style: italic;
            color: #bebebe;
            padding:15px 21px;
            border:none;
            .border-radius(0);
            position: relative;
            &.searchsubmit{
              background: rgba(0, 0, 0, 0) none repeat scroll 0 0;
              display: block;
              font-size: 0;
              height: 20px;
              padding: 0;
              position: absolute;
              right: 9px;
              top: 14px;
              width: 20px;
              z-index: 9;
            }
          }
          span{
            position: absolute;
            right: 11px;
            top: 17px;
            padding:0;
            font-size: 14px;
            color: @color-theme;
          }
        }
        &.tz-header-search-form-show{
          top:100%;
          visibility: visible;
          opacity: 1;
        }
      }
    }
  }

  /* Header Type 2 */
  &.tz-header-type-2{
    .tz-headerTop{
      background:#FFFFFF;
      .tz-headerTop-box{
        &:after{
          content: '';
          clear: both;
          display: block;
        }

        border-bottom:1px solid #e3e3e3;

        .tz-headerLeft{
          .tzheader_support{
            .tzheader_wpml{
              #lang_sel{
                ul{
                  li{
                    a{
                      color: #333333;
                      &:after{
                        color: #333333;
                      }
                    }
                    ul{
                      border-top: 3px solid #333333;
                      background: #fff;
                      .box-shadow(0 1px 6px -4px #000000);
                      .transition(all 0.3s ease-in-out 0s);
                      z-index: 99;
                      li{
                        a{
                          border-bottom: 1px solid #dcdcdc;
                          padding:15px 30px;
                          color:#333333;
                          &:after{
                            content: none;
                          }
                          img{
                            margin-right: 15px;
                          }
                          &:hover{
                            background: #ededed;
                            color: #222222;
                          }
                        }
                      }
                    }
                    &:hover{
                      ul{
                        top:100%;
                        visibility: visible;
                        opacity: 1;
                      }
                    }
                  }
                }
              }
            }
            .tzheader_line{
              color: #e3e3e3;
            }
            .tzheader_site{
              color: #333333;
            }
          }
        }
        .tz-headerRight{
          ul.tzheader_social{
            li{
              a{
                i{
                  color: #222222;
                  .transition(all 0.3s ease-in-out 0s);
                }
                &:hover i{
                  color: @color-theme;
                }
              }
            }
          }
        }
      }
    }
    .tz-headerBottom {
      nav{
        ul.tz-nav{
          >li{
            >a{
              padding:44px 22px 40px;
              font-size: 15px;
              text-transform: none;
              position: relative;
              &:after{
                content: '';
                display: block;
                width: 100%;
                height: 2px;
                background: @color-theme;
                position: absolute;
                top: 5px;
                left: 0;
                opacity: 0;
                visibility: hidden;
                .transform(scale(0.6));
                .transition(all 0.3s ease-in-out 0s);
              }
              &:hover{
                background: none;
                &:after{
                  opacity: 1;
                  visibility: visible;
                  top: -1px;
                  .transform(scale(1));
                }
              }
            }
            &.current-menu-item{
              >a{
                color: #222222;
                &:after{
                  content: '';
                  display: block;
                  width: 100%;
                  height: 2px;
                  background: @color-theme;
                  position: absolute;
                  top: -1px;
                  left: 0;
                  .transform(scale(1));
                  visibility: visible;
                  opacity: 1;

                }
              }
            }
          }
        }
      }
    }

    .tz-header .tz-headerBottom nav ul.tz-nav li.current-menu-item a {
      color: #fbc02d;
    }
  }

  &.tz-header-type-3{
    position: relative;
    .tz-headerBottom{
      display: block;
      width: 100%;
      position: absolute;
      top:0;
      left: 0;
      padding:22px 0 0;
      nav{
        ul.tz-nav{
          li{
            a{
              color: #FFFFFF;
              padding: 44px 18px 40px;
              &:hover{
                background: none;
                color: @color-theme;
              }
              &:focus{
                background: none;
              }
            }
            &.current-menu-item{
              >a{
                color: @color-theme;
              }
            }
          }
        }
      }
      .tz-header-search{
        span{
          color: #FFFFFF;
        }
      }
      .tz-header-cart{
        span{
          color: #FFFFFF;
        }
      }
    }
  }
}
