/* 11.4 Price Table */

.tzPricing_table{
  &.tzPricing_table_type1{
    .pricing-header{
      border-width: 1px 1px 0px;
      border-style: solid;
      border-color: #f3f3f3;
      text-align: center;
      padding:30px 0px 18px;
      background: #fdfdfd;
      h3.pricinge-title{
      }
      span{
        font-size: 41px;
        font-size: #222222;
      }
    }

    .pricing-cn{
      border-width: 1px 1px 0px;
      border-style: solid;
      border-color: #f3f3f3;
      ul{
        margin:0px;
        padding:22px 0px;
        li{
          list-style: none;
          font-family: 'Ubuntu',sans-serif;
          font-weight: 300;
          font-size: 14px;
          line-height: 24px;
          color: #666666;
          text-align: center;
          padding:9px;
        }
      }
    }

    .pricing-footer{
      border-width: 1px 1px 0px;
      border-style: solid;
      border-color: #f3f3f3;
      background: #fdfdfd;
      text-align: center;
      padding:11px 0px 5px;
      .pricing-footer-box{
        display: inline-block;
        padding-right: 45px;
        .pricing-footer-iner{
          position: relative;
          span{
            font-family: 'Montserrat',sans-serif;
            font-weight: 700;
            color: #222222;
            &.pricing-price{
              font-size: 50px;
            }
            &.pricing-curren{
              font-size: 16px;
              left: 100%;
              position: absolute;
              top: 3px;
            }
            &.pricing-period{
              bottom: 15px;
              font-size: 12px;
              left: 100%;
              position: absolute;
            }
          }
        }
      }
    }

    .pricing-button{
      background: @color-theme;
      .transition(all 0.3s ease-in-out 0s);
      a{
        display: block;
        text-align: center;
        font-family: 'Ubuntu',sans-serif;
        font-weight: 300;
        font-size: 14px;
        color: #ffffff;
        line-height: 24px;
        text-decoration: none;
        padding:13px;
        &:hover{
          cursor: pointer;
        }
      }
    }
    &:hover{
      .pricing-button{
        background: #222222;
      }
    }
  }

  &.tzPricing_table_type2{
    border:1px solid #3a3a3a;
    padding:0px 11px;
    .transition(all 0.3s ease-in-out 0s);
    .pricing-header{
      text-align: center;
      padding:27px 0px 21px;
      h3.pricinge-title{
        font-family: 'Montserrat',sans-serif;
        font-weight: 700;
        font-size: 27px;
        color: #222222;
        padding:0px;
        margin:13px 0px 0px;
        .transition(all 0.3s ease-in-out 0s);
      }
      span{
        font-size: 41px;
        color: #222222;
        .transition(all 0.3s ease-in-out 0s);
      }
    }
    .pricing-footer{
      border-width: 1px 1px 0px;
      border-style: solid;
      border-color: #dfdfdf;
      text-align: center;
      padding:13px 0px 9px;
      .pricing-footer-box{
        display: inline-block;
        padding-right: 45px;
        .pricing-footer-iner{
          position: relative;
          span{
            font-family: 'Montserrat',sans-serif;
            font-weight: 700;
            color: #222222;
            &.pricing-price{
              font-size: 50px;
            }
            &.pricing-curren{
              font-size: 16px;
              left: 100%;
              position: absolute;
              top: 3px;
            }
            &.pricing-period{
              bottom: 15px;
              font-size: 12px;
              left: 100%;
              position: absolute;
            }
          }
        }
      }
    }

    .pricing-cn{
      border-width: 0px 1px 1px;
      border-style: solid;
      border-color: #dfdfdf;
      ul{
        margin:0px;
        padding:0px 20px;
        li{
          border-top: 1px solid #f1f1f1;
          list-style: none;
          font-family: 'Ubuntu',sans-serif;
          font-weight: 300;
          font-size: 14px;
          line-height: 24px;
          color: #666666;
          text-align: center;
          padding:15px 0px;
        }
      }
    }

    .pricing-button{
      text-align: center;
      padding:25px 0px;
      a{
        display: inline-block;
        text-align: center;
        font-family: 'Ubuntu',sans-serif;
        font-weight: 300;
        font-size: 14px;
        color: #ffffff;
        line-height: 24px;
        text-decoration: none;
        padding:8px 28px;
        background: #3a3a3a;
        .border-radius(20px);
        .transition(all 0.3s ease-in-out 0s);
        &:hover{
          cursor: pointer;
        }
      }
    }
    &:hover{
      border:1px solid @color-theme;
      .pricing-header{
        h3.pricinge-title{
          color: @color-theme;
        }
        span{
          font-size: 41px;
          color: @color-theme;
        }
      }
      .pricing-button{
        a{
          background: @color-theme;
        }
      }
    }
  }

}