/* 13.2 Shop Category */
.tzshop-wrap{
  ul.products{
    li.product-category{
      margin:0px;
      width: 25%;
      .tz-shop-subcategory{
        padding:15px;
        .tz-shop-subcategory-inner{
          position: relative;
          overflow: hidden;
          img{
            width: 100%;
            height: auto;
          }

          .tz-subcategory-overlay{
            position: absolute;
            top:0;
            left: 0;
            width: 100%;
            height: 100%;
            background: rgba(0,0,0,0);
            .transition(all 0.3s ease-in-out 0s);
          }

          .tz-subcategory-info{
            position: absolute;
            top:0px;
            left: 0px;
            width: 100%;
            height: 100%;
            opacity: 0;
            visibility: hidden;
            .transform(scale(2));
            .transition(all 0.3s ease-in-out 0s);
            .tz-subcategory-table{
              width: 100%;
              height: 100%;
              display: table;
              .tz-subcategory-table-cell{
                display: table-cell;
                vertical-align: middle;
                text-align: center;
                a{
                  font-family: 'Montserrat',sans-serif;
                  font-size: 30px;
                  font-weight: 700;
                  text-decoration: none;
                  text-transform: uppercase;
                  color: #ffffff;
                  .transition(all 0.3s ease-in-out 0s);
                  &:hover{
                    color: @color-theme;
                  }
                }
              }
            }
          }

          &:hover{
            .tz-subcategory-overlay{
              background: rgba(0,0,0,0.5);
            }

            .tz-subcategory-info{
              opacity: 1;
              visibility: visible;
              .transform(scale(1));
            }
          }
        }
      }
    }
  }
}