/* 11.13 View Post */
.tzElement_viewPost{
  .tzViewPost_slider{
      margin:0px -15px;
      .tzViewPost_item{
        padding:0px 15px;
        .tzPost_videoHtml5{
          width: 100%;
          height: 197px;
          overflow: hidden;
          position: relative;
          .tzPost_autoplay{
            background: @color-theme;
            border: 0 none;
            .border-radius(50%);
            height:50px;
            left: 50%;
            margin-left: -25px;
            margin-top: -25px;
            opacity: 1;
            position: absolute;
            top: 50%;
            width: 50px;
            z-index: 99;
            text-align: center;
            i{
              display: inline-block;
              font-size: 16px;
              color: #FFFFFF;
              padding: 17px 0px;
            }
            &:hover{
              cursor: pointer;
            }
          }
          .tzPost_pauses{
            background: @color-theme;
            border: 0 none;
            .border-radius(50%);
            height:50px;
            left: 50%;
            margin-left: -25px;
            margin-top: -25px;
            position: absolute;
            top: 50%;
            width: 50px;
            z-index: 99;
            text-align: center;
            display: none;
            opacity: 0;
            i{
              display: inline-block;
              font-size: 16px;
              color: #FFFFFF;
              padding: 17px 0px;
            }
            &:hover{
              cursor: pointer;
            }
          }
          &:hover .tzPost_pauses{
            opacity: 1;
          }
          .tzPost_bgVideo{
            height: 100%;
            left: 0;
            position: absolute;
            top: 0;
            width: 100%;
            z-index: 1;
          }
          .tzPost_videoID{
            width: 100%;
            height: auto;
            overflow: hidden;
            position: absolute;
            top: 0;
            left: 0;
          }
        }

        .tzPost_video{
          height: 197px;
          iframe{
            width: 100%;
            height: 100%;
            border:none;
          }
        }

        .tzPost_Slider{
          position: relative;
          ul.flex-direction-nav{
            li{
              a{
                width: 30px;
                height: 30px;
                border:1px solid #FFFFFF;
                .border-radius(50%);
                opacity: 1;
                top:auto;
                bottom:30px;
                text-align: center;
                .transition(all 0.3s ease-in-out 0s);
                &.flex-prev{
                  left: 35px;
                }
                &.flex-next{
                  right: 35px;
                }
                &:before{
                  font-size: 10px;
                  font-weight: lighter;
                  color: #FFFFFF;
                  text-shadow: none;
                }
                &:hover{
                  border-color: @color-theme;
                  background: @color-theme;
                }
              }
            }
          }
        }

        .tzPost_info{
          padding:20px 0 0;
          background: #ffffff;
          .tzPost_title{
            margin:0px 0px 12px;
            padding:0px;
            a{
              font-family: 'Montserrat',sans-serif;
              font-size: 15px;
              font-weight: 700;
              color: #222222;
              text-decoration: none;
            }
          }
          .tzPost_infomation{
            display: block;
            margin:0 0 20px;
            a{
              font-family: 'Ubuntu',sans-serif;
              font-size: 13px;
              font-weight: 300;
              color: #c3c3c3;
              text-decoration: none;
              margin-right: 10px;
              &:hover{
                color:@color-theme;
              }
            }
            span{
              font-family: 'Ubuntu',sans-serif;
              font-size: 13px;
              font-weight: 300;
              color: #c3c3c3;
              text-decoration: none;
              margin-right: 10px;
              a{
                margin:0px;
              }
            }
            small{
              font-family: 'Ubuntu',sans-serif;
              font-size: 13px;
              font-weight: 300;
              color: #c3c3c3;
              text-decoration: none;
              margin-right: 10px;
            }
          }
          p{
            margin-bottom: 27px;
          }
          .tzPost_readMore{
            font-family: 'Ubuntu',sans-serif;
            font-size: 14px;
            font-weight: 300;
            color: @color-theme;
            text-decoration: none;
            i{
              font-size: 14px;
              padding-left: 7px;
            }
          }
        }
      }
    }
