  .tzViewPost_Grid{
    margin:-15px;
    .tzPost_Item{
      .tzPost_Box{
        padding:15px;
        .tzPost_Content{
          background: #FFFFFF;
          .transition(all 0.3s ease-in-out 0s);

          .tzPostImage{
            width: 50%;
            float: left;
            position: relative;
            &.tzPostImage_fixHeight{
              position: relative;
              overflow: hidden;
              img{
                max-width: none;
                position: relative;
              }
            }

            .tzViewPost_Date{
              position: absolute;
              top:0px;
              left: 0px;
              z-index: 999;
              .tzViewPost_Text{
                width: 60px;
                height: 55px;
                background: @color-theme;
                padding:8px 5px;
                .tzViewPost_Day{
                  display: block;
                  font-family: 'Montserrat',sans-serif;
                  font-weight: 400;
                  font-size: 20px;
                  line-height: 20px;
                  color: #ffffff;
                  text-align: center;
                  margin-bottom: 4px;
                }
                .tzViewPost_Month{
                  display: block;
                  font-family: 'Montserrat',sans-serif;
                  font-weight: 700;
                  font-size: 11px;
                  line-height: 11px;
                  color: #ffffff;
                  text-align: center;
                  text-transform: uppercase;
                }
              }
              .tzViewPost_Icon{
                width: 60px;
                height: 55px;
                background: #444444;
                text-align: center;
                i{
                  font-size: 22px;
                  color: #fff;
                  padding: 18px;
                }
              }
            }
          }

          &:hover{
            .box-shadow(2px 2px 5px -3px rgba(0, 0, 0, 0.3));
            .tzPostImage{
              &.tzPostImage_fixHeight{
                img{
                  .transition(all 2s ease-in-out 0s);
                  .transform(scale(1.2) rotate(4deg));
                }
              }
            }
          }

          .tzPost_info{
            margin-left: 50%;
            padding:35px 30px;
            .tzPost_infomation{
              margin:0 0 8px;
              a{
                font-family: 'Ubuntu',sans-serif;
                font-size: 13px;
                font-weight: 300;
                font-style: italic;
                color: #b5b5b5;
                text-decoration: none;
              }
              small{
                font-family: 'Ubuntu',sans-serif;
                font-size: 13px;
                font-weight: 300;
                font-style: italic;
                color: #b5b5b5;
                padding:0px 5px;
              }
            }
            .tzPost_title{
              position: relative;
              padding:0 0 18px;
              margin:0 0 13px;
              a{
                font-family: 'Montserrat',sans-serif;
                font-size: 15px;
                font-weight: 700;
                color: #222222;
                text-decoration: none;
              }
              &:after{
                content: '';
                display: block;
                width: 30px;
                height: 1px;
                background: @color-theme;
                position: absolute;
                bottom: 0;
                left: 0;
              }
            }
            .tzPost_readMore{
              font-family: 'Ubuntu',sans-serif;
              font-size: 14px;
              font-weight: 300;
              color: @color-theme;
              text-decoration: none;
              i{
                font-size: 14px;
                padding-left: 7px;
              }
            }
          }
          &:after{
            content: '';
            display: block;
            clear: both;
          }
        }
      }
    }
