/* 13.1 Shop */
.tzshop-wrap {
  padding:80px 0px;
  .grid_pagination_block {
    margin-bottom: 30px;
    padding:0 0 25px;
    border-width: 0 0 1px;
    border-style: solid;
    border-color: #ededed;
    form.woocommerce-ordering {
      float: left;
      margin:0;
      select.orderby {
        appearance:none;
        -moz-appearance:none;
        -webkit-appearance:none;
        -ms-appearance:none;
        -o-appearance:none;
        background:url(images/orderby.png);
        background-repeat: no-repeat;
        background-position: 162px 16px;
        font-family: 'Ubuntu',sans-serif;
        font-size: 14px;
        font-weight: 300;
        color:#666666;
        border:1px solid #dedede;
        .border-radius(1px);
        padding:9px 11px;
      }
    }
    p.woocommerce-result-count {
      float:left;
      font-family: 'Ubuntu',sans-serif;
      font-size: 14px;
      font-weight: 300;
      color:#666666;
      border:1px solid #dedede;
      .border-radius(1px);
      line-height: 20px;
      margin: 0 0 0 20px;
      padding: 9px 15px;
    }
    .tzview-style {
      float: right;
      margin-top: 13px;
      label {
        float: left;
        font-family: 'Ubuntu',sans-serif;
        font-size: 14px;
        font-weight: 300;
        line-height: 14px;
        color: #222222;
        margin-right: 7px;
      }
      .switchToList{
        float:left;
        position: relative;
        width: 30px;
        text-align: center;
        i{
          font-size: 18px;
          color: #222222;
          line-height: 18px;
        }
        span{
          width: 80px;
          height: 25px;
          display: block;
          position: absolute;
          top:-40px;
          left: 50%;
          font-family: 'Ubuntu',sans-serif;
          font-size: 12px;
          font-weight: 400;
          color:#ffffff;
          text-align: center;
          background: @color-theme;
          padding:3px 9px;
          margin-left: -43px;
          .border-radius(1px);
          opacity: 0;
          visibility: hidden;
          &:after{
            border-left: 5px solid rgba(0, 0, 0, 0);
            border-right: 5px solid rgba(0, 0, 0, 0);
            border-top: 5px solid @color-theme;
            content: "";
            left: 50%;
            margin-left: -2px;
            position: absolute;
            top: 100%;
          }
        }
        &:hover{
          cursor: pointer;
          i{
            color:@color-theme;
          }
          span{
            top:-32px;
            opacity: 1;
            visibility: visible;
            .transition(all 0.3s ease-in 0s);
          }
        }
      }
      .switchToGrid{
        float:left;
        position: relative;
        width: 30px;
        text-align: center;
        i{
          font-size: 18px;
          color: #222222;
          line-height: 18px;
        }
        span{
          width: 80px;
          height: 25px;
          display: block;
          position: absolute;
          top:-40px;
          left: 50%;
          font-family: 'Ubuntu',sans-serif;
          font-size: 12px;
          font-weight: 400;
          color:#ffffff;
          text-align: center;
          background: @color-theme;
          padding:3px 9px;
          margin-left: -43px;
          .border-radius(1px);
          opacity: 0;
          visibility: hidden;
          &:after{
            border-left: 5px solid rgba(0, 0, 0, 0);
            border-right: 5px solid rgba(0, 0, 0, 0);
            border-top: 5px solid @color-theme;
            content: "";
            left: 50%;
            margin-left: -2px;
            position: absolute;
            top: 100%;
          }
        }
        &:hover{
          cursor: pointer;
          i{
            color:@color-theme;
          }
          span{
            top:-32px;
            opacity: 1;
            visibility: visible;
            .transition(all 0.3s ease-in 0s);
          }
        }
      }
    }
  }
  .grid_pagination_block:after {
    content:'';
    display: block;
    clear: both;
  }

  .product-grid{
    border-color: #ededed;
    border-style: solid;
    border-width: 0 0 1px;
    padding-bottom: 16px;
    ul.products{
      margin:0px -15px;
      li.tzShop-item{
        margin:0px;
        padding:15px;
        &.tzShop-4column{
          width: 25%;
        }
        &.tzShop-3column{
          width: 33.33333%;
        }
        &.tzShop-2column{
          width: 50%;
        }
        .tzShop-item_inner{
          .tzShop-item_image{
            position: relative;
            overflow: hidden;
            img{
              position: relative;
              max-width: none;
            }
            .tzShop-item_overlay{
              width: 100%;
              height: 100%;
              background: rgba(0,0,0,0);
              position: absolute;
              top:0;
              left: 0;
              .transition(all 0.3s ease-in-out 0s);
            }
            .yith-wcwl-add-to-wishlist{
              position: absolute;
              top:50%;
              margin-top: -20px;
              left: 50%;
              margin-left: -45px;
              width: 40px;
              height: 40px;
              padding:0px;
              background: @color-theme;
              float: left;
              .transform(scale(1.5));
              opacity: 0;
              visibility: hidden;
              .transition(all 0.3s ease-in-out 0.3s);
              .border-radius(50%);
              .yith-wcwl-add-button{
                width: 100%;
                height: 100%;
                a{
                  display: inline-block;
                  width: 100%;
                  height: 100%;
                  text-align: center;
                  padding:9px 0px;
                  i{
                    font-size: 14px;
                    color: #000;
                    margin:0px;
                    padding:0px;
                  }
                  span{
                    position: absolute;
                    top:-60px;
                    left: 50%;
                    margin-left: -53px;
                    width: 106px;
                    padding:3px 0;
                    background: #fff;
                    font-family: 'Ubuntu',sans-serif;
                    font-weight: 400;
                    font-size: 12px;
                    color:#666666;
                    .border-radius(2px);
                    .transition(all 0.3s ease-in-out 0s);
                    opacity: 0;
                    visibility: hidden;
                    &:after{
                      content: '';
                      display: block;
                      border-left: 4px solid rgba(0, 0, 0, 0);
                      border-right: 4px solid rgba(0, 0, 0, 0);
                      border-top: 4px solid #fff;
                      left: 50%;
                      margin-left: -4px;
                      position: absolute;
                      top: 100%;
                      z-index: 9999;
                    }
                  }
                  &:hover{
                    span{
                      top:-44px;
                      opacity: 1;
                      visibility: visible;
                    }
                  }
                }
              }

              .yith-wcwl-wishlistexistsbrowse{
                width: 100%;
                height: 100%;
                a{
                  display: inline-block;
                  width: 100%;
                  height: 100%;
                  text-align: center;
                  padding:9px 0px;
                  i{
                    font-size: 14px;
                    color: #000;
                    margin:0px;
                    padding:0px;
                  }
                  span{
                    position: absolute;
                    top:-60px;
                    left: 50%;
                    margin-left: -53px;
                    width: 106px;
                    padding:3px 0;
                    background: #fff;
                    font-family: 'Ubuntu',sans-serif;
                    font-weight: 400;
                    font-size: 12px;
                    color:#666666;
                    .border-radius(2px);
                    .transition(all 0.3s ease-in-out 0s);
                    opacity: 0;
                    visibility: hidden;
                    &:after{
                      content: '';
                      display: block;
                      border-left: 4px solid rgba(0, 0, 0, 0);
                      border-right: 4px solid rgba(0, 0, 0, 0);
                      border-top: 4px solid #fff;
                      left: 50%;
                      margin-left: -4px;
                      position: absolute;
                      top: 100%;
                      z-index: 9999;
                    }
                  }
                  &:hover{
                    span{
                      top:-44px;
                      opacity: 1;
                      visibility: visible;
                    }
                  }
                }
              }
            }

            .tzShop-item_button{
              position: absolute;
              top:50%;
              margin-top: -20px;
              right: 50%;
              margin-right: -45px;
              width: 40px;
              height: 40px;
              padding:0px;
              background: @color-theme;
              .border-radius(50%);
              float: left;
              .transform(scale(1.5));
              opacity: 0;
              visibility: hidden;
              .transition(all 0.3s ease-in-out 0.3s);
              a{
                display: inline-block;
                width: 100%;
                height: 100%;
                text-align: center;
                padding:13px 0px;
                margin:0px;
                background: none;
                i{
                  font-size: 14px;
                  color: #000;
                  margin:0px;
                  padding:0px;
                }
                span{
                  position: absolute;
                  top:-60px;
                  left: 50%;
                  margin-left: -53px;
                  width: 106px;
                  padding:9px 0;
                  background: #fff;
                  font-family: 'Ubuntu',sans-serif;
                  font-weight: 400;
                  font-size: 12px;
                  color:#666666;
                  .border-radius(2px);
                  .transition(all 0.3s ease-in-out 0s);
                  opacity: 0;
                  visibility: hidden;
                  &:after{
                    content: '';
                    display: block;
                    border-left: 4px solid rgba(0, 0, 0, 0);
                    border-right: 4px solid rgba(0, 0, 0, 0);
                    border-top: 4px solid #fff;
                    left: 50%;
                    margin-left: -4px;
                    position: absolute;
                    top: 100%;
                    z-index: 9999;
                  }
                }
                &:hover{
                  span{
                    top:-44px;
                    opacity: 1;
                    visibility: visible;
                  }
                }
                &.added{
                  &:after{
                   display: none;
                  }
                }
                &.wc-forward{
                  position: absolute;
                  top:55px;
                  left: 50%;
                  margin-left: -53px;
                  width: 106px;
                  height: 30px;
                  padding:3px 0;
                  background: #fff;
                  font-family: 'Ubuntu',sans-serif;
                  font-weight: 400;
                  font-size: 12px;
                  color:#666666;
                  .border-radius(2px);
                  .transition(all 0.3s ease-in-out 0s);
                  &:after{
                    border-bottom: 4px solid #fff;
                    border-left: 4px solid rgba(0, 0, 0, 0);
                    border-right: 4px solid rgba(0, 0, 0, 0);
                    bottom: 100%;
                    content: "";
                    display: block;
                    left: 50%;
                    margin-left: -4px;
                    position: absolute;
                    z-index: 9999;
                  }
                }
              }
            }
          }
          &:hover{
            .tzShop-item_overlay{
              background: rgba(0,0,0,0.5);
            }
            .yith-wcwl-add-to-wishlist{
              .transform(scale(1));
              opacity: 1;
              visibility: visible;
            }
            .tzShop-item_button{
              .transform(scale(1));
              opacity: 1;
              visibility: visible;
            }
          }

          .tzShop-item_info{
            padding:18px 0 13px;
            h3.tzShop-item_title{
              margin:0px;
              padding:0px;
              a{
                font-family: 'Montserrat',sans-serif;
                font-size: 15px;
                font-weight: 400;
                color: #222222;
                line-height: 24px;
              }
            }

            .star-rating{
              margin:12px 0 0;
              &:before{
                color: #b5b5b5;
              }
              span{
                &:before{
                  color: @color-theme;
                }
              }
            }

            .price{
              font-family: 'Montserrat',sans-serif;
              font-size: 15px;
              font-weight: 700;
              color: @color-theme;
              margin:10px 0 0;
              del{
                display: inline-block;
                color: #b5b5b5;
                span.amount{
                  font-family: 'Montserrat',sans-serif;
                  font-size: 15px;
                  font-weight: 400;
                  color: #b5b5b5;
                }
              }

              ins{
                display: inline-block;
                text-decoration: none;
                margin-left: 8px;
                span.amount{
                  font-family: 'Montserrat',sans-serif;
                  font-size: 15px;
                  font-weight: 700;
                  color: @color-theme;
                }
              }
            }

            .tzShop-item_des{
              display: none;
            }
            .tzShop-item_button_list{
              display: none;
            }
          }
        }
      }
    }
  }

  .product-list{
    margin-bottom: 40px;
    ul.products{
      margin:0px -15px;
      li.tzShop-item{
        width: 100%;
        margin:0 0 50px;
        padding:0px 15px;
        .tzShop-item_inner{
          .tzShop-item_image{
            width:270px;
            float: left;
            position: relative;
            overflow: hidden;
            img{
              position: relative;
              max-width: none;
            }
            .tzShop-item_overlay{
              display: none;
            }
            .yith-wcwl-add-to-wishlist{
              display: none;
            }
            .tzShop-item_button{
              display: none;
            }
          }
          .tzShop-item_info{
            margin-left: 300px;
            padding:0px;
            border-bottom: 1px solid #ededed;
            h3.tzShop-item_title{
              margin:0px;
              padding:0px;
              a{
                font-family: 'Montserrat',sans-serif;
                font-size: 15px;
                font-weight: 400;
                color: #222222;
                line-height: 24px;
              }
            }

            .star-rating{
              margin:12px 0 0;
              &:before{
                color: #b5b5b5;
              }
              span{
                &:before{
                  color: @color-theme;
                }
              }
            }

            .price{
              font-family: 'Montserrat',sans-serif;
              font-size: 15px;
              font-weight: 700;
              color: @color-theme;
              margin:10px 0 0;
              del{
                display: inline-block;
                color: #b5b5b5;
                span.amount{
                  font-family: 'Montserrat',sans-serif;
                  font-size: 15px;
                  font-weight: 400;
                  color: #b5b5b5;
                }
              }

              ins{
                display: inline-block;
                text-decoration: none;
                margin-left: 8px;
                span.amount{
                  font-family: 'Montserrat',sans-serif;
                  font-size: 15px;
                  font-weight: 700;
                  color: @color-theme;
                }
              }
            }

            .tzShop-item_des{
              margin:26px 0 0;
              p{
                font-family: 'Ubuntu',sans-serif;
                font-size: 14px;
                font-weight: 300;
                line-height: 24px;
                color:#666666;
              }
            }

            .tzShop-item_button_list{
              margin-top: 49px;
              margin-bottom: 39px;
              .yith-wcwl-add-to-wishlist{
                float: left;
                margin:0px;
                padding:0px;
                .yith-wcwl-add-button{
                  float: left;
                  margin-right: 10px;
                  a{
                    display: inline-block;
                    .border-radius(20px);
                    background: #c7c7c7;
                    font-family: 'Ubuntu',sans-serif;
                    font-size: 14px;
                    font-weight: 400;
                    line-height: 24px;
                    color:#ffffff;
                    padding:8px 21px;
                    .transition(all 0.3s ease-in-out 0s);
                    &:hover{
                      background: @color-theme;
                    }
                  }
                }

                .yith-wcwl-wishlistexistsbrowse{
                  float: left;
                  margin-right: 10px;
                  a{
                    display: inline-block;
                    .border-radius(20px);
                    background: #c7c7c7;
                    font-family: 'Ubuntu',sans-serif;
                    font-size: 14px;
                    font-weight: 400;
                    line-height: 24px;
                    color:#ffffff;
                    padding:8px 21px;
                    .transition(all 0.3s ease-in-out 0s);
                    &:hover{
                      background: @color-theme;
                    }
                    i{
                      margin-right: 10px;
                    }
                  }
                }
              }

              .tzShop-item_button{
                a{
                  display: inline-block;
                  .border-radius(20px);
                  background: #c7c7c7;
                  font-family: 'Ubuntu',sans-serif;
                  font-size: 14px;
                  font-weight: 400;
                  line-height: 24px;
                  color:#ffffff;
                  padding:8px 21px;
                  margin:0px;
                  .transition(all 0.3s ease-in-out 0s);
                  &:hover{
                    background: @color-theme;
                  }
                  i{
                    margin-right: 10px;
                  }
                }
              }
            }
          }

          &:after{
            content: '';
            display: block;
            clear: both;
          }
        }
        &.first{
          clear: none;
        }
        &.tzShopList-2column{
          &:nth-child(2n+1){
            clear: both;
          }
          width: 50%;
          .tzShop-item_info{
            .tzShop-item_button_list{
              .yith-wcwl-add-to-wishlist{
                .yith-wcwl-add-button{
                  a{
                    width: 40px;
                    height: 40px;
                    padding:8px 12px;
                    .transition(all 0.3s ease-in-out 0s);
                    overflow: hidden;
                    span{
                      opacity: 0;
                      visibility: hidden;
                      .transition(all 0.3s ease-in-out 0s);
                    }
                    &:hover{
                      background: @color-theme;
                      width: 150px;
                      span{
                        opacity: 1;
                        visibility: visible;
                      }
                    }
                  }
                }

                .yith-wcwl-wishlistexistsbrowse{
                  a{
                    width: 40px;
                    height: 40px;
                    padding:8px 12px;
                    .transition(all 0.3s ease-in-out 0s);
                    overflow: hidden;
                    span{
                      opacity: 0;
                      visibility: hidden;
                      .transition(all 0.3s ease-in-out 0s);
                    }
                    &:hover{
                      background: @color-theme;
                      width: 165px;
                      span{
                        opacity: 1;
                        visibility: visible;
                      }
                    }
                  }
                }
              }

              .tzShop-item_button{
                a{
                  width: 40px;
                  height: 40px;
                  padding:8px 12px;
                  .transition(all 0.3s ease-in-out 0s);
                  overflow: hidden;
                  text-align: center;
                  span{
                    opacity: 0;
                    visibility: hidden;
                    .transition(all 0.3s ease-in-out 0s);
                  }
                  &:hover{
                    background: @color-theme;
                    width: 165px;
                    span{
                      opacity: 1;
                      visibility: visible;
                    }
                  }
                }
              }
            }
          }
        }
        &:last-child{
          margin:0px;
        }
      }
    }
  }

  .woocommerce-pagination{
    text-align: right !important;
    margin-top: 20px;
    ul.page-numbers{
      margin:0px;
      padding:0px;
      border:none;
      li{
        border: none;
        span{
          font-family: 'Ubuntu',sans-serif;
          font-size: 14px;
          font-weight: 400;
          color: #666666;
          border-width: 1px;
          border-style: solid;
          border-color: #dedede;
          .border-radius(1px);
          margin-left: 10px;
          padding:12px 14px;
          &.current{
            background: @color-theme;
            color:#ffffff;
            border-color: @color-theme;
          }
        }
        a{
          font-family: 'Ubuntu',sans-serif;
          font-size: 14px;
          font-weight: 400;
          color: #666666;
          border:1px solid #dedede;
          .border-radius(1px);
          margin-left: 10px;
          padding:12px 14px;

          &:hover{
            background: @color-theme;
            color:#ffffff;
            border-color: @color-theme;
          }
        }
      }
    }
  }
}