/* 11.7 Counter */
.tzElement_Counter{
  padding:0px 35px;
  .tzElement_counterIcon{
    border-bottom: 1px solid #353535;
    text-align: center;
    padding-bottom:28px;
    margin-bottom: 12px;
    span{
      font-size: 46px;
      color: #FFFFFF;
    }
  }
  .tzElement_count{
    display: block;
    text-align: center;
    em{
      font-family: 'Montserrat',sans-serif;
      font-weight: 700;
      font-size: 50px;
      color: #ffffff;
      font-style: normal;
    }
  }
  p{
    font-family: 'Ubuntu',sans-serif;
    font-size: 14px;
    font-weight: 300;
    color: @color-theme;
    text-transform: uppercase;
    text-align: center;
  }

  &.tz_Counter_type2{
    padding:0;
    .tzElement_counterIcon{
      border:none;
      padding:0;
      margin:0 0 10px;
    }
    .tzElement_count{
      margin-bottom: 7px;
      em{
        font-family: 'Raleway',sans-serif;
        font-weight: 300;
        font-size: 50px;
        color: @color-theme;
      }
    }
    p{
      font-family: 'Raleway',sans-serif;
      font-weight: 300;
      color: #FFFFFF;
      letter-spacing: 3px;
    }
  }
}