.woocommerce{
  &.columns-4{
    ul.product{
      li.product{
        width: 25%;
      }
    }
  }
  &.columns-3{
    ul.product{
      li.product{
        width: 33.33333%;
      }
    }
  }
  &.columns-2{
    ul.product{
      li.product{
        width: 50%;
      }
    }
  }

  ul.products{
    margin:0 -15px;
    padding:0;
    li.product{
      margin:0;
      padding:0 15px;
      width: 25%;
      .tzProduct-item_inner{
        .tzProduct-item_image{
          position: relative;
          overflow: hidden;
          img{
            width: 100%;
            height: auto;
          }
          .yith-wcwl-add-to-wishlist{
            border: 1px solid #222222;
            border-radius: 2px;
            -webkit-border-radius: 2px;
            -moz-border-radius: 2px;
            -ms-border-radius: 2px;
            -o-border-radius: 2px;
            bottom: 50px;
            margin: 0;
            padding: 0;
            position: absolute;
            right: -45px;
            text-align: center;
            opacity: 0;
            visibility: hidden;
            transition: all 0.3s ease-in-out 0.1s;
            -webkit-transition: all 0.3s ease-in-out 0.1s;
            -moz-transition: all 0.3s ease-in-out 0.1s;
            -ms-transition: all 0.3s ease-in-out 0.1s;
            -o-transition: all 0.3s ease-in-out 0.1s;
            a{
              margin:0;
              padding:0;
              display: block;
              width: 40px;
              height: 40px;
              position: relative;
              i{
                font-size: 14px;
                color: #222222;
                padding:13px;
              }
              span{
                left: -150px;
                position: absolute;
                top: 7px;
                font-family: 'Raleway',sans-serif;
                font-size: 13px;
                font-weight: 800;
                color: #222222;
                opacity: 0;
                visibility: hidden;
                transition: all 0.3s ease-in-out 0s;
                -webkit-transition: all 0.3s ease-in-out 0s;
                -moz-transition: all 0.3s ease-in-out 0s;
                -ms-transition: all 0.3s ease-in-out 0s;
                -o-transition: all 0.3s ease-in-out 0s;
              }
              &:hover{
                i{
                  color: @color-theme;
                }
                span{
                  left: -110px;
                  opacity: 1;
                  visibility: visible;
                }
              }
            }
          }

          .tzProduct-item_button{
            border: 1px solid #222222;
            border-radius: 2px;
            -webkit-border-radius: 2px;
            -moz-border-radius: 2px;
            -ms-border-radius: 2px;
            -o-border-radius: 2px;
            bottom: 100px;
            margin: 0;
            padding: 0;
            position: absolute;
            right: -45px;
            text-align: center;
            opacity: 0;
            visibility: hidden;
            transition: all 0.3s ease-in-out 0s;
            -webkit-transition: all 0.3s ease-in-out 0s;
            -moz-transition: all 0.3s ease-in-out 0s;
            -ms-transition: all 0.3s ease-in-out 0s;
            -o-transition: all 0.3s ease-in-out 0s;
            a{
              margin:0;
              padding:0;
              display: block;
              width: 40px;
              height: 40px;
              background: none;
              position: relative;
              i{
                font-size: 14px;
                color: #222222;
                padding:13px;
              }
              span{
                left: -150px;
                position: absolute;
                top: 13px;
                font-family: 'Raleway',sans-serif;
                font-size: 13px;
                font-weight: 800;
                color: #222222;
                opacity: 0;
                visibility: hidden;
                transition: all 0.3s ease-in-out 0s;
                -webkit-transition: all 0.3s ease-in-out 0s;
                -moz-transition: all 0.3s ease-in-out 0s;
                -ms-transition: all 0.3s ease-in-out 0s;
                -o-transition: all 0.3s ease-in-out 0s;
              }
              &:hover{
                i{
                  color: @color-theme;
                }
                span{
                  left: -110px;
                  opacity: 1;
                  visibility: visible;
                }
              }
            }
          }

          .tzProduct-View-detail{
            position: absolute;
            bottom:-50px;
            left: 0;
            display: block;
            width: 100%;
            background: rgba(251,192,45,0.8);
            opacity: 0;
            visibility: hidden;
            .transition(all 0.3s ease-in-out 0.2s);
            a{
              display: block;
              text-align: center;
              font-family: 'Raleway',sans-serif;
              font-size: 13px;
              text-transform: uppercase;
              letter-spacing: 2px;
              padding: 11px 10px 5px;
              color: #ffffff;
            }
          }
          &:hover{
            .yith-wcwl-add-to-wishlist{
              opacity: 1;
              visibility: visible;
              right: 10px;
            }
            .tzProduct-item_button{
              opacity: 1;
              visibility: visible;
              right: 10px;
            }
            .tzProduct-View-detail{
              opacity: 1;
              visibility: visible;
              bottom:0;
            }
          }
        }

        .tzProduct-item_info{
          text-align: center;
          h3.tzProduct-item_title{
            margin:0;
            padding:23px 0 0;
            a{
              display: block;
              font-family: 'Raleway',sans-serif;
              font-weight: 800;
              font-size: 14px;
              color:#222222;
              letter-spacing: 1px;
            }
          }
          .star-rating{
            display: inline-block;
            margin:15px 0 0;
            &:before{
              color: #b5b5b5;
            }
            span{
              &:before{
                color: @color-theme;
              }
            }
          }
          .price{
            font-family: 'Raleway',sans-serif;
            font-size: 18px;
            font-weight: 400;
            color: @color-theme;
            margin:5px 0 0;
            padding:0;
            del{
              display: inline-block;
              color: #b5b5b5;
              span.amount{
                font-family: 'Raleway',sans-serif;
                font-size: 18px;
                font-weight: 400;
                color: #b5b5b5;
              }
            }

            ins{
              display: inline-block;
              text-decoration: none;
              margin-left: 8px;
              font-weight: 400;
              span.amount{
                font-family: 'Raleway',sans-serif;
                font-size: 18px;
                font-weight: 400;
                color: @color-theme;
              }
            }
          }
        }
      }
    }
  }
}