@media (max-width: 768px) {
  .vc_col-sm-3{
    width: 50% !important;
  }
  .tzElement_Feature{
    &.tzFeature_type2{
      .tzFeature_Icon{
        float: none;
        text-align: center;
        .tzFeature_iconBox{
          display: inline-block;
        }
      }
      h5.tzFeature_title{
        margin-left: 0;
        text-align: center;
      }
      p.tzFeature_description{
        margin-left: 0;
        text-align: center;
      }
      &.tzFeature_text-icon{
        .tzFeature_Icon{
          float: none;
          text-align: center;
          .tzFeature_iconBox{
            display: inline-block;
          }
        }
        h5.tzFeature_title{
          margin-right: 0;
          text-align: center;
        }
        p.tzFeature_description{
          margin-right: 0;
          text-align: center;
        }
      }
    }
  }

  .tzElement_viewPost{
    .tzViewPost_Grid{
      .tzPost_Item{
        .tzPost_Box{
          .tzPost_Content{
            .tzPostImage{
              float: none;
              width: 100%;
            }
            .tzPost_info{
              margin-left: 0;
              .tzPost_infomation{
                text-align: center;
              }
              .tzPost_title{
                text-align: center;
                &:after{
                  left: 50%;
                  margin-left: -15px;
                }
              }
              p{
                text-align: center;
              }
            }
          }
        }
      }
    }
  }
}