/* 11.8 Feature Item */
@keyframes tz_rotate{
  from {
    .transform(rotateY(0));
  }
  to {
    .transform(rotateY(360deg));
  }
}
.tzElement_Feature{
  &.tzFeature_type1{
    .tzFeature_Icon{
      .tzFeature_iconBox{
        width: 42px;
        height: 42px;
        background: @color-theme;
        .border-radius(50%);
        text-align: center;
        padding: 11px 0px;
        position: relative;
        margin:4px;
        float: left;
        &:after{
          content: '';
          display: block;
          width: 100%;
          height: 100%;
          position: absolute;
          top:0;
          left: 0;
          border:2px solid @color-theme;
          .transform(scale(1.2));
          .border-radius(50%);
        }
        span{
          font-size: 20px;
          color: #ffffff;
        }
        i{
          font-size: 20px;
          color: #ffffff;
        }
      }
    }

    h5.tzFeature_title{
      font-weight: 700;
      padding:3px 0px;
      margin:0px 0px 0px 70px;
    }
    p.tzFeature_subTitle{
      margin:0px 0px 20px 70px;
    }
  }

  &.tzFeature_type2{
    .tzFeature_Icon{
      float: left;
      .tzFeature_iconBox{
        width: 75px;
        height: 75px;
        background: @color-theme;
        border:2px solid @color-theme;
        .border-radius(50%);
        text-align: center;
        padding: 25px 0px;
        position: relative;
        .transition(all 0.3s ease-in-out 0s);
        span{
          font-size: 25px;
          color: #ffffff;
          .transition(all 0.3s ease-in-out 0s);
        }
        i{
          font-size: 25px;
          color: #ffffff;
          .transition(all 0.3s ease-in-out 0s);
        }
        small.tzFeature_Number{
          position: absolute;
          top:-2px;
          left:-2px;
          width: 25px;
          height: 25px;
          border:2px solid @color-theme;
          background: #FFFFFF;
          .border-radius(50%);

          font-family: 'Montserrat',sans-serif;
          font-weight: 400;
          font-size: 14px;
          color: @color-theme;
          text-align: center;
          padding:1px 0px;
        }
      }
    }

    h5.tzFeature_title{
      font-weight: 700;
      padding:3px 0px;
      margin:0px 0px 13px 100px;
    }
    p.tzFeature_description{
      margin:0px 0px 0px 100px;
    }

    &.tzFeature_text-icon{
      .tzFeature_Icon{
        float: right;
        .tzFeature_iconBox{
          small.tzFeature_Number{
            left: auto;
            right:-2px;
          }
        }
      }

      h5.tzFeature_title{
        font-weight: 700;
        padding:3px 0px;
        margin:0px 100px 13px 0px;
        text-align: right;
      }
      p.tzFeature_description{
        margin:0px 100px 0px 0px;
        text-align: right;
      }
    }

    &:hover{
      .tzFeature_Icon{
        .tzFeature_iconBox{
          background: #FFFFFF;
          border:2px solid @color-theme;
          span{
            color: @color-theme;
          }
          i{
            color: @color-theme;
          }
        }
      }
    }
  }

  &.tzFeature_type3{
    text-align: center;
    .tzFeature_Icon{
      display: inline-block;
      margin-bottom: 35px;
      .tzFeature_iconBox{
        width: 96px;
        height: 96px;
        background: @color-theme;
        border:2px solid @color-theme;
        .border-radius(50%);
        text-align: center;
        padding: 33px 0px;
        position: relative;
        .transition(all 0.3s ease-in-out 0s);
        span{
          font-size: 25px;
          color: #ffffff;
          .transition(all 0.3s ease-in-out 0s);
        }
        i{
          font-size: 30px;
          color: #ffffff;
          .transition(all 0.3s ease-in-out 0s);
        }
        small.tzFeature_Number{
          position: absolute;
          top:-2px;
          left:-2px;
          width: 30px;
          height: 30px;
          border:2px solid @color-theme;
          background: #FFFFFF;
          .border-radius(50%);

          font-family: 'Montserrat',sans-serif;
          font-weight: 400;
          font-size: 14px;
          color: @color-theme;
          text-align: center;
          padding:3px 0px;
        }
      }
    }

    h5.tzFeature_title{
      font-weight: 700;
      padding:3px 0px;
      margin:0px 0px 13px;
      text-transform: uppercase;
    }
    p.tzFeature_description{
      margin:0px;
    }
    &:hover{
      .tzFeature_Icon{
        .tzFeature_iconBox{
          background: #FFFFFF;
          span{
            color: @color-theme;
          }
          i{
            color: @color-theme;
          }
        }
      }
    }
  }

  &.tzFeature_type4{
    text-align: center;
    .tzFeature_Icon{
      margin-bottom: 33px;
      .tzFeature_iconBox{
        i{
          font-size: 65px;
          color: @color-theme;
        }
      }
    }

    h5.tzFeature_title{
      font-family: 'Raleway', sans-serif;
      font-weight: 800;
      text-transform: uppercase;
      letter-spacing: 2px;
    }
    .tzFeature_description{
      font-family: 'Raleway',sans-serif;
    }
    a.tzFeature_readmore{
      display: block;
      font-family: 'Raleway',sans-serif;
      font-size: 12px;
      font-weight: 800;
      color: @color-theme;
      text-decoration: none;
      text-transform: uppercase;
      margin-top: 32px;
    }

    &:hover{
      .tzFeature_Icon{
        .tzFeature_iconBox{
          i{
            animation-name: tz_rotate;
            animation-duration: 1.5s;
            animation-delay: 0s;
            animation-timing-function: linear;
            animation-iteration-count: infinite;
          }
        }
      }
    }
  }
}