/* 11.10 Video HTML5 */
.tzElement_Video{
  width: 100%;
  height: 500px;
  overflow: hidden;
  position: relative;
  .tzElement_autoplay{
    background: @color-theme;
    border: 0 none;
    height:60px;
    left: 50%;
    margin-left: -30px;
    margin-top: -30px;
    opacity: 1;
    position: absolute;
    top: 50%;
    width: 60px;
    z-index: 99;
    text-align: center;
    i{
      display: inline-block;
      font-size: 20px;
      color: #FFFFFF;
      padding: 20px 0px;
    }
    &:hover{
      cursor: pointer;
    }
  }
  .tzElement_pauses{
    background: @color-theme;
    border: 0 none;
    height:60px;
    left: 50%;
    margin-left: -30px;
    margin-top: -30px;
    opacity: 1;
    position: absolute;
    top: 50%;
    width: 60px;
    z-index: 99;
    text-align: center;
    display: none;
    opacity: 0;
    i{
      display: inline-block;
      font-size: 20px;
      color: #FFFFFF;
      padding: 20px 0px;
    }
    &:hover{
      cursor: pointer;
    }
  }
  &:hover .tzElement_pauses{
    opacity: 1;
  }

  .tzElement_bgVideo{
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 1;
  }
  .tzElement_videoID{
    width: 100%;
    height: auto;
    overflow: hidden;
    position: absolute;
    top: 0;
    left: 0;
  }
  .tzElement_Overlay{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.5);
    z-index: 1;
  }
  .tzElement_textLeft{
    max-width: 50%;
    position: absolute;
    top:50%;
    right: 50%;
    font-family: 'Montserrat',sans-serif;
    font-size: 30px;
    font-weight: 400;
    color: #fafafa;
    z-index: 9;
    padding:0px 65px;
  }
  .tzElement_textRight{
    max-width: 50%;
    position: absolute;
    top:50%;
    left: 50%;
    font-family: 'Montserrat',sans-serif;
    font-size: 30px;
    font-weight: 400;
    color: #fafafa;
    z-index: 9;
    padding:0px 65px;
  }
}